export function convertRectangleFromExtendedHomeCoordsToNonextendedHomeCoords(
  extended: Rectangle
): Rectangle {
  const x =
    ((extended.topLeft.x - MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.width;

  const y =
    ((extended.topLeft.y -
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.height;

  const width =
    (extended.dimensions.width /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.width) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.width;

  const height =
    (extended.dimensions.height /
      MAIN_AREA_DIMENSIONS_IN_EXTENDED_HOME_COORDS.height) *
    NONEXTENDED_HOME_BACKGROUND_DIMENSIONS.height;

  return { topLeft: { x, y }, dimensions: { width, height } };
}

function loadImage(src: string): Loading<HTMLImageElement> {
  const i = new Image() as HTMLImageElement & {
    ready: Promise<HTMLImageElement>;
    hasLoaded: boolean;
  };

  i.hasLoaded = false;
  i.ready = new Promise((resolve, reject) => {
    i.addEventListener("load", () => {
      i.hasLoaded = true;
      resolve(i);
    });
    i.addEventListener("error", reject);
  });
  i.src = src;

  return i;
}

function whiteOverlay(
  loadingImg: Loading<HTMLImageElement>
): Loading<HTMLCanvasElement> {
  return getOverlay(loadingImg, { r: 255, g: 255, b: 255, a: 255 });
}

function blackOverlay(
  loadingImg: Loading<HTMLImageElement>
): Loading<HTMLCanvasElement> {
  return getOverlay(loadingImg, { r: 0, g: 0, b: 0, a: 255 });
}

function redOverlay(
  loadingImg: Loading<HTMLImageElement>
): Loading<HTMLCanvasElement> {
  return getOverlay(loadingImg, { r: 255, g: 0, b: 0, a: 255 });
}

function getOverlay(
  loadingImg: Loading<HTMLImageElement>,
  color: Color32
): Loading<HTMLCanvasElement> {
  const canvas = document.createElement("canvas") as HTMLCanvasElement & {
    ready: Promise<HTMLCanvasElement>;
    hasLoaded: boolean;
  };
  const ctx = canvas.getContext("2d")!;

  canvas.hasLoaded = false;
  canvas.ready = loadingImg.ready.then((img) => {
    const width = img.naturalWidth;
    const height = img.naturalHeight;

    canvas.width = width;
    canvas.height = height;

    ctx.drawImage(img, 0, 0);

    const imgData = ctx.getImageData(0, 0, width, height);
    const bytes = imgData.data;
    for (let i = 0; i < bytes.length; i += 4) {
      if (bytes[i + 3] > 0) {
        bytes[i] = color.r;
        bytes[i + 1] = color.g;
        bytes[i + 2] = color.b;
        bytes[i + 3] = color.a;
      }
    }

    ctx.clearRect(0, 0, width, height);
    ctx.putImageData(imgData, 0, 0);

    return canvas;
  });

  return canvas;
}

export type Loading<T> = T & {
  readonly ready: Promise<T>;
  readonly hasLoaded: boolean;
};
