import { typecheckedAssertNever } from "../App";
import "./fonts.css";

export const fonts = {
  checkpoint: waitForFont("Checkpoint"),
  notoSans: waitForFont("Noto Sans JP"),
  twemojiCountryFlags: waitForFont("Twemoji Country Flags"),
} as const;

function waitForFont(fontFamily: string): Promise<FontFace> {
  return new Promise((resolve, reject) => {
    pollForFont(fontFamily, resolve, reject);
  });
}

function pollForFont(
  fontFamily: string,
  resolve: (f: FontFace) => void,
  reject: (e: unknown) => void
): void {
  document.fonts.ready.then((fontSet): void | Promise<void> => {
    const targetFont = Array.from(fontSet.values()).find(
      (font) => font.family === fontFamily
    );
    if (targetFont === undefined) {
      requestAnimationFrame(() => {
        pollForFont(fontFamily, resolve, reject);
      });
      return;
    }

    switch (targetFont.status) {
      case "unloaded":
        targetFont.load().then(resolve, reject);
        return;
      case "error":
        reject(new Error(`Failed to load ${JSON.stringify(fontFamily)}.`));
        return;
      case "loaded":
      case "loading":
        targetFont.loaded.then(resolve, reject);
        return;
      default:
        typecheckedAssertNever(targetFont.status);
    }
  });
}
