import { SafeArea, SafeAreaInsets } from "capacitor-plugin-safe-area";

export type SafeAreaChangeListener = (insets: SafeAreaInsets) => void;

const listeners: SafeAreaChangeListener[] = [];

SafeArea.addListener("safeAreaChanged", (data) => {
  for (const listener of listeners) {
    listener(data);
  }
});

export function getSafeAreaInsets(): Promise<SafeAreaInsets> {
  return SafeArea.getSafeAreaInsets();
}

export function addSafeAreaChangeListener(
  listener: SafeAreaChangeListener
): void {
  if (listeners.includes(listener)) {
    return;
  }

  listeners.push(listener);
}

export function removeSafeAreaChangeListener(
  listener: SafeAreaChangeListener
): void {
  while (true) {
    const i = listeners.indexOf(listener);
    if (i === -1) {
      break;
    }

    listeners.splice(i, 1);
  }
}
