import React from "react";
import { Dimensions } from "./miscTypes";
import { Vec2, addVec, subVec, invertY } from "./vec2";
import { DROPDOWN_ARROW } from "./icons/dropdownArrow";
import { TRASH_CAN } from "./icons/trash_can";

export enum BlockDefKind {
  Command = "Command",
  Query = "Query",
}

export enum BlockSignaturePartKind {
  Label = "Label",
  Dropdown = "Dropdown",
  Expression = "Expression",
  CommandSequence = "CommandSequence",
}

export enum RenderSpecKind {
  Block = "Block",
  BlockSequence = "BlockSequence",
  Label = "Label",
  Dropdown = "Dropdown",
  Literal = "Literal",
}

export enum UiTouchStateKind {
  BlockDrag = "BlockDrag",
  Dropdown = "Dropdown",
  Literal = "Literal",
  DropdownMenuChoice = "DropdownMenuChoice",
  DropdownMenuScroll = "DropdownMenuScroll",
}

export type BlockEditorCursor =
  | "default"
  | "grab"
  | "grabbing"
  | "text"
  | "pointer";

enum BlockPartKind {
  Label = "Label",
  Dropdown = "Dropdown",
  Literal = "Literal",
  Block = "Block",
  CommandSequence = "CommandSequence",
}

export const CANVAS_ADDRESS_PART = "canvas";
export const PALETTE_ADDRESS_PART = "palette";
export const DRAGGEE_ADDRESS_PART = "draggee";
export const ARG_DROPDOWN_ADDRESS_PART = "argDropdown";
export const ARG_EXPRESSION_ADDRESS_PART = "argExpression";
export const ARG_COMMAND_SEQUENCE_ADDRESS_PART = "argCommandSequence";
export const INSERTION_MARK_ADDRESS_PART = "insertionMark";

const NODE_ADDRESS_PART_STRINGS: Record<NodeAddressPartString, true> = {
  [CANVAS_ADDRESS_PART]: true,
  [PALETTE_ADDRESS_PART]: true,
  [DRAGGEE_ADDRESS_PART]: true,
  [ARG_DROPDOWN_ADDRESS_PART]: true,
  [ARG_EXPRESSION_ADDRESS_PART]: true,
  [ARG_COMMAND_SEQUENCE_ADDRESS_PART]: true,
  [INSERTION_MARK_ADDRESS_PART]: true,
};

const COMMAND_INSERTION_MARK_SENTINEL = "COMMAND_INSERTION_MARK_SENTINEL";

const MINIMAL_LOREM_IPSUM_TEXT_FOR_FONT_MEASUREMENT = "M";

const DATA_TOUCH_ROLES = {
  canvasBackground: "CanvasBackground",
  paletteBackground: "PaletteBackground",
  blockAddressPrefix: "BlockAddress_",
  dropdownAddressPrefix: "DropdownAddress_",
  literalAddressPrefix: "LiteralAddress_",
  dropdownChoicePrefix: "DropdownChoice_",
} as const;
const DOM_ID_SUFFIXES = {
  root: "_Root",
  editorClipPath: "_EditorClipPath",
  backgroundDotPattern: "_BackgroundDotPattern",
  paletteClipPath: "_PaletteClipPath",
  dropdownMenuClipPath: "_DropdownMenuClipPath",
  canvasBackground: "_CanvasBackground",
} as const;

const TRANSPARENT: Color32 = { r: 0, g: 0, b: 0, a: 0 };

export interface BlockEditorTypeParams {
  readonly BlockKind: string | number | symbol;
  readonly DropdownValueKind: string | number | symbol;
}

export interface BlockEditorProps<B extends BlockEditorTypeParams> {
  readonly config: BlockEditorConfig<B>;
  readonly state: BlockEditorState<B>;

  readonly measureText: (
    text: string,
    font: string,
    fontSizePx: number
  ) => Dimensions;
  readonly setState: (
    updater: (oldState: BlockEditorState<B>) => BlockEditorState<B>,
    callback?: (newState: BlockEditorState<B>) => void
  ) => void;
}

export interface BlockEditorConfig<B extends BlockEditorTypeParams> {
  readonly componentInstanceUid: string;
  readonly sizing: BlockEditorSizingProps;
  readonly colors: BlockEditorColorProps<B>;
  readonly fonts: BlockEditorFontProps;
  readonly blockDefs: BlockDefs<B>;
  readonly dropdownTexts: ReadonlyRecord<B["DropdownValueKind"], string>;
}

