  readonly snapVerticalRange: number;
  readonly snapHorizontalRange: number;
  readonly backgroundDotRadius: number;
  readonly blockLabelTextSizePx: number;
  readonly blockDropdownTextSizePx: number;
  readonly blockDropdownHorizontalPadding: number;
  readonly blockDropdownVerticalPadding: number;
  readonly blockDropdownArrowWidth: number;
  readonly blockDropdownArrowLeftMargin: number;
  readonly blockDropdownArrowVerticalMargin: number;
  readonly blockLiteralTextSizePx: number;
  readonly blockLiteralHorizontalPadding: number;
