function get_ofTRe_to_blockBRe_path(
  teeth: readonly CommandBlockTooth[],
  toothIndex: number,
  editorSizing: BlockEditorSizingProps
): string {
  const tooth = teeth[toothIndex];
  const toothX = tooth.rbox.topLeft.x;
  const toothY = tooth.rbox.topLeft.y;
  const toothWidth = tooth.rbox.dimensions.width;
  const toothHeight = tooth.rbox.dimensions.height;
  const innerRadius = editorSizing.blockBorderRadius;
  const outerRadius = innerRadius + editorSizing.blockBorder;

  return `L${toothX + toothWidth - outerRadius} ${toothY}
      
  A${outerRadius} ${outerRadius} 0 0 1 ${toothX + toothWidth} ${
    toothY + outerRadius
  }

  L${toothX + toothWidth} ${toothY + toothHeight - outerRadius}

  A${outerRadius} ${outerRadius} 0 0 1 ${toothX + toothWidth - outerRadius} ${
    toothY + toothHeight
  }`;
}

