function get_omTLe_to_nextToothBlockTLe_assumingSequenceArgIndent_path(
  teeth: readonly CommandBlockTooth[],
  toothIndex: number,
  editorSizing: BlockEditorSizingProps
): string {
  if (toothIndex >= teeth.length - 1) {
    throw new Error(
      "Called get_omTLe_to_nextToothBlockTLe_assumingSequenceArgIndent_path when there was no next tooth."
    );
  }

  const tooth = teeth[toothIndex];
  const toothX = tooth.rbox.topLeft.x;
  const toothY = tooth.rbox.topLeft.y;
  const toothHeight = tooth.rbox.dimensions.height;

  const nextTooth = teeth[toothIndex + 1];
  const nextToothY = nextTooth.rbox.topLeft.y;
  const innerRadius = editorSizing.blockBorderRadius;
  const commandSequenceArgIndent = editorSizing.commandSequenceArgIndent;

  return `L${toothX + commandSequenceArgIndent + innerRadius} ${
    toothY + toothHeight
  }
    
    A${innerRadius} ${innerRadius} 0 0 0 ${toothX + commandSequenceArgIndent} ${
    toothY + toothHeight + innerRadius
  }
    
    L${toothX + commandSequenceArgIndent} ${nextToothY - innerRadius}
    
    A${innerRadius} ${innerRadius} 0 0 0 ${
    toothX + commandSequenceArgIndent + innerRadius
  } ${nextToothY}`;
}

function get_imTLe_to_nextToothBlockTLe_assumingSequenceArgIndent_path(
  teeth: readonly CommandBlockTooth[],
  toothIndex: number,
  editorSizing: BlockEditorSizingProps
): string {
  if (toothIndex >= teeth.length - 1) {
    throw new Error(
      "Called get_imTLe_to_nextToothBlockTLe_assumingSequenceArgIndent_path when there was no next tooth."
    );
  }

  const tooth = teeth[toothIndex];
  const toothX = tooth.rbox.topLeft.x;
  const toothY = tooth.rbox.topLeft.y;
  const toothHeight = tooth.rbox.dimensions.height;

  const nextTooth = teeth[toothIndex + 1];
  const nextToothY = nextTooth.rbox.topLeft.y;
  const borderWidth = editorSizing.blockBorder;
  const innerRadius = editorSizing.blockBorderRadius;
  const outerRadius = innerRadius + borderWidth;
  const commandSequenceArgIndent = editorSizing.commandSequenceArgIndent;

  return `L${toothX + commandSequenceArgIndent - borderWidth + outerRadius} ${
    toothY + toothHeight - borderWidth
  }
    
    A${outerRadius} ${outerRadius} 0 0 0 ${
    toothX + commandSequenceArgIndent - borderWidth
  } ${toothY + toothHeight - borderWidth + outerRadius}
    
    L${toothX + commandSequenceArgIndent - borderWidth} ${
    nextToothY + borderWidth - outerRadius
  }
    
    A${outerRadius} ${outerRadius} 0 0 0 ${
    toothX + commandSequenceArgIndent - borderWidth + outerRadius
  } ${nextToothY + borderWidth}`;
}

function QueryBlockBody({
  rbox,
  editorSizing,
  outerColor,
  innerColor,
}: QueryBlockBodyProps): React.ReactElement {
  const { outerPath, innerPath } =
    getLandscapePillOuterAndInnerPathsFromBoundingBox(
      rbox,
      editorSizing.blockBorder
    );
  return (
    <>
      <path d={outerPath} fill={stringifyColor(outerColor)} />
      <path d={innerPath} fill={stringifyColor(innerColor)} />
    </>
  );
}

function getLandscapePillOuterAndInnerPathsFromBoundingBox(
  bbox: Rectangle,
  borderWidth: number
): { outerPath: string; innerPath: string } {
  const outerPath = getLandscapePillPathFromBoundingBox(bbox);

  const { x, y } = bbox.topLeft;
  const { width, height } = bbox.dimensions;
  const innerPathBbox = {
    topLeft: {
      x: x + borderWidth,
      y: y + borderWidth,
    },
    dimensions: {
      width: width - 2 * borderWidth,
      height: height - 2 * borderWidth,
    },
  };
  const innerPath = getLandscapePillPathFromBoundingBox(innerPathBbox);

  return { outerPath, innerPath };
}

function getLandscapePillPathFromBoundingBox(bbox: Rectangle): string {
  const { x, y } = bbox.topLeft;
  const { width, height } = bbox.dimensions;
  const radius = height / 2;

  if (height > width) {
    throw new Error("The width must be greater than or equal to the height.");
  }

  return `M${x + radius} ${y}

  L${x + width - radius} ${y}

  A${radius} ${radius} 0 0 1 ${x + width - radius} ${y + height}
  
  L${x + radius} ${y + height}
  
  A${radius} ${radius} 0 0 1 ${x + radius} ${y}`;
}

function DropdownView<B extends BlockEditorTypeParams>({
  spec,
  editor,
}: DropdownViewProps<B>): React.ReactElement {
  const editorProps = editor.props;
  const { sizing, fonts, colors, dropdownTexts } = editorProps.config;

  const { x, y } = spec.rbox.topLeft;
  const dropdownDimensions = spec.rbox.dimensions;

  const blockColorTriplet = spec.isUnreachable
    ? colors.unreachableBlocks[spec.parentBlockKind]
    : colors.blocks[spec.parentBlockKind];

  const { outerPath, innerPath } =
    getLandscapePillOuterAndInnerPathsFromBoundingBox(
      spec.rbox,
      sizing.blockDropdownBorder
    );

  const borderRadius = 0.5 * spec.rbox.dimensions.height;

  const dropdownText = dropdownTexts[spec.value];
  const naiveTextDimensions = editor.props.measureText(
    dropdownText,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const minimumTextDimensions = editor.props.measureText(
    MINIMAL_LOREM_IPSUM_TEXT_FOR_FONT_MEASUREMENT,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const textDimensions = {
    width: Math.max(naiveTextDimensions.width, minimumTextDimensions.width),
    height: Math.max(naiveTextDimensions.height, minimumTextDimensions.height),
  };

  const arrowScale = sizing.blockDropdownArrowWidth / DROPDOWN_ARROW.width;

  const haloRbox = {
    topLeft: subVec(spec.rbox.topLeft, {
      x: sizing.queryInsertionMarkHaloRadius,
      y: sizing.queryInsertionMarkHaloRadius,
    }),
    dimensions: {
      width:
        spec.rbox.dimensions.width + 2 * sizing.queryInsertionMarkHaloRadius,
      height:
        spec.rbox.dimensions.height + 2 * sizing.queryInsertionMarkHaloRadius,
    },
  };
  const haloBody = getLandscapePillOuterAndInnerPathsFromBoundingBox(
    haloRbox,
    0
  );

  return (
    <g data-touch-role={getDataTouchRoleFromDropdownAddress(spec.address)}>
      {spec.hasPendingEditMark && (
        <path
          d={haloBody.outerPath}
          fill={stringifyColor(colors.queryInsertionMarkHalo)}
        />
      )}

      <path d={outerPath} fill={stringifyColor(blockColorTriplet.tertiary)} />

      <path d={innerPath} fill={stringifyColor(blockColorTriplet.secondary)} />

      <text
        x={x + sizing.blockDropdownHorizontalPadding + borderRadius}
        y={y + 0.5 * dropdownDimensions.height}
        fontSize={sizing.blockDropdownTextSizePx}
        fontFamily={fonts.blockDropdownFont}
        dominantBaseline="middle"
        fill={stringifyColor(colors.blockDropdownText)}
      >
        {dropdownText}
      </text>

      <path
        transform={`scale(${arrowScale}) translate(${
          (1 / arrowScale) *
          (x +
            sizing.blockDropdownHorizontalPadding +
            borderRadius +
            textDimensions.width +
            sizing.blockDropdownArrowLeftMargin)
        } ${
          (1 / arrowScale) *
          (y +
            0.5 * dropdownDimensions.height -
            0.5 * arrowScale * DROPDOWN_ARROW.height)
        })`}
        d={DROPDOWN_ARROW.path}
        fill={stringifyColor(editor.props.config.colors.blockDropdownText)}
      />
    </g>
  );
}

function LiteralView<B extends BlockEditorTypeParams>({
  spec,
  editor,
}: LiteralViewProps<B>): React.ReactElement {
  const editorProps = editor.props;
  const { sizing, colors, fonts } = editorProps.config;

  const { x, y } = spec.rbox.topLeft;
  const literalDimensions = spec.rbox.dimensions;

  const haloRbox = {
    topLeft: subVec(spec.rbox.topLeft, {
      x: sizing.queryInsertionMarkHaloRadius,
      y: sizing.queryInsertionMarkHaloRadius,
    }),
    dimensions: {
      width:
        spec.rbox.dimensions.width + 2 * sizing.queryInsertionMarkHaloRadius,
      height:
        spec.rbox.dimensions.height + 2 * sizing.queryInsertionMarkHaloRadius,
    },
  };
  const haloBody = getLandscapePillOuterAndInnerPathsFromBoundingBox(
    haloRbox,
    0
  );

  const literalBody = getLandscapePillOuterAndInnerPathsFromBoundingBox(
    spec.rbox,
    sizing.blockLiteralBorder
  );

  const borderRadius = 0.5 * spec.rbox.dimensions.height;

  const blockColorTriplet = spec.isUnreachable
    ? colors.unreachableBlocks[spec.parentBlockKind]
    : colors.blocks[spec.parentBlockKind];

  return (
    <g data-touch-role={getDataTouchRoleFromLiteralAddress(spec.address)}>
      {spec.hasQueryInsertionMark && (
        <path
          d={haloBody.outerPath}
          fill={stringifyColor(colors.queryInsertionMarkHalo)}
        />
      )}

      <path
        d={literalBody.outerPath}
        fill={stringifyColor(blockColorTriplet.tertiary)}
      />

      <path
        d={literalBody.innerPath}
        fill={stringifyColor(colors.blockLiteralBackground)}
      />

      <text
        x={x + sizing.blockLiteralHorizontalPadding + borderRadius}
        y={y + 0.5 * literalDimensions.height}
        fontSize={sizing.blockLiteralTextSizePx}
        fontFamily={fonts.blockLiteralFont}
        dominantBaseline="middle"
        fill={stringifyColor(colors.blockLiteralText)}
      >
        {spec.source}
      </text>
    </g>
  );
}

function BlockSequenceView<B extends BlockEditorTypeParams>({
  spec,
  editor,
}: BlockSequenceViewProps<B>): React.ReactElement {
  return (
    <>
      {spec.blockSpecs.map((blockSpec) => (
        <BlockView<B>
          key={blockSpec.address.map(stringifyNodeAddressPart).join("_")}
          spec={blockSpec}
          editor={editor}
        />
      ))}
    </>
  );
}

function getCanvasItemRenderSpec<B extends BlockEditorTypeParams>({
  editorProps,
  item,
  address,
  insertionMark,
  draggeeOriginalAddress,
  hasDragPositionChangedSinceDragStart,
  dropdownMenuDropdownAddress,
}: {
  editorProps: BlockEditorProps<B>;
  item: CanvasItem<B>;
  address: NodeAddress;
  insertionMark: null | InsertionMark<B>;
  draggeeOriginalAddress: null | NodeAddress;
  hasDragPositionChangedSinceDragStart: boolean;
  dropdownMenuDropdownAddress: null | NodeAddress;
}): BlockSequenceRenderSpec<B> {
  const { position, content } = item;
  return getBlockSequenceRenderSpec({
    editorProps,
    position,
    sequence: content,
    address,
    insertionMark,
    draggeeOriginalAddress,
    hasDragPositionChangedSinceDragStart,
    dropdownMenuDropdownAddress,
    isUnreachable: !isFirstCommandEntryPoint(
      editorProps.config.blockDefs,
      content
    ),
  });
}

export function getBlockSequenceRenderSpec<B extends BlockEditorTypeParams>({
  editorProps,
  position: rawPosition,
  sequence,
  address,
  insertionMark,
  draggeeOriginalAddress,
  hasDragPositionChangedSinceDragStart,
  dropdownMenuDropdownAddress,
  isUnreachable,
}: {
  editorProps: BlockEditorProps<B>;
  position: Vec2;
  sequence: BlockSequence<B>;
  address: NodeAddress;
  insertionMark: null | InsertionMark<B>;
  draggeeOriginalAddress: null | NodeAddress;
  hasDragPositionChangedSinceDragStart: boolean;
  dropdownMenuDropdownAddress: null | NodeAddress;
  isUnreachable: boolean;
}): BlockSequenceRenderSpec<B> {
  const { sizing } = editorProps.config;

  const localDraggeeAddress =
    draggeeOriginalAddress === null
      ? null
      : withoutPrefix(address, draggeeOriginalAddress);
  const commandsAfterRemovingDraggee =
    localDraggeeAddress !== null &&
    localDraggeeAddress.length === 1 &&
    hasDragPositionChangedSinceDragStart
      ? sequence.slice(0, expectNumericAddressPart(localDraggeeAddress[0]))
      : sequence;

  const localInsertionMarkAddress =
    insertionMark === null
      ? null
      : withoutPrefix(address, insertionMark.address);
  const adjustedPosition = {
    x: rawPosition.x,
    y:
      rawPosition.y +
      (insertionMark !== null &&
      insertionMark.address.length === 3 &&
      localInsertionMarkAddress !== null &&
      localInsertionMarkAddress.length === 1 &&
      localInsertionMarkAddress[0] === 0
        ? -(getDefaultCommandBlockHeight(editorProps) - sizing.blockBorder)
        : 0),
  };
  type Commandlike = Block<B> | typeof COMMAND_INSERTION_MARK_SENTINEL;
  const commandlikes: readonly Commandlike[] =
    localInsertionMarkAddress !== null &&
    localInsertionMarkAddress.length === 1 &&
    expectNumericAddressPart(localInsertionMarkAddress[0]) <=
      commandsAfterRemovingDraggee.length
      ? identityFunctionToForceTypesafeCast<readonly Commandlike[]>(
          commandsAfterRemovingDraggee
        )
          .slice(0, expectNumericAddressPart(localInsertionMarkAddress[0]))
          .concat(
            [COMMAND_INSERTION_MARK_SENTINEL],
            commandsAfterRemovingDraggee.slice(
              expectNumericAddressPart(localInsertionMarkAddress[0])
            )
          )
      : commandsAfterRemovingDraggee;
  const blockSpecs: BlockRenderSpec<B>[] = [];
  let y = adjustedPosition.y;
  let nonInsertionMarkIndex = 0;
  for (let i = 0; i < commandlikes.length; ++i) {
    const commandlike = commandlikes[i];
    if (commandlike === COMMAND_INSERTION_MARK_SENTINEL) {
      if (insertionMark === null) {
        throw new Error(
          "Impossible: insertionMark is null but reached command insertion mark sentinel."
        );
      }
      const spec = getBlockRenderSpec({
        editorProps,
        position: { x: adjustedPosition.x, y },
        block: getInsertionMarkCommand(
          insertionMark.draggee,
          editorProps.config.blockDefs
        ),
        address: [INSERTION_MARK_ADDRESS_PART],
        insertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable,
      });
      blockSpecs.push(spec);

      y += spec.rbox.dimensions.height - sizing.blockBorder;
      continue;
    }

    const spec = getBlockRenderSpec({
      editorProps,
      position: { x: adjustedPosition.x, y },
      block: commandlike,
      address: address.concat([nonInsertionMarkIndex]),
      insertionMark,
      draggeeOriginalAddress,
      hasDragPositionChangedSinceDragStart,
      dropdownMenuDropdownAddress,
      isUnreachable,
    });
    blockSpecs.push(spec);

    ++nonInsertionMarkIndex;

    y += spec.rbox.dimensions.height - sizing.blockBorder;
  }

  const dimensions =
    blockSpecs.length === 0
      ? {
          width: 0,
          height: getMinimumCommandBlockHeight(editorProps),
        }
      : {
          width: Math.max(
            ...blockSpecs.map((command) => command.rbox.dimensions.width)
          ),
          height: y - adjustedPosition.y,
        };

  return {
    kind: RenderSpecKind.BlockSequence,
    sequence,
    address,
    rbox: {
      topLeft: { x: adjustedPosition.x, y: adjustedPosition.y },
      dimensions,
    },
    blockSpecs,
  };
}

function getBlockRenderSpec<B extends BlockEditorTypeParams>({
  editorProps,
  position,
  block,
  address,
  insertionMark,
  draggeeOriginalAddress,
  hasDragPositionChangedSinceDragStart,
  dropdownMenuDropdownAddress,
  isUnreachable,
}: {
  editorProps: BlockEditorProps<B>;
  position: Vec2;
  block: Block<B>;
  address: NodeAddress;
  insertionMark: null | InsertionMark<B>;
  draggeeOriginalAddress: null | NodeAddress;
  hasDragPositionChangedSinceDragStart: boolean;
  dropdownMenuDropdownAddress: null | NodeAddress;
  isUnreachable: boolean;
}): BlockRenderSpec<B> {
  const blockDef = editorProps.config.blockDefs[block.kind];

  if (blockDef.kind === BlockDefKind.Command) {
    return getCommandBlockRenderSpec({
      editorProps,
      position,
      block,
      blockDef,
      address,
      insertionMark,
      draggeeOriginalAddress,
      hasDragPositionChangedSinceDragStart,
      dropdownMenuDropdownAddress,
      isUnreachable,
    });
  }

  return getQueryBlockRenderSpec({
    editorProps,
    position,
    block,
    blockDef,
    address,
    insertionMark,
    draggeeOriginalAddress,
    hasDragPositionChangedSinceDragStart,
    dropdownMenuDropdownAddress,
    isUnreachable,
  });
}

function getCommandBlockRenderSpec<B extends BlockEditorTypeParams>({
  editorProps,
  position,
  block,
  blockDef,
  address,
  insertionMark,
  draggeeOriginalAddress,
  hasDragPositionChangedSinceDragStart,
  dropdownMenuDropdownAddress,
  isUnreachable,
}: {
  editorProps: BlockEditorProps<B>;
  position: Vec2;
  block: Block<B>;
  blockDef: CommandBlockDef<B>;
  address: NodeAddress;
  insertionMark: null | InsertionMark<B>;
  draggeeOriginalAddress: null | NodeAddress;
  hasDragPositionChangedSinceDragStart: boolean;
  dropdownMenuDropdownAddress: null | NodeAddress;
  isUnreachable: boolean;
}): BlockRenderSpec<B> {
  if (address.length === 1 && address[0] === INSERTION_MARK_ADDRESS_PART) {
    const dummyAddress: NodeAddress = [CANVAS_ADDRESS_PART, 0];
    const insertionMarkCommandSpec = getCommandBlockRenderSpec({
      editorProps,
      position,
      block,
      blockDef,
      address: dummyAddress,
      insertionMark,
      draggeeOriginalAddress,
      hasDragPositionChangedSinceDragStart,
      dropdownMenuDropdownAddress,
      isUnreachable,
    });

    const rbox = {
      topLeft: position,
      dimensions: {
        width: insertionMarkCommandSpec.rbox.dimensions.width,
        height: getDefaultCommandBlockHeight(editorProps),
      },
    };
    const teeth = [
      {
        rbox,
        hasFemaleSocket: blockDef.hasFemaleSocket,
        hasMaleSocket: blockDef.hasMaleSocket,
      },
    ];
    return {
      kind: RenderSpecKind.Block,
      block,
      address,
      isUnreachable,
      rbox,
      teeth,
      labelSpecs: [],
      dropdownSpecs: [],
      subexpressionSpecs: [],
      subsequenceSpecs: [],
    };
  }

  const { sizing, blockDefs } = editorProps.config;
  const minimumNonLabelNonSequencePartX =
    position.x + getCommandBlockFemaleSocketPathPoints(sizing).ofTRe.x;

  const localDraggeeAddress =
    draggeeOriginalAddress === null
      ? null
      : withoutPrefix(address, draggeeOriginalAddress);

  const parts = getBlockParts(blockDef, block);
  const toothPartLists = groupBlockPartsByTooth(parts);
  const sequences: readonly BlockPartCommandSequence<B>[] = parts.filter(
    (p): p is BlockPartCommandSequence<B> =>
      p.kind === BlockPartKind.CommandSequence
  );

  const teeth: CommandBlockTooth[] = [];
  const labelSpecs: LabelRenderSpec[] = [];
  const dropdownSpecs: DropdownRenderSpec<B>[] = [];
  const subexpressionSpecs: ExpressionRenderSpec<B>[] = [];
  const subsequenceSpecs: BlockSequenceRenderSpec<B>[] = [];
  let currentY = position.y;
  let previousSequence: null | BlockSequence<B> = null;
  let sequenceIndex = 0;
  let dropdownIndex = 0;
  let expressionIndex = 0;

  for (let toothIndex = 0; toothIndex < toothPartLists.length; ++toothIndex) {
    const toothParts = toothPartLists[toothIndex];
    const toothDimensions = getCommandToothDimensionsFromParts(
      toothParts,
      address,
      expressionIndex,
      draggeeOriginalAddress,
      hasDragPositionChangedSinceDragStart,
      dropdownMenuDropdownAddress,
      editorProps
    );

    let currentX = position.x + sizing.interBlockPartHorizontalMargin;

    for (const part of toothParts) {
      if (part.kind === BlockPartKind.Label) {
        const dimensions = editorProps.measureText(
          part.text,
          editorProps.config.fonts.blockLabelFont,
          editorProps.config.sizing.blockLabelTextSizePx
        );
        labelSpecs.push({
          kind: RenderSpecKind.Label,
          text: part.text,
          rbox: {
            topLeft: {
              x: currentX,
              y: currentY + 0.5 * toothDimensions.height,
            },
            dimensions,
          },
        });
        currentX += dimensions.width + sizing.interBlockPartHorizontalMargin;
        continue;
      }

      if (part.kind === BlockPartKind.Dropdown) {
        currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);

        const dropdownDimensions = getDropdownDimensions(
          part.value,
          editorProps
        );

        const dropdownAddress = address.concat([
          ARG_DROPDOWN_ADDRESS_PART,
          dropdownIndex,
        ]);

        dropdownSpecs.push({
          kind: RenderSpecKind.Dropdown,
          parentBlockKind: block.kind,
          value: part.value,
          hasPendingEditMark:
            dropdownMenuDropdownAddress !== null &&
            areNodeAddressesEqual(dropdownAddress, dropdownMenuDropdownAddress),
          address: dropdownAddress,
          isUnreachable,
          rbox: {
            topLeft: {
              x: currentX,
              y:
                currentY +
                0.5 * toothDimensions.height -
                0.5 * dropdownDimensions.height,
            },
            dimensions: dropdownDimensions,
          },
        });
        ++dropdownIndex;
        currentX +=
          dropdownDimensions.width + sizing.interBlockPartHorizontalMargin;
        continue;
      }

      if (part.kind === BlockPartKind.Literal) {
        currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);

        const literalDimensions = getLiteralDimensions(
          part.source,
          editorProps
        );
        const literalAddress = address.concat([
          ARG_EXPRESSION_ADDRESS_PART,
          expressionIndex,
        ]);

        subexpressionSpecs.push({
          kind: RenderSpecKind.Literal,
          parentBlockKind: block.kind,
          source: part.source,
          hasQueryInsertionMark:
            insertionMark !== null &&
            areNodeAddressesEqual(literalAddress, insertionMark.address),
          address: literalAddress,
          isUnreachable,
          rbox: {
            topLeft: {
              x: currentX,
              y:
                currentY +
                0.5 * toothDimensions.height -
                0.5 * literalDimensions.height,
            },
            dimensions: literalDimensions,
          },
        });
        ++expressionIndex;
        currentX +=
          literalDimensions.width + sizing.interBlockPartHorizontalMargin;
        continue;
      }

      if (part.kind === BlockPartKind.Block) {
        currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);

        if (
          localDraggeeAddress !== null &&
          localDraggeeAddress.length === 2 &&
          localDraggeeAddress[0] === ARG_EXPRESSION_ADDRESS_PART &&
          localDraggeeAddress[1] === expressionIndex &&
          hasDragPositionChangedSinceDragStart
        ) {
          const literalDimensions = getLiteralDimensions("", editorProps);
          const literalAddress = address.concat([
            ARG_EXPRESSION_ADDRESS_PART,
            expressionIndex,
          ]);

          subexpressionSpecs.push({
            kind: RenderSpecKind.Literal,
            parentBlockKind: block.kind,
            source: "",
            hasQueryInsertionMark:
              insertionMark !== null &&
              areNodeAddressesEqual(literalAddress, insertionMark.address),
            address: literalAddress,
            isUnreachable,
            rbox: {
              topLeft: {
                x: currentX,
                y:
                  currentY +
                  0.5 * toothDimensions.height -
                  0.5 * literalDimensions.height,
              },
              dimensions: literalDimensions,
            },
          });
          ++expressionIndex;
          currentX +=
            literalDimensions.width + sizing.interBlockPartHorizontalMargin;
          continue;
        }

        const misplacedButCorrectlySizedSpec = getBlockRenderSpec({
          editorProps,
          position: { x: 0, y: 0 },
          block: part.block,
          address: address.concat([
            ARG_EXPRESSION_ADDRESS_PART,
            expressionIndex,
          ]),
          insertionMark,
          draggeeOriginalAddress,
          hasDragPositionChangedSinceDragStart,
          dropdownMenuDropdownAddress,
          isUnreachable,
        });
        const spec = getBlockRenderSpec({
          editorProps,
          position: {
            x: currentX,
            y:
              currentY +
              0.5 *
                (toothDimensions.height -
                  misplacedButCorrectlySizedSpec.rbox.dimensions.height),
          },
          block: part.block,
          address: address.concat([
            ARG_EXPRESSION_ADDRESS_PART,
            expressionIndex,
          ]),
          insertionMark,
          draggeeOriginalAddress,
          hasDragPositionChangedSinceDragStart,
          dropdownMenuDropdownAddress,
          isUnreachable,
        });
        subexpressionSpecs.push(spec);
        currentX +=
          spec.rbox.dimensions.width + sizing.interBlockPartHorizontalMargin;
        ++expressionIndex;
        continue;
      }
    }

    const isLastTooth = toothIndex === toothPartLists.length - 1;
    teeth.push({
      rbox: {
        topLeft: { x: position.x, y: currentY },
        dimensions: {
          width: toothDimensions.width,
          // Ensure that empty teeth have a non-zero height.
          height: Math.max(
            toothDimensions.height,
            getMinimumCommandBlockHeight(editorProps)
          ),
        },
      },
      hasFemaleSocket:
        previousSequence === null
          ? blockDef.hasFemaleSocket
          : previousSequence.length === 0
          ? true
          : (
              blockDefs[
                previousSequence[previousSequence.length - 1].kind
              ] as CommandBlockDef<B>
            ).hasMaleSocket,
      hasMaleSocket: isLastTooth ? blockDef.hasMaleSocket : true,
    });
    currentY += toothDimensions.height - sizing.blockBorder;

    if (sequenceIndex < sequences.length) {
      const sequence = sequences[sequenceIndex];
      const originalSequenceIndex = sequenceIndex;
      ++sequenceIndex;

      const spec = getBlockSequenceRenderSpec<B>({
        editorProps,
        position: {
          x: position.x + sizing.commandSequenceArgIndent - sizing.blockBorder,
          y: currentY,
        },
        sequence: sequence.sequence,
        address: address.concat([
          ARG_COMMAND_SEQUENCE_ADDRESS_PART,
          originalSequenceIndex,
        ]),
        insertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable,
      });
      subsequenceSpecs.push(spec);
      currentY += spec.rbox.dimensions.height;
      previousSequence = sequence.sequence;
    }
  }

  const lastTooth = teeth[teeth.length - 1];
  const lastToothBottom =
    lastTooth.rbox.topLeft.y + lastTooth.rbox.dimensions.height;
  const maxToothWidth = Math.max(
    ...teeth.map((tooth) => tooth.rbox.dimensions.width)
  );
  const rbox = {
    topLeft: position,
    dimensions: {
      width: maxToothWidth,
      height: lastToothBottom - position.y,
    },
  };

  const teethWithAdjustedWidth = teeth.map((tooth) => ({
    ...tooth,
    rbox: {
      ...tooth.rbox,
      dimensions: { ...tooth.rbox.dimensions, width: maxToothWidth },
    },
  }));

  return {
    kind: RenderSpecKind.Block,
    block,
    address,
    isUnreachable,
    rbox,
    teeth: teethWithAdjustedWidth,
    labelSpecs,
    dropdownSpecs,
    subexpressionSpecs,
    subsequenceSpecs,
  };
}

function groupBlockPartsByTooth<B extends BlockEditorTypeParams>(
  parts: readonly BlockPart<B>[]
): readonly (readonly BlockPart<B>[])[] {
  const toothPartLists: (readonly BlockPart<B>[])[] = [];
  const currentToothParts: BlockPart<B>[] = [];

  for (const part of parts) {
    if (
      part.kind === BlockPartKind.Label ||
      part.kind === BlockPartKind.Dropdown ||
      part.kind === BlockPartKind.Literal ||
      part.kind === BlockPartKind.Block
    ) {
      currentToothParts.push(part);
      continue;
    }

    if (part.kind === BlockPartKind.CommandSequence) {
      toothPartLists.push(currentToothParts.slice());
      currentToothParts.splice(0, currentToothParts.length);
      continue;
    }

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const _exhaustivenessCheck: never = part;
  }

  toothPartLists.push(currentToothParts.slice());
  return toothPartLists;
}

function getCommandToothDimensionsFromParts<B extends BlockEditorTypeParams>(
  toothParts: readonly BlockPart<B>[],
  commandAddress: NodeAddress,
  totalExpressionsInPreviousTeeth: number,
  draggeeOriginalAddress: null | NodeAddress,
  hasDragPositionChangedSinceDragStart: boolean,
  dropdownMenuDropdownAddress: null | NodeAddress,
  editorProps: BlockEditorProps<B>
): Dimensions {
  const { sizing } = editorProps.config;

  const minimumNonLabelNonSequencePartX =
    getCommandBlockFemaleSocketPathPoints(sizing).ofTRe.x;

  let expressionIndex = totalExpressionsInPreviousTeeth;

  let currentX = sizing.commandBlockHorizontalPadding;
  let maxHeight = 0;
  for (let partIndex = 0; partIndex < toothParts.length; ++partIndex) {
    const part = toothParts[partIndex];
    const leftMargin =
      partIndex === 0 ? 0 : sizing.interBlockPartHorizontalMargin;
    if (part.kind === BlockPartKind.Label) {
      const dimensions = editorProps.measureText(
        part.text,
        editorProps.config.fonts.blockLabelFont,
        editorProps.config.sizing.blockLabelTextSizePx
      );
      currentX += leftMargin + dimensions.width;
      maxHeight = Math.max(maxHeight, dimensions.height);
      continue;
    }

    if (part.kind === BlockPartKind.Dropdown) {
      currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);
      const dimensions = getDropdownDimensions(part.value, editorProps);
      currentX += leftMargin + dimensions.width;
      maxHeight = Math.max(maxHeight, dimensions.height);
      continue;
    }

    if (part.kind === BlockPartKind.Literal) {
      currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);
      ++expressionIndex;
      const dimensions = getLiteralDimensions(part.source, editorProps);
      currentX += leftMargin + dimensions.width;
      maxHeight = Math.max(maxHeight, dimensions.height);
      continue;
    }

    if (part.kind === BlockPartKind.Block) {
      currentX = Math.max(currentX, minimumNonLabelNonSequencePartX);

      const localDraggeeAddress =
        draggeeOriginalAddress === null
          ? null
          : withoutPrefix(commandAddress, draggeeOriginalAddress);
      if (
        localDraggeeAddress !== null &&
        localDraggeeAddress.length === 2 &&
        localDraggeeAddress[0] === ARG_EXPRESSION_ADDRESS_PART &&
        localDraggeeAddress[1] === expressionIndex &&
        hasDragPositionChangedSinceDragStart
      ) {
        ++expressionIndex;
        const dimensions = getLiteralDimensions("", editorProps);
        currentX += leftMargin + dimensions.width;
        maxHeight = Math.max(maxHeight, dimensions.height);
        continue;
      }

      // We only need to calculate the dimensions of the block spec,
      // and can ignore the rest of the spec.
      // Therefore, we can use dummy values for the arguments that will not
      // affect the dimensions of the block spec.
      const dummyPosition = { x: 0, y: 0 };
      const dummyAddress: NodeAddress = [DRAGGEE_ADDRESS_PART];
      const dummyInsertionMark = null;
      const dummyIsUnreachable = false;

      const spec = getBlockRenderSpec({
        editorProps,
        position: dummyPosition,
        block: part.block,
        address: dummyAddress,
        insertionMark: dummyInsertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable: dummyIsUnreachable,
      });
      ++expressionIndex;
      const { dimensions } = spec.rbox;
      currentX += leftMargin + dimensions.width;
      maxHeight = Math.max(maxHeight, dimensions.height);
      continue;
    }

    if (part.kind === BlockPartKind.CommandSequence) {
      throw new Error(
        "Impossible: A tooth should never contain a command sequence."
      );
    }

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const _exhaustivenessCheck: never = part;
  }

  const minToothWidth =
    getCommandBlockFemaleSocketPathPoints(sizing).ofTRe.x +
    sizing.commandSocket.indent;

  return {
    width: Math.max(
      currentX + sizing.commandBlockHorizontalPadding,
      minToothWidth
    ),
    height:
      maxHeight +
      sizing.commandSocket.height +
      2 * sizing.commandBlockVerticalPadding,
  };
}

function getQueryRboxDimensionsFromParts<B extends BlockEditorTypeParams>(
  blockParts: readonly BlockPart<B>[],
  queryAddress: NodeAddress,
  draggeeOriginalAddress: null | NodeAddress,
  hasDragPositionChangedSinceDragStart: boolean,
  dropdownMenuDropdownAddress: null | NodeAddress,
  editorProps: BlockEditorProps<B>
): Dimensions {
  const { sizing, fonts } = editorProps.config;

  let expressionIndex = 0;

  const partDimensions: Dimensions[] = blockParts.map((part): Dimensions => {
    if (part.kind === BlockPartKind.Label) {
      return editorProps.measureText(
        part.text,
        fonts.blockLabelFont,
        sizing.blockLabelTextSizePx
      );
    }

    if (part.kind === BlockPartKind.Dropdown) {
      return getDropdownDimensions(part.value, editorProps);
    }

    if (part.kind === BlockPartKind.Literal) {
      ++expressionIndex;
      return getLiteralDimensions(part.source, editorProps);
    }

    if (part.kind === BlockPartKind.Block) {
      const localDraggeeAddress =
        draggeeOriginalAddress === null
          ? null
          : withoutPrefix(queryAddress, draggeeOriginalAddress);
      if (
        localDraggeeAddress !== null &&
        localDraggeeAddress.length === 2 &&
        localDraggeeAddress[0] === ARG_EXPRESSION_ADDRESS_PART &&
        localDraggeeAddress[1] === expressionIndex &&
        hasDragPositionChangedSinceDragStart
      ) {
        ++expressionIndex;
        return getLiteralDimensions("", editorProps);
      }

      // We only need to calculate the dimensions of the block spec,
      // and can ignore the rest of the spec.
      // Therefore, we can use dummy values for the arguments that will not
      // affect the dimensions of the block spec.
      const dummyPosition = { x: 0, y: 0 };
      const dummyAddress: NodeAddress = [DRAGGEE_ADDRESS_PART];
      const dummyInsertionMark = null;
      const dummyIsUnreachable = false;

      const spec = getBlockRenderSpec({
        editorProps,
        position: dummyPosition,
        block: part.block,
        address: dummyAddress,
        insertionMark: dummyInsertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable: dummyIsUnreachable,
      });
      ++expressionIndex;
      return spec.rbox.dimensions;
    }

    if (part.kind === BlockPartKind.CommandSequence) {
      throw new Error(
        "Impossible: A query should never contain a command sequence."
      );
    }

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const exhaustivenessCheck: never = part;
    return exhaustivenessCheck;
  });

  const totalWidth = sumArray(partDimensions.map((d) => d.width));
  const maxHeight = Math.max(...partDimensions.map((d) => d.height));
  const rboxHeight = maxHeight + 2 * sizing.queryBlockVerticalPadding;

  const radius = 0.5 * rboxHeight;

  const totalInterBlockPartHorizontalMargin =
    sizing.interBlockPartHorizontalMargin * (partDimensions.length - 1);

  const leftPadding =
    blockParts[0].kind === BlockPartKind.Label
      ? radius
      : sizing.queryBlockVerticalPadding;
  const rightPadding =
    blockParts[blockParts.length - 1].kind === BlockPartKind.Label
      ? radius
      : sizing.queryBlockVerticalPadding;

  return {
    width:
      totalWidth +
      totalInterBlockPartHorizontalMargin +
      leftPadding +
      rightPadding,
    height: rboxHeight,
  };
}

function getDropdownDimensions<B extends BlockEditorTypeParams>(
  dropdownValue: B["DropdownValueKind"],
  editorProps: BlockEditorProps<B>
): Dimensions {
  const { sizing, fonts, dropdownTexts } = editorProps.config;
  const dropdownText = dropdownTexts[dropdownValue];
  const naiveTextDimensions = editorProps.measureText(
    dropdownText,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const minimumTextDimensions = editorProps.measureText(
    MINIMAL_LOREM_IPSUM_TEXT_FOR_FONT_MEASUREMENT,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const textDimensions = {
    width: Math.max(naiveTextDimensions.width, minimumTextDimensions.width),
    height: Math.max(naiveTextDimensions.height, minimumTextDimensions.height),
  };
  const textDimensionsWithPadding = {
    width: textDimensions.width + 2 * sizing.blockDropdownHorizontalPadding,
    height: textDimensions.height + 2 * sizing.blockDropdownVerticalPadding,
  };
  const arrowDimensions = {
    width: sizing.blockDropdownArrowWidth,
    height:
      (DROPDOWN_ARROW.height * sizing.blockDropdownArrowWidth) /
      DROPDOWN_ARROW.width,
  };
  const arrowDimensionsWithMargin = {
    width: arrowDimensions.width + sizing.blockDropdownArrowLeftMargin,
    height: arrowDimensions.height + sizing.blockDropdownArrowVerticalMargin,
  };
  const dropdownRadius = 0.5 * textDimensionsWithPadding.height;
  return {
    width:
      textDimensionsWithPadding.width +
      arrowDimensionsWithMargin.width +
      2 * dropdownRadius,
    height: Math.max(
      textDimensionsWithPadding.height,
      arrowDimensionsWithMargin.height
    ),
  };
}

function getLiteralDimensions<B extends BlockEditorTypeParams>(
  literalSource: string,
  editorProps: BlockEditorProps<B>
): Dimensions {
  const { sizing, fonts } = editorProps.config;
  const naiveTextDimensions = editorProps.measureText(
    literalSource,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const minimumTextDimensions = editorProps.measureText(
    MINIMAL_LOREM_IPSUM_TEXT_FOR_FONT_MEASUREMENT,
    fonts.blockDropdownFont,
    sizing.blockDropdownTextSizePx
  );
  const textDimensions = {
    width: Math.max(naiveTextDimensions.width, minimumTextDimensions.width),
    height: Math.max(naiveTextDimensions.height, minimumTextDimensions.height),
  };
  const textDimensionsWithPadding = {
    width: textDimensions.width + 2 * sizing.blockLiteralHorizontalPadding,
    height: textDimensions.height + 2 * sizing.blockLiteralVerticalPadding,
  };
  const literalRadius = 0.5 * textDimensionsWithPadding.height;
  return {
    width: textDimensionsWithPadding.width + 2 * literalRadius,
    height: textDimensionsWithPadding.height,
  };
}

function sumArray(arr: readonly number[]): number {
  return arr.reduce((acc, x) => acc + x, 0);
}

function getQueryBlockRenderSpec<B extends BlockEditorTypeParams>({
  editorProps,
  position,
  block,
  blockDef,
  address,
  insertionMark,
  draggeeOriginalAddress,
  hasDragPositionChangedSinceDragStart,
  dropdownMenuDropdownAddress,
  isUnreachable,
}: {
  editorProps: BlockEditorProps<B>;
  position: Vec2;
  block: Block<B>;
  blockDef: QueryBlockDef<B>;
  address: NodeAddress;
  insertionMark: null | InsertionMark<B>;
  draggeeOriginalAddress: null | NodeAddress;
  hasDragPositionChangedSinceDragStart: boolean;
  dropdownMenuDropdownAddress: null | NodeAddress;
  isUnreachable: boolean;
}): BlockRenderSpec<B> {
  const { sizing, fonts } = editorProps.config;

  const localDraggeeAddress =
    draggeeOriginalAddress === null
      ? null
      : withoutPrefix(address, draggeeOriginalAddress);

  const parts = getBlockParts(blockDef, block);

  const labelSpecs: LabelRenderSpec[] = [];
  const dropdownSpecs: DropdownRenderSpec<B>[] = [];
  const subexpressionSpecs: ExpressionRenderSpec<B>[] = [];
  let currentY = position.y;

  const rboxDimensions = getQueryRboxDimensionsFromParts(
    parts,
    address,
    draggeeOriginalAddress,
    hasDragPositionChangedSinceDragStart,
    dropdownMenuDropdownAddress,
    editorProps
  );
  const queryRadius = 0.5 * rboxDimensions.height;

  const leftPadding =
    parts[0].kind === BlockPartKind.Label
      ? queryRadius
      : sizing.queryBlockVerticalPadding;

  let currentX = position.x + leftPadding;
  let dropdownIndex = 0;
  let expressionIndex = 0;

  for (const part of parts) {
    if (part.kind === BlockPartKind.Label) {
      const dimensions = editorProps.measureText(
        part.text,
        fonts.blockLabelFont,
        sizing.blockLabelTextSizePx
      );
      labelSpecs.push({
        kind: RenderSpecKind.Label,
        text: part.text,
        rbox: {
          topLeft: {
            x: currentX,
            y: currentY + 0.5 * rboxDimensions.height,
          },
          dimensions,
        },
      });
      currentX += dimensions.width + sizing.interBlockPartHorizontalMargin;
      continue;
    }

    if (part.kind === BlockPartKind.Dropdown) {
      const dropdownDimensions = getDropdownDimensions(part.value, editorProps);

      const dropdownAddress = address.concat([
        ARG_DROPDOWN_ADDRESS_PART,
        dropdownIndex,
      ]);

      dropdownSpecs.push({
        kind: RenderSpecKind.Dropdown,
        parentBlockKind: block.kind,
        value: part.value,
        hasPendingEditMark:
          dropdownMenuDropdownAddress !== null &&
          areNodeAddressesEqual(dropdownAddress, dropdownMenuDropdownAddress),
        address: dropdownAddress,
        isUnreachable,
        rbox: {
          topLeft: {
            x: currentX,
            y:
              currentY +
              0.5 * rboxDimensions.height -
              0.5 * dropdownDimensions.height,
          },
          dimensions: dropdownDimensions,
        },
      });
      ++dropdownIndex;
      currentX +=
        dropdownDimensions.width + sizing.interBlockPartHorizontalMargin;
      continue;
    }

    if (part.kind === BlockPartKind.Literal) {
      const literalDimensions = getLiteralDimensions(part.source, editorProps);
      const literalAddress = address.concat([
        ARG_EXPRESSION_ADDRESS_PART,
        expressionIndex,
      ]);

      subexpressionSpecs.push({
        kind: RenderSpecKind.Literal,
        parentBlockKind: block.kind,
        source: part.source,
        address: literalAddress,
        isUnreachable,
        hasQueryInsertionMark:
          insertionMark !== null &&
          areNodeAddressesEqual(literalAddress, insertionMark.address),
        rbox: {
          topLeft: {
            x: currentX,
            y:
              currentY +
              0.5 * rboxDimensions.height -
              0.5 * literalDimensions.height,
          },
          dimensions: literalDimensions,
        },
      });
      ++expressionIndex;
      currentX +=
        literalDimensions.width + sizing.interBlockPartHorizontalMargin;
      continue;
    }

    if (part.kind === BlockPartKind.Block) {
      if (
        localDraggeeAddress !== null &&
        localDraggeeAddress.length === 2 &&
        localDraggeeAddress[0] === ARG_EXPRESSION_ADDRESS_PART &&
        localDraggeeAddress[1] === expressionIndex &&
        hasDragPositionChangedSinceDragStart
      ) {
        const literalDimensions = getLiteralDimensions("", editorProps);
        const literalAddress = address.concat([
          ARG_EXPRESSION_ADDRESS_PART,
          expressionIndex,
        ]);

        subexpressionSpecs.push({
          kind: RenderSpecKind.Literal,
          parentBlockKind: block.kind,
          source: "",
          hasQueryInsertionMark:
            insertionMark !== null &&
            areNodeAddressesEqual(literalAddress, insertionMark.address),
          address: literalAddress,
          isUnreachable,
          rbox: {
            topLeft: {
              x: currentX,
              y:
                currentY +
                0.5 * rboxDimensions.height -
                0.5 * literalDimensions.height,
            },
            dimensions: literalDimensions,
          },
        });
        ++expressionIndex;
        currentX +=
          literalDimensions.width + sizing.interBlockPartHorizontalMargin;
        continue;
      }

      const misplacedButCorrectlySizedSpec = getBlockRenderSpec({
        editorProps,
        position: { x: 0, y: 0 },
        block: part.block,
        address: address.concat([ARG_EXPRESSION_ADDRESS_PART, expressionIndex]),
        insertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable,
      });
      const spec = getBlockRenderSpec({
        editorProps,
        position: {
          x: currentX,
          y:
            currentY +
            0.5 *
              (rboxDimensions.height -
                misplacedButCorrectlySizedSpec.rbox.dimensions.height),
        },
        block: part.block,
        address: address.concat([ARG_EXPRESSION_ADDRESS_PART, expressionIndex]),
        insertionMark,
        draggeeOriginalAddress,
        hasDragPositionChangedSinceDragStart,
        dropdownMenuDropdownAddress,
        isUnreachable,
      });
      subexpressionSpecs.push(spec);
      currentX +=
        spec.rbox.dimensions.width + sizing.interBlockPartHorizontalMargin;
      ++expressionIndex;
      continue;
    }
  }

  const rbox = {
    topLeft: position,
    dimensions: rboxDimensions,
  };

  return {
    kind: RenderSpecKind.Block,
    block,
    address,
    isUnreachable,
    rbox,
    teeth: [],
    labelSpecs,
    dropdownSpecs,
    subexpressionSpecs,
    subsequenceSpecs: [],
  };
}

function getBlockParts<B extends BlockEditorTypeParams>(
  blockDef: BlockDef<B>,
  block: Block<B>
): readonly BlockPart<B>[] {
  const { dropdowns, expressions, sequences } = block.args;

  const parts: BlockPart<B>[] = [];
  let dropdownIndex = 0;
  let expressionIndex = 0;
  let sequenceIndex = 0;

  for (const signaturePart of blockDef.signature) {
    if (signaturePart.kind === BlockSignaturePartKind.Label) {
      parts.push({
        kind: BlockPartKind.Label,
        text: signaturePart.text,
      });
      continue;
    }

    if (signaturePart.kind === BlockSignaturePartKind.Dropdown) {
      parts.push({
        kind: BlockPartKind.Dropdown,
        value: dropdowns[dropdownIndex],
      });
      ++dropdownIndex;
      continue;
    }

    if (signaturePart.kind === BlockSignaturePartKind.Expression) {
      const expression = expressions[expressionIndex];
      ++expressionIndex;

      if (expression.isLiteral) {
        parts.push({
          kind: BlockPartKind.Literal,
          source: expression.source,
        });
        continue;
      }

      parts.push({
        kind: BlockPartKind.Block,
        block: expression,
      });
      continue;
    }

    if (signaturePart.kind === BlockSignaturePartKind.CommandSequence) {
      const sequence = sequences[sequenceIndex];
      ++sequenceIndex;

      parts.push({
        kind: BlockPartKind.CommandSequence,
        sequence,
      });
      continue;
    }

    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const _exhaustivenessCheck: never = signaturePart;
  }

  return parts;
}

function getCommandBlockFemaleSocketPathPoints(
  sizing: BlockEditorSizingProps
): CommandBlockFemaleSocketPathPoints {
  const border = sizing.blockBorder;
  const bmin = sizing.commandSocket.minorBase;
  const bmaj = sizing.commandSocket.majorBase;
  const h = sizing.commandSocket.height;
  const halfDiff = (bmaj - bmin) / 2;
  const theta = Math.atan(halfDiff / h);
  const k = h / halfDiff;
  const { indent } = sizing.commandSocket;
  /** Outer corner radius */
  const ro = sizing.blockBorderRadius + sizing.blockBorder;
  /** Inner corner radius */
  const ri = sizing.blockBorderRadius;

  const ofTLs = { x: indent, y: 0 };
  const fTLc = { x: indent, y: -ro };
  const ofTLe = addVec(fTLc, {
    x: ro * Math.cos(theta),
    y: ro * Math.sin(theta),
  });
  const ofTLt = addVec(ofTLe, { x: ofTLe.y / k, y: -ofTLe.y });
  const ofBLt = addVec(ofTLt, { x: h / k, y: -h });
  const ofBLf = addVec(ofBLt, { x: -ri / k, y: ri });
  const fBLc = addVec(ofBLf, { x: ri / Math.cos(theta), y: 0 });
  const ofBLs = subVec(fBLc, {
    x: ri * Math.cos(theta),
    y: ri * Math.sin(theta),
  });
  const ofBLe = addVec(fBLc, { x: 0, y: -ri });
  const ofBRt = addVec(ofBLt, { x: bmin, y: 0 });
  const ofBRf = addVec(ofBRt, { x: ri / k, y: ri });
  const fBRc = subVec(ofBRf, { x: ri / Math.cos(theta), y: 0 });
  const ofBRs = addVec(fBRc, { x: 0, y: -ri });
  const ofBRe = addVec(fBRc, {
    x: ri * Math.cos(theta),
    y: ri * -Math.sin(theta),
  });
  const ofTRt = addVec(ofTLt, { x: bmaj, y: 0 });
  const ofTRe = addVec(ofTRt, subVec(ofTLt, ofTLs));
  const fTRc = addVec(ofTRe, { x: 0, y: -ro });
  const ofTRs = addVec(fTRc, {
    x: -ro * Math.cos(theta),
    y: ro * Math.sin(theta),
  });
  const ifTLs = addVec(ofTLs, { x: 0, y: -border });
  const ifTLe = addVec(fTLc, {
    x: ri * Math.cos(theta),
    y: ri * Math.sin(theta),
  });
  const ifBLs = subVec(fBLc, {
    x: ro * Math.cos(theta),
    y: ro * Math.sin(theta),
  });
  const ifBLe = addVec(fBLc, { x: 0, y: -ro });
  const ifBRs = addVec(fBRc, { x: 0, y: -ro });
  const ifBRe = addVec(fBRc, {
    x: ro * Math.cos(theta),
    y: -ro * Math.sin(theta),
  });
  const ifTRs = addVec(fTRc, {
    x: -ri * Math.cos(theta),
    y: ri * Math.sin(theta),
  });
  const ifTRe = addVec(fTRc, { x: 0, y: ri });

  return {
    ofTLs: invertY(ofTLs),
    ofTLe: invertY(ofTLe),
    ofBLs: invertY(ofBLs),
    ofBLe: invertY(ofBLe),
    ofBRs: invertY(ofBRs),
    ofBRe: invertY(ofBRe),
    ofTRs: invertY(ofTRs),
    ofTRe: invertY(ofTRe),

    ifTLs: invertY(ifTLs),
    ifTLe: invertY(ifTLe),
    ifBLs: invertY(ifBLs),
    ifBLe: invertY(ifBLe),
    ifBRs: invertY(ifBRs),
    ifBRe: invertY(ifBRe),
    ifTRs: invertY(ifTRs),
    ifTRe: invertY(ifTRe),
  };
}

function getMinimumCommandBlockHeight<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>
): number {
  const { sizing } = editorProps.config;
  return (
    0 + sizing.commandSocket.height + 2 * sizing.commandBlockVerticalPadding
  );
}

function getDefaultCommandBlockHeight<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>
): number {
  const { sizing, fonts } = editorProps.config;
  const textDimensions = editorProps.measureText(
    MINIMAL_LOREM_IPSUM_TEXT_FOR_FONT_MEASUREMENT,
    fonts.blockLabelFont,
    sizing.blockLabelTextSizePx
  );
  return (
    textDimensions.height +
    sizing.commandSocket.height +
    2 * sizing.commandBlockVerticalPadding
  );
}

function identityFunctionToForceTypesafeCast<T>(x: T): T {
  return x;
}

function expectNumericAddressPart(part: NodeAddressPart): number {
  if (Number.isInteger(part) && (part as number) >= 0) {
    return part as number;
  }

  throw new Error(
    `Expected numeric address part (i.e., a non-negative integer) but got the address part: ${String(
      part
    )}.`
  );
}

function isFirstCommandEntryPoint<B extends BlockEditorTypeParams>(
  defs: BlockDefs<B>,
  commands: readonly Block<B>[]
): boolean {
  if (commands.length === 0) {
    return false;
  }

  const def = defs[commands[0].kind];
  return def.kind === BlockDefKind.Command && def.isEntryPoint;
}

/**
 * If `list` starts with `prefix`, returns `list` without `prefix`.
 * Otherwise, returns `null`.
 */
function withoutPrefix<T>(
  prefix: readonly T[],
  list: readonly T[]
): null | readonly T[] {
  if (prefix.length > list.length) {
    return null;
  }

  for (let i = 0; i < prefix.length; ++i) {
    if (prefix[i] !== list[i]) {
      return null;
    }
  }

  return list.slice(prefix.length);
}

function getInsertionMarkCommand<B extends BlockEditorTypeParams>(
  draggee: BlockSequence<B>,
  defs: BlockDefs<B>
): Block<B> {
  if (draggee.length === 0) {
    throw new Error(
      "Cannot get insertion mark command for empty command sequence."
    );
  }

  const topCommand = draggee[0];
  const topDef = defs[topCommand.kind];
  if (topDef.kind !== BlockDefKind.Command) {
    throw new Error("Expected top command to be a command block.");
  }

  if (!topDef.hasFemaleSocket) {
    return draggee[draggee.length - 1];
  }

  return topCommand;
}

function translateBlockSpec<B extends BlockEditorTypeParams>(
  spec: BlockRenderSpec<B>,
  displacement: Vec2
): BlockRenderSpec<B> {
  const rbox = {
    topLeft: addVec(spec.rbox.topLeft, displacement),
    dimensions: spec.rbox.dimensions,
  };

  const teeth = spec.teeth.map((tooth) => ({
    rbox: {
      topLeft: addVec(tooth.rbox.topLeft, displacement),
      dimensions: tooth.rbox.dimensions,
    },
    hasFemaleSocket: tooth.hasFemaleSocket,
    hasMaleSocket: tooth.hasMaleSocket,
  }));

  const labelSpecs = spec.labelSpecs.map((s) =>
    translateLabelSpec(s, displacement)
  );
  const dropdownSpecs = spec.dropdownSpecs.map((s) =>
    translateDropdownSpec(s, displacement)
  );
  const subexpressionSpecs = spec.subexpressionSpecs.map((s) =>
    translateExpressionSpec(s, displacement)
  );
  const subsequenceSpecs = spec.subsequenceSpecs.map((s) =>
    translateBlockSequenceSpec(s, displacement)
  );

  return {
    kind: RenderSpecKind.Block,
    block: spec.block,
    address: spec.address,
    isUnreachable: spec.isUnreachable,
    rbox,
    teeth,
    labelSpecs,
    dropdownSpecs,
    subexpressionSpecs,
    subsequenceSpecs,
  };
}

function translateBlockSequenceSpec<B extends BlockEditorTypeParams>(
  spec: BlockSequenceRenderSpec<B>,
  displacement: Vec2
): BlockSequenceRenderSpec<B> {
  const rbox = {
    topLeft: addVec(spec.rbox.topLeft, displacement),
    dimensions: spec.rbox.dimensions,
  };

  const blockSpecs = spec.blockSpecs.map((s) =>
    translateBlockSpec(s, displacement)
  );

  return {
    kind: RenderSpecKind.BlockSequence,
    sequence: spec.sequence,
    address: spec.address,
    rbox,
    blockSpecs,
  };
}

function translateExpressionSpec<B extends BlockEditorTypeParams>(
  spec: ExpressionRenderSpec<B>,
  displacement: Vec2
): ExpressionRenderSpec<B> {
  if (spec.kind === RenderSpecKind.Literal) {
    return translateLiteralSpec(spec, displacement);
  }

  return translateBlockSpec(spec, displacement);
}

function translateLiteralSpec<B extends BlockEditorTypeParams>(
  spec: LiteralRenderSpec<B>,
  displacement: Vec2
): LiteralRenderSpec<B> {
  const rbox = {
    topLeft: addVec(spec.rbox.topLeft, displacement),
    dimensions: spec.rbox.dimensions,
  };

  return {
    kind: RenderSpecKind.Literal,
    parentBlockKind: spec.parentBlockKind,
    source: spec.source,
    hasQueryInsertionMark: spec.hasQueryInsertionMark,
    address: spec.address,
    isUnreachable: spec.isUnreachable,
    rbox,
  };
}

function translateDropdownSpec<B extends BlockEditorTypeParams>(
  spec: DropdownRenderSpec<B>,
  displacement: Vec2
): DropdownRenderSpec<B> {
  const rbox = {
    topLeft: addVec(spec.rbox.topLeft, displacement),
    dimensions: spec.rbox.dimensions,
  };

  return {
    kind: RenderSpecKind.Dropdown,
    parentBlockKind: spec.parentBlockKind,
    value: spec.value,
    hasPendingEditMark: spec.hasPendingEditMark,
    address: spec.address,
    isUnreachable: spec.isUnreachable,
    rbox,
  };
}

function translateLabelSpec(
  spec: LabelRenderSpec,
  displacement: Vec2
): LabelRenderSpec {
  const rbox = {
    topLeft: addVec(spec.rbox.topLeft, displacement),
    dimensions: spec.rbox.dimensions,
  };

  return {
    kind: RenderSpecKind.Label,
    text: spec.text,
    rbox,
  };
}

function readdressBlockSpecAndMarkAsReachableAndRemovePendingEditMark<
  B extends BlockEditorTypeParams
>(spec: BlockRenderSpec<B>, newAddress: NodeAddress): BlockRenderSpec<B> {
  return {
    kind: RenderSpecKind.Block,
    block: spec.block,
    address: newAddress,
    isUnreachable: false,
    rbox: spec.rbox,
    teeth: spec.teeth,
    labelSpecs: spec.labelSpecs,
    dropdownSpecs: spec.dropdownSpecs.map((s, i) =>
      readdressDropdownSpecAndMarkAsReachableAndRemovePendingEditMark(
        s,
        newAddress.concat([ARG_DROPDOWN_ADDRESS_PART, i])
      )
    ),
    subexpressionSpecs: spec.subexpressionSpecs.map((s, i) =>
      readdressExpressionSpecAndMarkAsReachableAndRemovePendingEditMark(
        s,
        newAddress.concat([ARG_EXPRESSION_ADDRESS_PART, i])
      )
    ),
    subsequenceSpecs: spec.subsequenceSpecs.map((s, i) =>
      readdressBlockSequenceSpecAndMarkAsReachableAndRemovePendingEditMark(
        s,
        newAddress.concat([ARG_COMMAND_SEQUENCE_ADDRESS_PART, i])
      )
    ),
  };
}

function readdressBlockSequenceSpecAndMarkAsReachableAndRemovePendingEditMark<
  B extends BlockEditorTypeParams
>(
  spec: BlockSequenceRenderSpec<B>,
  newAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  return {
    kind: RenderSpecKind.BlockSequence,
    sequence: spec.sequence,
    address: newAddress,
    rbox: spec.rbox,
    blockSpecs: spec.blockSpecs.map((s, i) =>
      readdressBlockSpecAndMarkAsReachableAndRemovePendingEditMark(
        s,
        newAddress.concat([i])
      )
    ),
  };
}

function readdressExpressionSpecAndMarkAsReachableAndRemovePendingEditMark<
  B extends BlockEditorTypeParams
>(
  spec: ExpressionRenderSpec<B>,
  newAddress: NodeAddress
): ExpressionRenderSpec<B> {
  if (spec.kind === RenderSpecKind.Literal) {
    return readdressLiteralSpecAndMarkAsReachableAndRemovePendingEditMark(
      spec,
      newAddress
    );
  }

  return readdressBlockSpecAndMarkAsReachableAndRemovePendingEditMark(
    spec,
    newAddress
  );
}

function readdressLiteralSpecAndMarkAsReachableAndRemovePendingEditMark<
  B extends BlockEditorTypeParams
>(spec: LiteralRenderSpec<B>, newAddress: NodeAddress): LiteralRenderSpec<B> {
  return {
    kind: RenderSpecKind.Literal,
    parentBlockKind: spec.parentBlockKind,
    source: spec.source,
    hasQueryInsertionMark: spec.hasQueryInsertionMark,
    address: newAddress,
    isUnreachable: false,
    rbox: spec.rbox,
  };
}

function readdressDropdownSpecAndMarkAsReachableAndRemovePendingEditMark<
  B extends BlockEditorTypeParams
>(spec: DropdownRenderSpec<B>, newAddress: NodeAddress): DropdownRenderSpec<B> {
  return {
    kind: RenderSpecKind.Dropdown,
    parentBlockKind: spec.parentBlockKind,
    value: spec.value,
    hasPendingEditMark: false,
    address: newAddress,
    isUnreachable: false,
    rbox: spec.rbox,
  };
}

function getDraggeeFromBlockSequenceUsingLocalAddress<
  B extends BlockEditorTypeParams
>(
  spec: BlockSequenceRenderSpec<B>,
  dragDisplacement: Vec2,
  localAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  const rawSpec =
    getLongestPossibleSubsequenceSpecFromSequenceSpecStartingAtLocalAddress(
      spec,
      localAddress
    );
  const translated = translateBlockSequenceSpec(rawSpec, dragDisplacement);
  return readdressBlockSequenceSpecAndMarkAsReachableAndRemovePendingEditMark(
    translated,
    [DRAGGEE_ADDRESS_PART]
  );
}

function getLongestPossibleSubsequenceSpecFromSequenceSpecStartingAtLocalAddress<
  B extends BlockEditorTypeParams
>(
  spec: BlockSequenceRenderSpec<B>,
  localAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  if (localAddress.length === 0) {
    return spec;
  }

  if (localAddress.length > 1) {
    const commandIndex = expectNumericAddressPart(localAddress[0]);
    return getLongestPossibleSubsequenceSpecFromBlockSpecStartingAtLocalAddress(
      spec.blockSpecs[commandIndex],
      localAddress.slice(1)
    );
  }

  if (localAddress.length === 1) {
    const dragIndex = expectNumericAddressPart(localAddress[0]);
    const slicedSpecs = spec.blockSpecs.slice(dragIndex);
    const lastSlicedSpec = slicedSpecs[slicedSpecs.length - 1];
    const dummyAddress: NodeAddress = [DRAGGEE_ADDRESS_PART];
    return {
      kind: RenderSpecKind.BlockSequence,
      sequence: spec.sequence.slice(dragIndex),
      address: dummyAddress,
      rbox: {
        topLeft: slicedSpecs[0].rbox.topLeft,
        dimensions: {
          width: Math.max(...slicedSpecs.map((s) => s.rbox.dimensions.width)),
          height:
            lastSlicedSpec.rbox.topLeft.y +
            lastSlicedSpec.rbox.dimensions.height -
            slicedSpecs[0].rbox.topLeft.y,
        },
      },
      blockSpecs: slicedSpecs,
    };
  }

  console.error("Invalid local address.", { spec, localAddress });
  throw new Error("Invalid local address.");
}

function getLongestPossibleSubsequenceSpecFromBlockSpecStartingAtLocalAddress<
  B extends BlockEditorTypeParams
>(
  spec: BlockRenderSpec<B>,
  localAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  if (localAddress.length === 0) {
    const dummySequenceAddress: NodeAddress = spec.address.slice(0, -1);
    return {
      kind: RenderSpecKind.BlockSequence,
      sequence: [spec.block],
      address: dummySequenceAddress,
      blockSpecs: [spec],
      rbox: spec.rbox,
    };
  }

  if (localAddress[0] === ARG_EXPRESSION_ADDRESS_PART) {
    const subexpressionIndex = expectNumericAddressPart(localAddress[1]);
    const subexpression = spec.subexpressionSpecs[subexpressionIndex];

    if (subexpression.kind === RenderSpecKind.Literal) {
      throw new Error(
        "Impossible: a block literal node is on the draggee path."
      );
    }

    return getLongestPossibleSubsequenceSpecFromBlockSpecStartingAtLocalAddress(
      subexpression,
      localAddress.slice(2)
    );
  }

  if (localAddress[0] === ARG_COMMAND_SEQUENCE_ADDRESS_PART) {
    const subsequenceIndex = expectNumericAddressPart(localAddress[1]);
    const subsequenceSpec = spec.subsequenceSpecs[subsequenceIndex];
    return getLongestPossibleSubsequenceSpecFromSequenceSpecStartingAtLocalAddress(
      subsequenceSpec,
      localAddress.slice(2)
    );
  }

  console.error("Invalid local address.", { spec, localAddress });
  throw new Error("Invalid local address.");
}

function getDataTouchRoleFromBlockAddress(address: NodeAddress): string {
  return (
    DATA_TOUCH_ROLES.blockAddressPrefix +
    address.map(stringifyNodeAddressPart).join("_")
  );
}

function getDataTouchRoleFromDropdownAddress(address: NodeAddress): string {
  return (
    DATA_TOUCH_ROLES.dropdownAddressPrefix +
    address.map(stringifyNodeAddressPart).join("_")
  );
}

function getDataTouchRoleFromLiteralAddress(address: NodeAddress): string {
  return (
    DATA_TOUCH_ROLES.literalAddressPrefix +
    address.map(stringifyNodeAddressPart).join("_")
  );
}

function stringifyNodeAddressPart(part: NodeAddressPart): string {
  if (typeof part === "string" && isNodeAddressPartString(part)) {
    return part;
  }

  if (Number.isInteger(part) && part >= 0) {
    return String(part);
  }

  throw new Error(
    `Expected address part to be a non-negative integer or a NodeAddressPartString, but got: ${String(
      part
    )}.`
  );
}

function getBlockAddressFromDataTouchRole(role: string): null | NodeAddress {
  if (!role.startsWith(DATA_TOUCH_ROLES.blockAddressPrefix)) {
    return null;
  }

  const parts = role
    .slice(DATA_TOUCH_ROLES.blockAddressPrefix.length)
    .split("_");
  const parsed = parts.map(parseNodeAddressPart);
  if (parsed.some((part) => part === null)) {
    return null;
  }

  return parsed as NodeAddress;
}

function getLiteralAddressFromDataTouchRole(role: string): null | NodeAddress {
  if (!role.startsWith(DATA_TOUCH_ROLES.literalAddressPrefix)) {
    return null;
  }

  const parts = role
    .slice(DATA_TOUCH_ROLES.literalAddressPrefix.length)
    .split("_");
  const parsed = parts.map(parseNodeAddressPart);
  if (parsed.some((part) => part === null)) {
    return null;
  }

  return parsed as NodeAddress;
}

function getDropdownAddressFromDataTouchRole(role: string): null | NodeAddress {
  if (!role.startsWith(DATA_TOUCH_ROLES.dropdownAddressPrefix)) {
    return null;
  }

  const parts = role
    .slice(DATA_TOUCH_ROLES.dropdownAddressPrefix.length)
    .split("_");
  const parsed = parts.map(parseNodeAddressPart);
  if (parsed.some((part) => part === null)) {
    return null;
  }

  return parsed as NodeAddress;
}

function parseNodeAddressPart(part: string): null | NodeAddressPart {
  if (isNodeAddressPartString(part)) {
    return part;
  }

  const asNumber = Number.parseInt(part, 10);
  if (Number.isInteger(asNumber) && asNumber >= 0) {
    return asNumber;
  }

  return null;
}

function isNodeAddressPartString(s: string): s is NodeAddressPartString {
  return Boolean(NODE_ADDRESS_PART_STRINGS[s as NodeAddressPartString]);
}

function getTouchRoleAtClientPosition(
  clientPosition: Vec2,
  editorComponentInstanceUid: string
): null | string {
  const root = document.getElementById(
    editorComponentInstanceUid + DOM_ID_SUFFIXES.root
  );
  if (root === null) {
    return null;
  }

  const elements = document.elementsFromPoint(
    clientPosition.x,
    clientPosition.y
  );
  for (const element of elements) {
    const role = getTouchRoleOrNearestAncestorTouchRole(element);
    if (role !== null && root.contains(element)) {
      return role;
    }
  }
  return null;
}

function getTouchRoleOrNearestAncestorTouchRole(
  element: Element
): null | string {
  let current: Element | null = element;
  while (current !== null) {
    const role = current.getAttribute("data-touch-role");
    if (role !== null && role.length > 0) {
      return role;
    }

    current = current.parentElement;
  }

  return null;
}

export function getCanvasItemRenderSpecs<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>
): readonly BlockSequenceRenderSpec<B>[] {
  const { state } = editorProps;
  const draggeeSpec = getDraggeeSpecOrNull(editorProps);
  const { uiTouch: nodeTouch } = state;
  const insertionMark: null | InsertionMark<B> =
    nodeTouch !== null &&
    nodeTouch.kind === UiTouchStateKind.BlockDrag &&
    nodeTouch.hasPositionChangedSinceDragStart &&
    nodeTouch.insertionMarkAddress !== null &&
    draggeeSpec !== null
      ? {
          address: nodeTouch.insertionMarkAddress,
          draggee: draggeeSpec.sequence,
        }
      : null;
  const draggeeOriginalAddress =
    nodeTouch !== null && nodeTouch.kind === UiTouchStateKind.BlockDrag
      ? nodeTouch.draggeeOriginalAddress
      : null;
  const hasDragPositionChangedSinceDragStart =
    nodeTouch !== null && nodeTouch.kind === UiTouchStateKind.BlockDrag
      ? nodeTouch.hasPositionChangedSinceDragStart
      : false;
  const dropdownMenuDropdownAddress =
    state.dropdownMenu === null ? null : state.dropdownMenu.dropdownAddress;

  return editorProps.state.canvas.items.map((item, i) =>
    getCanvasItemRenderSpec({
      editorProps,
      item,
      address: [CANVAS_ADDRESS_PART, i],
      insertionMark,
      draggeeOriginalAddress,
      hasDragPositionChangedSinceDragStart,
      dropdownMenuDropdownAddress,
    })
  );
}

function getDraggeeSpecOrNull<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>
): null | BlockSequenceRenderSpec<B> {
  const { uiTouch: nodeTouch } = editorProps.state;
  if (!(nodeTouch !== null && nodeTouch.kind === UiTouchStateKind.BlockDrag)) {
    return null;
  }

  return getDraggeeSpec(editorProps, nodeTouch);
}

function getDraggeeSpec<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>,
  drag: BlockDragState
): BlockSequenceRenderSpec<B> {
  const { canvas } = editorProps.state;

  const { draggeeOriginalAddress } = drag;
  const dragDisplacement = subVec(
    drag.currentClientPosition,
    drag.startClientPosition
  );

  if (draggeeOriginalAddress[0] === PALETTE_ADDRESS_PART) {
    const scrollDisplacement =
      canvas.scrollInProgress === null
        ? canvas.scroll
        : addVec(
            canvas.scroll,
            subVec(
              canvas.scrollInProgress.currentClientPosition,
              canvas.scrollInProgress.startClientPosition
            )
          );
    const totalDisplacement = subVec(dragDisplacement, scrollDisplacement);
    const draggeeSpecInOriginalPosition =
      getPaletteBlockRenderSpecs(editorProps)[
        expectNumericAddressPart(draggeeOriginalAddress[1])
      ];
    const translatedSpec = translateBlockSpec(
      draggeeSpecInOriginalPosition,
      totalDisplacement
    );

    const dummyAddress: NodeAddress = [DRAGGEE_ADDRESS_PART];
    return readdressBlockSequenceSpecAndMarkAsReachableAndRemovePendingEditMark(
      {
        kind: RenderSpecKind.BlockSequence,
        sequence: [translatedSpec.block],
        address: dummyAddress,
        blockSpecs: [translatedSpec],
        rbox: translatedSpec.rbox,
      },
      [DRAGGEE_ADDRESS_PART]
    );
  }

  if (draggeeOriginalAddress[0] === CANVAS_ADDRESS_PART) {
    const { state } = editorProps;
    const itemIndex = expectNumericAddressPart(draggeeOriginalAddress[1]);
    const canvasItemSpec = getCanvasItemRenderSpec({
      editorProps,
      item: state.canvas.items[itemIndex],
      address: [CANVAS_ADDRESS_PART, itemIndex],
      insertionMark: null,
      draggeeOriginalAddress: null,
      hasDragPositionChangedSinceDragStart: false,
      dropdownMenuDropdownAddress: null,
    });
    const totalDisplacement = dragDisplacement;
    return getDraggeeFromBlockSequenceUsingLocalAddress(
      canvasItemSpec,
      totalDisplacement,
      draggeeOriginalAddress.slice(2)
    );
  }

  throw new Error(
    `Impossible draggee address: [${draggeeOriginalAddress
      .map(String)
      .join(", ")}]`
  );
}

export function getPaletteBlockRenderSpecs<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>
): readonly BlockRenderSpec<B>[] {
  const { state } = editorProps;
  const { sizing, blockDefs } = editorProps.config;
  const { blockKinds, scroll, scrollInProgress } = state.palette;
  const totalScroll =
    scrollInProgress === null
      ? scroll
      : addVec(
          scroll,
          subVec(
            scrollInProgress.currentClientPosition,
            scrollInProgress.startClientPosition
          )
        );
  let y = totalScroll.y + sizing.paletteTopPadding;
  const specs: BlockRenderSpec<B>[] = [];
  for (let i = 0; i < blockKinds.length; ++i) {
    const blockKind = blockKinds[i];
    const spec = getBlockRenderSpec({
      editorProps,
      position: { x: totalScroll.x + sizing.paletteLeftPadding, y },
      block: {
        isLiteral: false,
        kind: blockKind,
        args: blockDefs[blockKind].defaultArgs,
      },
      address: [PALETTE_ADDRESS_PART, i],
      insertionMark: null,
      draggeeOriginalAddress:
        state.uiTouch !== null &&
        state.uiTouch.kind === UiTouchStateKind.BlockDrag
          ? state.uiTouch.draggeeOriginalAddress
          : null,
      hasDragPositionChangedSinceDragStart:
        state.uiTouch !== null &&
        state.uiTouch.kind === UiTouchStateKind.BlockDrag
          ? state.uiTouch.hasPositionChangedSinceDragStart
          : false,
      dropdownMenuDropdownAddress: null,
      isUnreachable: false,
    });
    specs.push(spec);

    y += spec.rbox.dimensions.height + sizing.paletteInterblockMargin;
  }
  return specs;
}

function getInsertionMarkAddressBasedOnDraggee<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>,
  canvasItemSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeSpec: BlockSequenceRenderSpec<B>,
  draggeeOriginalAddress: NodeAddress,
  dragCurrentClientPosition: Vec2
): null | NodeAddress {
  if (
    !isPointInBlockCanvasDomElement(
      dragCurrentClientPosition,
      editorProps.config.componentInstanceUid
    )
  ) {
    return null;
  }

  const { blockDefs: defs } = editorProps.config;
  const isQuery =
    defs[draggeeSpec.sequence[0].kind].kind === BlockDefKind.Query;

  if (isQuery) {
    return getQueryInsertionMarkAddressBasedOnDraggeeAssumingDragIsInBlockCanvas(
      editorProps,
      canvasItemSpecs,
      draggeeSpec
    );
  }

  return getCommandInsertionMarkAddressBasedOnDraggeeAssumingDragIsInBlockCanvas(
    editorProps,
    canvasItemSpecs,
    draggeeSpec,
    draggeeOriginalAddress
  );
}

function getQueryInsertionMarkAddressBasedOnDraggeeAssumingDragIsInBlockCanvas<
  B extends BlockEditorTypeParams
>(
  editorProps: BlockEditorProps<B>,
  canvasItemSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeSpec: BlockSequenceRenderSpec<B>
): null | NodeAddress {
  const { snapHorizontalRange, snapVerticalRange } = editorProps.config.sizing;

  const candidates = canvasItemSpecs.flatMap(
    getQueryInsertionMarkCandidatesFromBlockSequenceSpec
  );

  let closest = null;
  let minSquareDistance = Infinity;
  for (const candidate of candidates) {
    const displacement = getDisplacementBetweenQueryBodyLeftCircleCenters(
      candidate.rbox,
      draggeeSpec.rbox
    );
    const squareDistance =
      displacement.x * displacement.x + displacement.y * displacement.y;
    if (
      squareDistance < minSquareDistance &&
      Math.abs(displacement.x) <= snapHorizontalRange &&
      Math.abs(displacement.y) <= snapVerticalRange
    ) {
      closest = candidate.address;
      minSquareDistance = squareDistance;
    }
  }

  return closest;
}

function getQueryInsertionMarkCandidatesFromBlockSpec<
  B extends BlockEditorTypeParams
>(spec: BlockRenderSpec<B>): readonly QueryInsertionMarkCandidate[] {
  const candidates: QueryInsertionMarkCandidate[] = [];

  for (const subSpec of spec.subexpressionSpecs) {
    if (subSpec.kind === RenderSpecKind.Literal) {
      const literalSpec: LiteralRenderSpec<B> = subSpec;
      candidates.push({
        address: literalSpec.address,
        rbox: literalSpec.rbox,
      });
      continue;
    }

    candidates.push(...getQueryInsertionMarkCandidatesFromBlockSpec(subSpec));
  }

  for (const subSpec of spec.subsequenceSpecs) {
    candidates.push(
      ...getQueryInsertionMarkCandidatesFromBlockSequenceSpec(subSpec)
    );
  }

  return candidates;
}

function getQueryInsertionMarkCandidatesFromBlockSequenceSpec<
  B extends BlockEditorTypeParams
>(spec: BlockSequenceRenderSpec<B>): readonly QueryInsertionMarkCandidate[] {
  return spec.blockSpecs.flatMap(getQueryInsertionMarkCandidatesFromBlockSpec);
}

function getDisplacementBetweenQueryBodyLeftCircleCenters(
  a: Rectangle,
  b: Rectangle
): Vec2 {
  const aRadius = 0.5 * a.dimensions.height;
  const aCenterX = a.topLeft.x + aRadius;
  const aCenterY = a.topLeft.y + aRadius;

  const bRadius = 0.5 * b.dimensions.height;
  const bCenterX = b.topLeft.x + bRadius;
  const bCenterY = b.topLeft.y + bRadius;

  const dx = aCenterX - bCenterX;
  const dy = aCenterY - bCenterY;

  return { x: dx, y: dy };
}

function getCommandInsertionMarkAddressBasedOnDraggeeAssumingDragIsInBlockCanvas<
  B extends BlockEditorTypeParams
>(
  editorProps: BlockEditorProps<B>,
  canvasItemSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeSpec: BlockSequenceRenderSpec<B>,
  draggeeOriginalAddress: NodeAddress
): null | NodeAddress {
  const naiveAddress =
    getCommandInsertionMarkAddressBasedOnDraggeeIgnoringSocketFit(
      editorProps,
      canvasItemSpecs,
      draggeeSpec,
      draggeeOriginalAddress
    );
  if (naiveAddress === null) {
    return null;
  }

  const afterInsertion = applyDragToCanvasItems({
    editorProps,
    canvasItems: canvasItemSpecs.map(getCanvasItemFromSpec),
    draggeeSpec,
    draggeeOriginalAddress,
    insertionAddress: naiveAddress,
  });
  if (
    doCanvasItemsContainIllegalSocketMatches(
      afterInsertion,
      editorProps.config.blockDefs
    )
  ) {
    return null;
  }

  const { canvas } = editorProps.state;
  const totalScroll =
    canvas.scrollInProgress === null
      ? canvas.scroll
      : addVec(
          canvas.scroll,
          subVec(
            canvas.scrollInProgress.currentClientPosition,
            canvas.scrollInProgress.startClientPosition
          )
        );
  const { canvasBounds } = editorProps.config.sizing;
  const translatedCanvasBounds = {
    topLeft: subVec(canvasBounds.topLeft, totalScroll),
    dimensions: canvasBounds.dimensions,
  };
  if (!doRectanglesOverlap(draggeeSpec.rbox, translatedCanvasBounds)) {
    return null;
  }

  return naiveAddress;
}

function getCanvasItemFromSpec<B extends BlockEditorTypeParams>(
  spec: BlockSequenceRenderSpec<B>
): CanvasItem<B> {
  return {
    position: spec.rbox.topLeft,
    content: spec.sequence,
  };
}

function applyDragToCanvasItems<B extends BlockEditorTypeParams>({
  editorProps,
  canvasItems: originalCanvasItems,
  draggeeSpec,
  draggeeOriginalAddress,
  insertionAddress,
}: {
  editorProps: BlockEditorProps<B>;
  canvasItems: readonly CanvasItem<B>[];
  draggeeSpec: BlockSequenceRenderSpec<B>;
  draggeeOriginalAddress: NodeAddress;
  insertionAddress: null | NodeAddress;
}): readonly CanvasItem<B>[] {
  // We don't prune empty block sequences until right before we return,
  // since doing so could invalidate the insertion address.
  const afterRemoval =
    draggeeOriginalAddress[0] === CANVAS_ADDRESS_PART
      ? applyRemovalToCanvasItemsWithoutPruningEmptyBlockSequences(
          originalCanvasItems,
          draggeeOriginalAddress
        )
      : originalCanvasItems;

  // If the dropped block is outside the canvas bounds, we simply discard it.
  const { uiTouch } = editorProps.state;
  const isBlockDragOutsideCanvas =
    uiTouch !== null &&
    uiTouch.kind === UiTouchStateKind.BlockDrag &&
    !isPointInBlockCanvasDomElement(
      uiTouch.currentClientPosition,
      editorProps.config.componentInstanceUid
    );
  if (isBlockDragOutsideCanvas) {
    return doesBlockSequenceContainUndeletableBlock(
      draggeeSpec.sequence,
      editorProps.config.blockDefs
    )
      ? originalCanvasItems
      : afterRemoval.filter((item) => item.content.length > 0);
  }

  // We either _insert_ the draggee, or we simply _release_ it as-is.

  const draggee = draggeeSpec.sequence;
  const afterInsertionOrDisplacement =
    // If the draggee is close to a compatible block,
    // we insert it there.
    insertionAddress !== null
      ? applyInsertionToCanvasItems(
          editorProps,
          afterRemoval,
          draggee,
          insertionAddress
        )
      : // Otherwise, we release it.
        afterRemoval.concat([getCanvasItemFromSpec(draggeeSpec)]);

  return afterInsertionOrDisplacement.filter((item) => item.content.length > 0);
}

function applyRemovalToCanvasItemsWithoutPruningEmptyBlockSequences<
  B extends BlockEditorTypeParams
>(
  canvasItems: readonly CanvasItem<B>[],
  removalAddress: NodeAddress
): readonly CanvasItem<B>[] {
  return canvasItems.map((originalItem, itemIndex) => {
    const localAddress: null | NodeAddress = withoutPrefix(
      [CANVAS_ADDRESS_PART, itemIndex],
      removalAddress
    );
    const updatedContent = removeNodeAtLocalAddressFromBlockSequence(
      originalItem.content,
      localAddress
    );
    return { position: originalItem.position, content: updatedContent };
  });
}

function removeNodeAtLocalAddressFromBlock<B extends BlockEditorTypeParams>(
  block: Block<B>,
  localAddress: null | NodeAddress
): Block<B> {
  if (localAddress === null) {
    return block;
  }

  if (localAddress.length < 2) {
    throw new Error(
      "Illegal localAddress passed to removeNodeAtLocalAddressFromBlock."
    );
  }

  if (
    localAddress.length === 2 &&
    localAddress[0] === ARG_EXPRESSION_ADDRESS_PART
  ) {
    const removalIndex = expectNumericAddressPart(localAddress[1]);
    return {
      ...block,
      args: {
        ...block.args,
        expressions: block.args.expressions.map((original, i) =>
          i === removalIndex ? { isLiteral: true, source: "" } : original
        ),
      },
    };
  }

  return {
    isLiteral: false,
    kind: block.kind,
    args: {
      dropdowns: block.args.dropdowns,
      expressions: block.args.expressions.map((expression, expressionIndex) =>
        expression.isLiteral
          ? expression
          : removeNodeAtLocalAddressFromBlock(
              expression,
              withoutPrefix(
                [ARG_EXPRESSION_ADDRESS_PART, expressionIndex],
                localAddress
              )
            )
      ),
      sequences: block.args.sequences.map((sequence, sequenceIndex) =>
        removeNodeAtLocalAddressFromBlockSequence(
          sequence,
          withoutPrefix(
            [ARG_COMMAND_SEQUENCE_ADDRESS_PART, sequenceIndex],
            localAddress
          )
        )
      ),
    },
  };
}

function removeNodeAtLocalAddressFromBlockSequence<
  B extends BlockEditorTypeParams
>(
  sequence: BlockSequence<B>,
  localAddress: null | NodeAddress
): BlockSequence<B> {
  if (localAddress === null) {
    return sequence;
  }

  if (localAddress.length < 1) {
    throw new Error(
      "Illegal localAddress passed to removeNodeAtLocalAddressFromBlockSequence."
    );
  }

  if (localAddress.length === 1) {
    const removalIndex = expectNumericAddressPart(localAddress[0]);
    return sequence.slice(0, removalIndex);
  }

  return sequence.map((original, i) =>
    removeNodeAtLocalAddressFromBlock(
      original,
      withoutPrefix([i], localAddress)
    )
  );
}

function applyInsertionToCanvasItems<B extends BlockEditorTypeParams>(
  editorProps: BlockEditorProps<B>,
  canvasItems: readonly CanvasItem<B>[],
  draggee: BlockSequence<B>,
  insertionAddress: NodeAddress
): readonly CanvasItem<B>[] {
  return canvasItems.map((originalItem, itemIndex) => {
    const localAddress: null | NodeAddress = withoutPrefix(
      [CANVAS_ADDRESS_PART, itemIndex],
      insertionAddress
    );
    const updatedContent = insertNodeAtLocalAddressIntoBlockSequence(
      originalItem.content,
      draggee,
      localAddress
    );

    const adjustedPosition =
      localAddress !== null &&
      localAddress.length === 1 &&
      localAddress[0] === 0
        ? subVec(originalItem.position, {
            x: 0,
            y: getBlockSequenceRenderSpec({
              editorProps,
              position: { x: 0, y: 0 },
              address: insertionAddress,
              insertionMark: null,
              draggeeOriginalAddress: null,
              hasDragPositionChangedSinceDragStart: false,
              dropdownMenuDropdownAddress: null,
              isUnreachable: false,
              sequence: draggee,
            }).rbox.dimensions.height,
          })
        : originalItem.position;
    return { position: adjustedPosition, content: updatedContent };
  });
}

function insertNodeAtLocalAddressIntoBlock<B extends BlockEditorTypeParams>(
  block: Block<B>,
  draggee: BlockSequence<B>,
  localAddress: null | NodeAddress
): Block<B> {
  if (localAddress === null) {
    return block;
  }

  if (localAddress.length < 2) {
    throw new Error(
      "Illegal localAddress passed to insertNodeAtLocalAddressIntoBlock."
    );
  }

  if (
    localAddress.length === 2 &&
    localAddress[0] === ARG_EXPRESSION_ADDRESS_PART
  ) {
    if (draggee.length > 1) {
      throw new Error(
        "Impossible: a block sequence with more than one block is being inserted into an expression slot."
      );
    }

    const insertionIndex = expectNumericAddressPart(localAddress[1]);
    return {
      ...block,
      args: {
        ...block.args,
        expressions: block.args.expressions.map((original, index) =>
          index === insertionIndex ? draggee[0] : original
        ),
      },
    };
  }

  return {
    isLiteral: false,
    kind: block.kind,
    args: {
      dropdowns: block.args.dropdowns,
      expressions: block.args.expressions.map((expression, expressionIndex) =>
        expression.isLiteral
          ? expression
          : insertNodeAtLocalAddressIntoBlock(
              expression,
              draggee,
              withoutPrefix(
                [ARG_EXPRESSION_ADDRESS_PART, expressionIndex],
                localAddress
              )
            )
      ),
      sequences: block.args.sequences.map((sequence, sequenceIndex) =>
        insertNodeAtLocalAddressIntoBlockSequence(
          sequence,
          draggee,
          withoutPrefix(
            [ARG_COMMAND_SEQUENCE_ADDRESS_PART, sequenceIndex],
            localAddress
          )
        )
      ),
    },
  };
}

function insertNodeAtLocalAddressIntoBlockSequence<
  B extends BlockEditorTypeParams
>(
  sequence: BlockSequence<B>,
  draggee: BlockSequence<B>,
  localAddress: null | NodeAddress
): BlockSequence<B> {
  if (localAddress === null) {
    return sequence;
  }

  if (localAddress.length < 1) {
    throw new Error(
      "Illegal localAddress passed to insertNodeAtLocalAddressIntoBlockSequence."
    );
  }

  if (localAddress.length === 1) {
    const insertionIndex = expectNumericAddressPart(localAddress[0]);
    return [
      ...sequence.slice(0, insertionIndex),
      ...draggee,
      ...sequence.slice(insertionIndex),
    ];
  }

  return sequence.map((original, i) =>
    insertNodeAtLocalAddressIntoBlock(
      original,
      draggee,
      withoutPrefix([i], localAddress)
    )
  );
}

function doCanvasItemsContainIllegalSocketMatches<
  B extends BlockEditorTypeParams
>(canvasItems: readonly CanvasItem<B>[], defs: BlockDefs<B>): boolean {
  return canvasItems.some((item) => {
    const blocks = item.content;
    if (
      blocks.length === 0 ||
      defs[blocks[0].kind].kind === BlockDefKind.Query
    ) {
      return false;
    }
    const firstCommand = blocks[0];
    if (blocks.length === 1) {
      return doBlockSubsequencesContainMaleOrNonFinalFemaleSocket(
        firstCommand,
        defs
      );
    }

    const firstCommandDef = defs[firstCommand.kind];
    const isFirstCommandIllegal =
      (firstCommandDef.kind === BlockDefKind.Command &&
        !firstCommandDef.hasMaleSocket) ||
      doBlockSubsequencesContainMaleOrNonFinalFemaleSocket(firstCommand, defs);
    if (isFirstCommandIllegal) {
      return true;
    }

    const { length } = blocks;

    return blocks.slice(1).some((command, commandIndexMinusOne) => {
      const def = defs[command.kind];
      if (def.kind !== BlockDefKind.Command) {
        return false;
      }

      const commandIndex = commandIndexMinusOne + 1;
      const isMale = def.hasMaleSocket && !def.hasFemaleSocket;
      const isNonFinalFemaleSocket =
        !def.hasMaleSocket &&
        def.hasFemaleSocket &&
        commandIndex !== length - 1;
      return (
        isMale ||
        isNonFinalFemaleSocket ||
        doBlockSubsequencesContainMaleOrNonFinalFemaleSocket(command, defs)
      );
    });
  });
}

function doBlockSubsequencesContainMaleOrNonFinalFemaleSocket<
  B extends BlockEditorTypeParams
>(block: Block<B>, defs: BlockDefs<B>): boolean {
  return block.args.sequences.some((sequence) =>
    doesBlockSequenceContainMaleOrNonFinalFemaleSocket(sequence, defs)
  );
}

function doesBlockSequenceContainMaleOrNonFinalFemaleSocket<
  B extends BlockEditorTypeParams
>(sequence: BlockSequence<B>, defs: BlockDefs<B>): boolean {
  const { length } = sequence;
  return sequence.some((command, commandIndex) => {
    const def = defs[command.kind];
    if (def.kind !== BlockDefKind.Command) {
      return false;
    }

    // The code below has some seemingly unnecessary type annotations.
    // However, at the time of writing, if I remove them,
    // the linter will trigger a @typescript-eslint/strict-boolean-expressions warning.
    // I believe this is a false positive (i.e., bug).
    // But until it gets fixed, I'll keep the type annotations.
    const { hasFemaleSocket, hasMaleSocket } =
      identityFunctionToForceTypesafeCast<{
        hasFemaleSocket: boolean;
        hasMaleSocket: boolean;
      }>(def);
    const isMale: boolean = hasMaleSocket && !hasFemaleSocket;
    const isNonFinalFemaleSocket =
      !hasMaleSocket && hasFemaleSocket && commandIndex !== length - 1;
    return (
      isMale ||
      isNonFinalFemaleSocket ||
      doBlockSubsequencesContainMaleOrNonFinalFemaleSocket(command, defs)
    );
  });
}

function getCommandInsertionMarkAddressBasedOnDraggeeIgnoringSocketFit<
  B extends BlockEditorTypeParams
>(
  editorProps: BlockEditorProps<B>,
  canvasItemSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeSpec: BlockSequenceRenderSpec<B>,
  draggeeOriginalAddress: NodeAddress
): null | NodeAddress {
  for (let i = canvasItemSpecs.length - 1; i >= 0; --i) {
    const address =
      getCommandInsertionMarkAddressBasedOnDraggeeWithinBlocklikeSpec(
        editorProps,
        canvasItemSpecs[i],
        draggeeSpec,
        canvasItemSpecs,
        draggeeOriginalAddress
      );
    if (address !== null) {
      return address;
    }
  }

  return null;
}

function getCommandInsertionMarkAddressBasedOnDraggeeWithinBlocklikeSpec<
  B extends BlockEditorTypeParams
>(
  editorProps: BlockEditorProps<B>,
  spec: BlockSequenceRenderSpec<B>,
  draggeeSpec: BlockSequenceRenderSpec<B>,
  canvasItemRenderSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeOriginalAddress: NodeAddress
): null | NodeAddress {
  const sortedCandidates =
    getCommandInsertionMarkCandidatesFromBlockSequenceSpec(
      spec,
      draggeeSpec,
      canvasItemRenderSpecs,
      draggeeOriginalAddress
    )
      .slice()
      .sort((a, b) => a.rbox.topLeft.y - b.rbox.topLeft.y);

  if (sortedCandidates.length === 0) {
    return null;
  }

  const { snapHorizontalRange, snapVerticalRange } = editorProps.config.sizing;

  const draggeeLeft = draggeeSpec.rbox.topLeft.x;
  const draggeeTop = draggeeSpec.rbox.topLeft.y;
  const draggeeBottom = draggeeTop + draggeeSpec.rbox.dimensions.height;

  const topCandidate = sortedCandidates[0];
  const topCandidateLeft = topCandidate.rbox.topLeft.x;
  const topCandidateTop = topCandidate.rbox.topLeft.y;
  const xDistanceFromTopCandidate = Math.abs(draggeeLeft - topCandidateLeft);
  if (
    draggeeTop <= topCandidateTop &&
    Math.max(0, topCandidateTop - draggeeBottom) <= snapVerticalRange &&
    xDistanceFromTopCandidate <= snapHorizontalRange
  ) {
    return topCandidate.topAddress;
  }

  const bottomCandidate = sortedCandidates[sortedCandidates.length - 1];
  const bottomCandidateLeft = bottomCandidate.rbox.topLeft.x;
  const bottomCandidateTop = bottomCandidate.rbox.topLeft.y;
  const bottomCandidateBottom =
    bottomCandidateTop + bottomCandidate.rbox.dimensions.height;
  const xDistanceFromBottomCandidate = Math.abs(
    draggeeLeft - bottomCandidateLeft
  );
  if (
    draggeeTop >= bottomCandidateTop &&
    Math.max(0, draggeeTop - bottomCandidateBottom) <= snapVerticalRange &&
    xDistanceFromBottomCandidate <= snapHorizontalRange
  ) {
    return bottomCandidate.bottomAddress;
  }

  for (
    let currentIndex = 1;
    currentIndex < sortedCandidates.length;
    ++currentIndex
  ) {
    const current = sortedCandidates[currentIndex];
    const currentLeft = current.rbox.topLeft.x;
    const currentTop = current.rbox.topLeft.y;
    const previous = sortedCandidates[currentIndex - 1];
    const previousLeft = previous.rbox.topLeft.x;
    const previousTop = previous.rbox.topLeft.y;
    const xDistance = Math.min(
      Math.abs(draggeeLeft - currentLeft),
      Math.abs(draggeeLeft - previousLeft)
    );
    if (
      previousTop <= draggeeTop &&
      draggeeTop <= currentTop &&
      xDistance <= snapHorizontalRange
    ) {
      if (current.topAddress !== null) {
        return current.topAddress;
      }
      return previous.bottomAddress;
    }
  }

  return null;
}

function getCommandInsertionMarkCandidatesFromBlockSpec<
  B extends BlockEditorTypeParams
>(
  spec: BlockRenderSpec<B>,
  draggeeeSpec: BlockSequenceRenderSpec<B>,
  canvasItemContents: readonly BlockSequenceRenderSpec<B>[],
  draggeeOriginalAddress: NodeAddress
): readonly CommandInsertionMarkCandidate[] {
  if (
    spec.address.length === 1 &&
    spec.address[0] === INSERTION_MARK_ADDRESS_PART
  ) {
    return [];
  }

  const candidates: CommandInsertionMarkCandidate[] = [];

  for (let toothIndex = 0; toothIndex < spec.teeth.length; ++toothIndex) {
    const tooth = spec.teeth[toothIndex];
    const topAddress = getToothTopAddress(
      spec.address,
      toothIndex,
      canvasItemContents
    );
    const bottomAddress = getToothBottomAddress(
      spec.address,
      toothIndex,
      toothIndex === spec.teeth.length - 1
    );
    candidates.push({ topAddress, bottomAddress, rbox: tooth.rbox });
  }

  for (const subSpec of spec.subsequenceSpecs) {
    candidates.push(
      ...getCommandInsertionMarkCandidatesFromBlockSequenceSpec(
        subSpec,
        draggeeeSpec,
        canvasItemContents,
        draggeeOriginalAddress
      )
    );
  }

  return candidates;
}

function getCommandInsertionMarkCandidatesFromBlockSequenceSpec<
  B extends BlockEditorTypeParams
>(
  sequenceSpec: BlockSequenceRenderSpec<B>,
  draggeeSpec: BlockSequenceRenderSpec<B>,
  canvasItemRenderSpecs: readonly BlockSequenceRenderSpec<B>[],
  draggeeOriginalAddress: NodeAddress
): readonly CommandInsertionMarkCandidate[] {
  return sequenceSpec.blockSpecs.flatMap((commandSpec) => {
    return getCommandInsertionMarkCandidatesFromBlockSpec(
      commandSpec,
      draggeeSpec,
      canvasItemRenderSpecs,
      draggeeOriginalAddress
    );
  });
}

function getToothTopAddress<B extends BlockEditorTypeParams>(
  commandAddress: NodeAddress,
  toothIndex: number,
  canvasItemRenderSpecs: readonly BlockSequenceRenderSpec<B>[]
): null | NodeAddress {
  if (toothIndex === 0) {
    return commandAddress;
  }

  const sequenceAboveThisToothAddress = commandAddress.concat([
    ARG_COMMAND_SEQUENCE_ADDRESS_PART,
    toothIndex - 1,
  ]);
  const specForSequenceAboveThisTooth =
    getBlockSequenceSpecFromAncestorBlockSequenceSpecAndLocalAddress(
      canvasItemRenderSpecs[expectNumericAddressPart(commandAddress[1])],
      sequenceAboveThisToothAddress.slice(2)
    );

  return sequenceAboveThisToothAddress.concat([
    specForSequenceAboveThisTooth.blockSpecs.filter((spec) => {
      const isInsertionMark =
        spec.address.length === 1 &&
        spec.address[0] === INSERTION_MARK_ADDRESS_PART;
      return !isInsertionMark;
    }).length,
  ]);
}

function getBlockSequenceSpecFromAncestorBlockSequenceSpecAndLocalAddress<
  B extends BlockEditorTypeParams
>(
  spec: BlockSequenceRenderSpec<B>,
  localAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  if (localAddress.length === 0) {
    return spec;
  }

  const subSpec = spec.blockSpecs.filter(
    (spec) =>
      !(
        spec.address.length === 1 &&
        spec.address[0] === INSERTION_MARK_ADDRESS_PART
      )
  )[expectNumericAddressPart(localAddress[0])];
  return getBlockSequenceSpecFromAncestorBlockSpecAndLocalAddress(
    subSpec,
    localAddress.slice(1)
  );
}

function getBlockSequenceSpecFromAncestorBlockSpecAndLocalAddress<
  B extends BlockEditorTypeParams
>(
  spec: BlockRenderSpec<B>,
  localAddress: NodeAddress
): BlockSequenceRenderSpec<B> {
  if (localAddress.length === 0) {
    throw new Error("Invalid local address.");
  }

  if (localAddress[0] === ARG_EXPRESSION_ADDRESS_PART) {
    const subSpec =
      spec.subexpressionSpecs[expectNumericAddressPart(localAddress[1])];
    if (subSpec.kind === RenderSpecKind.Block) {
      return getBlockSequenceSpecFromAncestorBlockSpecAndLocalAddress(
        subSpec,
        localAddress.slice(2)
      );
    }
  }

  if (localAddress[0] === ARG_COMMAND_SEQUENCE_ADDRESS_PART) {
    const subSpec =
      spec.subsequenceSpecs[expectNumericAddressPart(localAddress[1])];

    return getBlockSequenceSpecFromAncestorBlockSequenceSpecAndLocalAddress(
      subSpec,
      localAddress.slice(2)
    );
  }

  throw new Error("Invalid local address.");
}

function getToothBottomAddress(
  commandAddress: NodeAddress,
  toothIndex: number,
  isLastTooth: boolean
): null | NodeAddress {
  if (isLastTooth) {
    const lastPart = expectNumericAddressPart(
      commandAddress[commandAddress.length - 1]
    );
    return commandAddress.slice(0, -1).concat(lastPart + 1);
  }

  return commandAddress.concat([
    ARG_COMMAND_SEQUENCE_ADDRESS_PART,
    toothIndex,
    0,
  ]);
}

function doRectanglesOverlap(a: Rectangle, b: Rectangle): boolean {
  const aLeft = a.topLeft.x;
  const aRight = aLeft + a.dimensions.width;
  const aTop = a.topLeft.y;
  const aBottom = aTop + a.dimensions.height;

  const bLeft = b.topLeft.x;
  const bRight = bLeft + b.dimensions.width;
  const bTop = b.topLeft.y;
  const bBottom = bTop + b.dimensions.height;

  return aLeft < bRight && aRight > bLeft && aTop < bBottom && aBottom > bTop;
}

export function areNodeAddressesEqual(a: NodeAddress, b: NodeAddress): boolean {
  if (a.length !== b.length) {
    return false;
  }

  for (let i = 0; i < a.length; ++i) {
    if (a[i] !== b[i]) {
      return false;
    }
  }

  return true;
}

function doesBlockSequenceContainUndeletableBlock<
  B extends BlockEditorTypeParams
>(sequence: BlockSequence<B>, defs: BlockDefs<B>): boolean {
  return sequence.some((block) =>
    isBlockOrAnyDescendantUndeletable(block, defs)
  );
}

function isBlockOrAnyDescendantUndeletable<B extends BlockEditorTypeParams>(
  block: Block<B>,
  defs: BlockDefs<B>
): boolean {
  const isUndeletable = !defs[block.kind].isDeletable;
  if (isUndeletable) {
    return true;
  }

  for (const expression of block.args.expressions) {
    if (expression.isLiteral) {
      continue;
    }

    if (isBlockOrAnyDescendantUndeletable(expression, defs)) {
      return true;
    }
  }

  for (const sequence of block.args.sequences) {
    if (doesBlockSequenceContainUndeletableBlock(sequence, defs)) {
      return true;
    }
  }

  return false;
}

function areVectorsEqual(a: Vec2, b: Vec2): boolean {
  return a.x === b.x && a.y === b.y;
}

function getSubexpressionParentBlockSpec<B extends BlockEditorTypeParams>(
  props: BlockEditorProps<B>,
  subexpressionAddress: NodeAddress
): BlockRenderSpec<B> {
  return getLongestPossibleSubsequenceSpecFromSequenceSpecStartingAtLocalAddress(
    getCanvasItemRenderSpecs(props)[
      expectNumericAddressPart(subexpressionAddress[1])
    ],
    subexpressionAddress.slice(2, -2)
  ).blockSpecs[0];
}

function getDropdownParentBlockSpec<B extends BlockEditorTypeParams>(
  props: BlockEditorProps<B>,
  dropdownAddress: NodeAddress
): BlockRenderSpec<B> {
  return getLongestPossibleSubsequenceSpecFromSequenceSpecStartingAtLocalAddress(
    getCanvasItemRenderSpecs(props)[
      expectNumericAddressPart(dropdownAddress[1])
    ],
    dropdownAddress.slice(2, -2)
  ).blockSpecs[0];
}

function DropdownMenu<B extends BlockEditorTypeParams>({
  menu,
  editorProps,
}: {
  menu: DropdownMenuState<B>;
  editorProps: BlockEditorProps<B>;
}): React.ReactElement {
  const { sizing, colors, fonts } = editorProps.config;
  const { dropdownAddress } = menu;
  const dropdownParentBlockSpec = getDropdownParentBlockSpec(
    editorProps,
    dropdownAddress
  );

  const menuSpec = getDropdownMenuRenderSpec({ menu, editorProps });

  const blockColorTriplet = dropdownParentBlockSpec.isUnreachable
    ? colors.unreachableBlocks[dropdownParentBlockSpec.block.kind]
    : colors.blocks[dropdownParentBlockSpec.block.kind];

  const clipPath = {
    topLeft: addVec(menuSpec.rbox.topLeft, {
      x: sizing.blockDropdownMenuBorder,
      y: sizing.blockDropdownMenuBorder,
    }),
    dimensions: {
      width:
        menuSpec.rbox.dimensions.width - 2 * sizing.blockDropdownMenuBorder,
      height:
        menuSpec.rbox.dimensions.height - 2 * sizing.blockDropdownMenuBorder,
    },
  };

  return (
    <>
      <defs>
        <clipPath
          id={
            editorProps.config.componentInstanceUid +
            DOM_ID_SUFFIXES.dropdownMenuClipPath
          }
        >
          <rect
            x={clipPath.topLeft.x}
            y={clipPath.topLeft.y}
            rx={
              sizing.blockDropdownMenuBorderRadius -
              sizing.blockDropdownMenuBorder
            }
            ry={
              sizing.blockDropdownMenuBorderRadius -
              sizing.blockDropdownMenuBorder
            }
            width={clipPath.dimensions.width}
            height={clipPath.dimensions.height}
          />
        </clipPath>
      </defs>

      <rect
        x={menuSpec.rbox.topLeft.x}
        y={menuSpec.rbox.topLeft.y}
        rx={sizing.blockDropdownMenuBorderRadius}
        ry={sizing.blockDropdownMenuBorderRadius}
        width={menuSpec.rbox.dimensions.width}
        height={menuSpec.rbox.dimensions.height}
        fill={stringifyColor(blockColorTriplet.tertiary)}
      />

      <g
        clipPath={`url(#${editorProps.config.componentInstanceUid}${DOM_ID_SUFFIXES.dropdownMenuClipPath})`}
      >
        {menuSpec.choiceSpecs.map((choiceSpec, choiceIndex) => (
          <g
            key={choiceIndex}
            data-touch-role={
              DATA_TOUCH_ROLES.dropdownChoicePrefix + choiceIndex
            }
          >
            <rect
              x={choiceSpec.rbox.topLeft.x}
              y={choiceSpec.rbox.topLeft.y}
              width={choiceSpec.rbox.dimensions.width}
              height={choiceSpec.rbox.dimensions.height}
              fill={stringifyColor(blockColorTriplet.primary)}
            />

            <text
              x={choiceSpec.textRbox.topLeft.x}
              y={choiceSpec.textRbox.topLeft.y}
              dominantBaseline="middle"
              fontFamily={fonts.blockDropdownMenuFont}
              fontSize={sizing.blockDropdownMenuTextSizePx}
              fill={stringifyColor(colors.blockDropdownText)}
            >
              {choiceSpec.text}
            </text>

            {choiceIndex > 0 && (
              <rect
                x={choiceSpec.rbox.topLeft.x}
                y={
                  choiceSpec.rbox.topLeft.y + choiceSpec.rbox.dimensions.height
                }
                width={choiceSpec.rbox.dimensions.width}
                height={sizing.blockDropdownMenuInterChoiceDividerHeight}
                fill={stringifyColor(blockColorTriplet.tertiary)}
              />
            )}
          </g>
        ))}
      </g>
    </>
  );
}

function getDropdownMenuRenderSpec<B extends BlockEditorTypeParams>({
  menu,
  editorProps,
}: {
  menu: DropdownMenuState<B>;
  editorProps: BlockEditorProps<B>;
}): DropdownMenuRenderSpec {
  const { state } = editorProps;
  const { sizing, fonts, dropdownTexts } = editorProps.config;
  const { canvas } = state;
  const totalCanvasScroll =
    canvas.scrollInProgress === null
      ? canvas.scroll
      : addVec(
          canvas.scroll,
          subVec(
            canvas.scrollInProgress.currentClientPosition,
            canvas.scrollInProgress.startClientPosition
          )
        );
  const { dropdownAddress } = menu;
  const dropdownParentBlockSpec = getDropdownParentBlockSpec(
    editorProps,
    dropdownAddress
  );
  const dropdownIndex = expectNumericAddressPart(
    dropdownAddress[dropdownAddress.length - 1]
  );
  const dropdownSpec = dropdownParentBlockSpec.dropdownSpecs[dropdownIndex];
  const dropdownSvgCoords = addVec(
    dropdownSpec.rbox.topLeft,
    totalCanvasScroll
  );
  const dropdownSvgCenter = addVec(dropdownSvgCoords, {
    x: 0.5 * dropdownSpec.rbox.dimensions.width,
    y: 0.5 * dropdownSpec.rbox.dimensions.height,
  });
  const canvasCenter = addVec(sizing.canvasBounds.topLeft, {
    x: 0.5 * sizing.canvasBounds.dimensions.width,
    y: 0.5 * sizing.canvasBounds.dimensions.height,
  });
  const totalMenuScroll =
    state.uiTouch !== null &&
    state.uiTouch.kind === UiTouchStateKind.DropdownMenuScroll
      ? addVec(
          menu.scroll,
          subVec(
            state.uiTouch.currentClientPosition,
            state.uiTouch.startClientPosition
          )
        )
      : menu.scroll;

  const choiceSpecsWithUnadjustedRboxes: DropdownMenuChoiceRenderSpec[] = [];
  let currentY = totalMenuScroll.y + sizing.blockDropdownMenuBorder;
  for (const choice of menu.choices) {
    const choiceText = dropdownTexts[choice];
    const textDimensions = editorProps.measureText(
      choiceText,
      fonts.blockDropdownMenuFont,
      sizing.blockDropdownMenuTextSizePx
    );

    const rbox = {
      topLeft: {
        x:
          dropdownSvgCoords.x +
          totalMenuScroll.x +
          sizing.blockDropdownMenuBorder,
        y: currentY,
      },
      dimensions: {
        width:
          textDimensions.width + 2 * sizing.blockDropdownMenuHorizontalPadding,
        height:
          textDimensions.height +
          2 * sizing.blockDropdownMenuChoiceVerticalMargin,
      },
    };

    choiceSpecsWithUnadjustedRboxes.push({
      text: choiceText,
      textRbox: {
        topLeft: {
          x:
            dropdownSvgCoords.x +
            totalMenuScroll.x +
            sizing.blockDropdownMenuBorder +
            sizing.blockDropdownMenuHorizontalPadding,
          y:
            sizing.blockDropdownMenuChoiceVerticalMargin +
            currentY +
            0.5 * textDimensions.height,
        },
        dimensions: {
          width: textDimensions.width,
          height: textDimensions.height,
        },
      },
      rbox,
    });

    currentY +=
      rbox.dimensions.height + sizing.blockDropdownMenuInterChoiceDividerHeight;
  }

  const maxChoiceSpecWidth = Math.max(
    ...choiceSpecsWithUnadjustedRboxes.map((s) => s.rbox.dimensions.width)
  );

  const lastUnadjustedRbox =
    choiceSpecsWithUnadjustedRboxes[choiceSpecsWithUnadjustedRboxes.length - 1]
      .rbox;
  const menuHeight = Math.min(
    sizing.blockDropdownMenuDimensions.height,
    lastUnadjustedRbox.topLeft.y +
      lastUnadjustedRbox.dimensions.height +
      sizing.blockDropdownMenuBorder,
    dropdownSvgCenter.y <= canvasCenter.y
      ? sizing.canvasBounds.topLeft.y +
          sizing.canvasBounds.dimensions.height -
          dropdownSvgCoords.y
      : dropdownSvgCoords.y - sizing.canvasBounds.topLeft.y
  );
  const menuY =
    dropdownSvgCenter.y <= canvasCenter.y
      ? dropdownSvgCoords.y + dropdownSpec.rbox.dimensions.height
      : dropdownSvgCoords.y - menuHeight;

  const menuOuterRbox: Rectangle = {
    topLeft: { x: dropdownSvgCoords.x, y: menuY },
    dimensions: {
      width: Math.min(
        sizing.blockDropdownMenuDimensions.width,
        maxChoiceSpecWidth + 2 * sizing.blockDropdownMenuBorder
      ),
      height: menuHeight,
    },
  };

  const choiceSpecs = choiceSpecsWithUnadjustedRboxes.map((spec) => ({
    text: spec.text,
    textRbox: {
      topLeft: addVec(spec.textRbox.topLeft, {
        x: 0,
        y: menuY,
      }),
      dimensions: spec.textRbox.dimensions,
    },
    rbox: {
      topLeft: addVec(spec.rbox.topLeft, { x: 0, y: menuY }),
      dimensions: { ...spec.rbox.dimensions, width: maxChoiceSpecWidth },
    },
  }));

  return {
    choiceSpecs,
    rbox: menuOuterRbox,
  };
}

function getDropdownChoiceIndexFromDataTouchRole(
  touchRole: string
): null | number {
  const withoutPrefix = touchRole.slice(
    DATA_TOUCH_ROLES.dropdownChoicePrefix.length
  );
  const parsed = Number.parseInt(withoutPrefix, 10);

  if (Number.isInteger(parsed) && parsed >= 0) {
    return parsed;
  }

  return null;
}

function updateDropdownValueInCanvas<B extends BlockEditorTypeParams>(
  canvas: BlockCanvasState<B>,
  dropdownAddress: NodeAddress,
  newDropdownValue: B["DropdownValueKind"]
): BlockCanvasState<B> {
  if (dropdownAddress[0]! !== CANVAS_ADDRESS_PART) {
    throw new Error(
      "Non-canvas dropdown address passed to updateDropdownValueInCanvas."
    );
  }

  return {
    items: canvas.items.map((item, itemIndex) => {
      if (itemIndex !== dropdownAddress[1]) {
        return item;
      }

      return {
        position: item.position,
        content: updateDropdownValueInBlockSequenceUsingLocalAddress(
          item.content,
          dropdownAddress.slice(2),
          newDropdownValue
        ),
      };
    }),
    scroll: canvas.scroll,
    scrollInProgress: canvas.scrollInProgress,
  };
}

function updateDropdownValueInBlockSequenceUsingLocalAddress<
  B extends BlockEditorTypeParams
>(
  sequence: BlockSequence<B>,
  localDropdownAddress: NodeAddress,
  newDropdownValue: B["DropdownValueKind"]
): BlockSequence<B> {
  return sequence.map((block, blockIndex) => {
    if (localDropdownAddress[0] !== blockIndex) {
      return block;
    }

    return updateDropdownValueInBlockUsingLocalAddress(
      block,
      localDropdownAddress.slice(1),
      newDropdownValue
    );
  });
}

function updateDropdownValueInBlockUsingLocalAddress<
  B extends BlockEditorTypeParams
>(
  block: Block<B>,
  localDropdownAddress: NodeAddress,
  newDropdownValue: B["DropdownValueKind"]
): Block<B> {
  return {
    isLiteral: false,
    kind: block.kind,
    args: {
      dropdowns: block.args.dropdowns.map((original, dropdownIndex) =>
        localDropdownAddress.length === 2 &&
        localDropdownAddress[0] === ARG_DROPDOWN_ADDRESS_PART &&
        localDropdownAddress[1] === dropdownIndex
          ? newDropdownValue
          : original
      ),
      expressions: block.args.expressions.map((original, expressionIndex) => {
        if (original.isLiteral) {
          return original;
        }

        if (
          localDropdownAddress[0] === ARG_EXPRESSION_ADDRESS_PART &&
          localDropdownAddress[1] === expressionIndex
        ) {
          return updateDropdownValueInBlockUsingLocalAddress(
            original,
            localDropdownAddress.slice(2),
            newDropdownValue
          );
        }

        return original;
      }),
      sequences: block.args.sequences.map((original, sequenceIndex) => {
        if (
          localDropdownAddress[0] === ARG_COMMAND_SEQUENCE_ADDRESS_PART &&
          localDropdownAddress[1] === sequenceIndex
        ) {
          return updateDropdownValueInBlockSequenceUsingLocalAddress(
            original,
            localDropdownAddress.slice(2),
            newDropdownValue
          );
        }

        return original;
      }),
    },
  };
}

function isPointInBlockCanvasDomElement(
  clientPosition: Vec2,
  editorComponentInstanceUid: string
): boolean {
  const targetId =
    editorComponentInstanceUid + DOM_ID_SUFFIXES.canvasBackground;
  const elements = document.elementsFromPoint(
    clientPosition.x,
    clientPosition.y
  );
  return elements.some((e) => e.id === targetId);
}

function getBlockEditorCursorBasedOnMousePosition(
  pointerClientPosition: Vec2,
  componentInstanceUid: string,
  state: BlockEditorState<BlockEditorTypeParams>
): BlockEditorCursor {
  const touchRoleAtClientPosition = getTouchRoleAtClientPosition(
    pointerClientPosition,
    componentInstanceUid
  );

  const { uiTouch } = state;

  if (uiTouch !== null && uiTouch.kind === UiTouchStateKind.BlockDrag) {
    return "grabbing";
  }

  if (
    touchRoleAtClientPosition !== null &&
    touchRoleAtClientPosition.startsWith(DATA_TOUCH_ROLES.blockAddressPrefix)
  ) {
    return "grab";
  }

  if (
    touchRoleAtClientPosition !== null &&
    touchRoleAtClientPosition.startsWith(DATA_TOUCH_ROLES.literalAddressPrefix)
  ) {
    return "text";
  }

  if (
    touchRoleAtClientPosition !== null &&
    (touchRoleAtClientPosition.startsWith(
      DATA_TOUCH_ROLES.dropdownAddressPrefix
    ) ||
      touchRoleAtClientPosition.startsWith(
        DATA_TOUCH_ROLES.dropdownChoicePrefix
      ))
  ) {
    return "pointer";
  }

  return "default";
}
