import { audioUrls } from "./audio";

export enum SoundKind {
  ButtonPop = "ButtonPop",
  ClubAttack = "ClubAttack",
  SwordAttack = "SwordAttack",
  CharacterMovement = "CharacterMovement",
  SuccessMusic = "SuccessMusic",
  GoblinYelp = "GoblinYelp",
  WitchYelp = "WitchYelp",
  HeroYelp = "HeroYelp",
  ErrorBuzzer = "ErrorBuzzer",
  Heal = "Heal",
  FireballLaunch = "FireballLaunch",
  FireballExplosion = "FireballExplosion",
  Greensleeves = "Greensleeves",
  PuzzleMusic = "PuzzleMusic",
  BlockPop = "BlockPop",
}

export interface AudioManager {
  scheduleSounds(sounds: readonly Sound[]): ScheduledSoundCollectionId;
  cancel(id: ScheduledSoundCollectionId): void;
  resume(): void;
}

export interface Sound {
  readonly kind: SoundKind;
  readonly timeInSeconds: number;
  readonly durationInSeconds?: number;
  readonly loop?: true;
  readonly gain?: number;
}

export interface ScheduledSoundCollectionId {
  readonly soundCollectionRawId: number;
}

interface SourceNodeCollection {
  readonly nodes: readonly AudioBufferSourceNode[];
}

export function getAudioManager(): AudioManager {
  return new AudioManagerImpl();
}

class AudioManagerImpl implements AudioManager {
  readonly ctx: AudioContext;
  readonly nodeCollections: Map<number, SourceNodeCollection>;
  nextAvailableSoundCollectionRawId: number;

  buttonPopBuffer: null | AudioBuffer;
  clubAttackBuffer: null | AudioBuffer;
  swordAttackBuffer: null | AudioBuffer;
  characterMovementBuffer: null | AudioBuffer;
  successMusicBuffer: null | AudioBuffer;
  goblinYelpBuffer: null | AudioBuffer;
  witchYelpBuffer: null | AudioBuffer;
  heroYelpBuffer: null | AudioBuffer;
  errorBuzzerBuffer: null | AudioBuffer;
  healBuffer: null | AudioBuffer;
  fireballLaunchBuffer: null | AudioBuffer;
  fireballExplosionBuffer: null | AudioBuffer;
  greensleevesBuffer: null | AudioBuffer;
  puzzleMusicBuffer: null | AudioBuffer;
  blockPopBuffer: null | AudioBuffer;

  constructor() {
    this.ctx = new AudioContext();

    this.nodeCollections = new Map();
    this.nextAvailableSoundCollectionRawId = 0;

    this.buttonPopBuffer = null;
    this.loadAudio(audioUrls.button_pop).then((buffer) => {
      this.buttonPopBuffer = buffer;
    });

    this.clubAttackBuffer = null;
    this.loadAudio(audioUrls.club_attack).then((buffer) => {
      this.clubAttackBuffer = buffer;
    });

    this.swordAttackBuffer = null;
    this.loadAudio(audioUrls.sword_attack).then((buffer) => {
      this.swordAttackBuffer = buffer;
    });

    this.characterMovementBuffer = null;
    this.loadAudio(audioUrls.character_movement).then((buffer) => {
      this.characterMovementBuffer = buffer;
    });

    this.successMusicBuffer = null;
    this.loadAudio(audioUrls.success_music).then((buffer) => {
      this.successMusicBuffer = buffer;
    });

    this.goblinYelpBuffer = null;
    this.loadAudio(audioUrls.goblin_yelp).then((buffer) => {
      this.goblinYelpBuffer = buffer;
    });

    this.witchYelpBuffer = null;
    this.loadAudio(audioUrls.witch_yelp).then((buffer) => {
      this.witchYelpBuffer = buffer;
    });

    this.heroYelpBuffer = null;
    this.loadAudio(audioUrls.hero_yelp).then((buffer) => {
      this.heroYelpBuffer = buffer;
    });

    this.errorBuzzerBuffer = null;
    this.loadAudio(audioUrls.error_buzzer).then((buffer) => {
      this.errorBuzzerBuffer = buffer;
    });

    this.healBuffer = null;
    this.loadAudio(audioUrls.heal).then((buffer) => {
      this.healBuffer = buffer;
    });

    this.fireballLaunchBuffer = null;
    this.loadAudio(audioUrls.fireball_launch).then((buffer) => {
      this.fireballLaunchBuffer = buffer;
    });

    this.fireballExplosionBuffer = null;
    this.loadAudio(audioUrls.fireball_explosion).then((buffer) => {
      this.fireballExplosionBuffer = buffer;
    });

    this.greensleevesBuffer = null;
    this.loadAudio(audioUrls.greensleeves).then((buffer) => {
      this.greensleevesBuffer = buffer;
    });

    this.puzzleMusicBuffer = null;
    this.loadAudio(audioUrls.puzzle_music).then((buffer) => {
      this.puzzleMusicBuffer = buffer;
    });

    this.blockPopBuffer = null;
    this.loadAudio(audioUrls.block_pop).then((buffer) => {
      this.blockPopBuffer = buffer;
    });
  }

  async loadAudio(audioUrl: string): Promise<AudioBuffer> {
    const response = await fetch(audioUrl);
    return await this.ctx.decodeAudioData(await response.arrayBuffer());
  }

  scheduleSounds(sounds: readonly Sound[]): ScheduledSoundCollectionId {
    const { currentTime } = this.ctx;

    const nodes: AudioBufferSourceNode[] = [];
    for (const sound of sounds) {
      const buffer = this.getBufferForSoundKind(sound.kind);
      if (buffer === null) {
        continue;
      }

      const startTime = Math.max(
        currentTime,
        currentTime + sound.timeInSeconds
      );
      const offsetTime = Math.max(0, -sound.timeInSeconds);

      const node = this.ctx.createBufferSource();
      node.buffer = buffer;
      node.loop = sound.loop ?? false;

      if (sound.gain === undefined) {
        node.connect(this.ctx.destination);
      } else {
        const gainNode = this.ctx.createGain();
        node.connect(gainNode);
        gainNode.gain.value = sound.gain;
        gainNode.connect(this.ctx.destination);
      }

      node.start(startTime, offsetTime, sound.durationInSeconds);
      nodes.push(node);
    }

    const collection: SourceNodeCollection = { nodes };

    const id: ScheduledSoundCollectionId = {
      soundCollectionRawId: this.nextAvailableSoundCollectionRawId,
    };
    ++this.nextAvailableSoundCollectionRawId;

    this.nodeCollections.set(id.soundCollectionRawId, collection);

    return id;
  }

  getBufferForSoundKind(kind: SoundKind): null | AudioBuffer {
    return {
      [SoundKind.ButtonPop]: this.buttonPopBuffer,
      [SoundKind.ClubAttack]: this.clubAttackBuffer,
      [SoundKind.SwordAttack]: this.swordAttackBuffer,
      [SoundKind.CharacterMovement]: this.characterMovementBuffer,
      [SoundKind.SuccessMusic]: this.successMusicBuffer,
      [SoundKind.GoblinYelp]: this.goblinYelpBuffer,
      [SoundKind.WitchYelp]: this.witchYelpBuffer,
      [SoundKind.HeroYelp]: this.heroYelpBuffer,
      [SoundKind.ErrorBuzzer]: this.errorBuzzerBuffer,
      [SoundKind.Heal]: this.healBuffer,
      [SoundKind.FireballLaunch]: this.fireballLaunchBuffer,
      [SoundKind.FireballExplosion]: this.fireballExplosionBuffer,
      [SoundKind.Greensleeves]: this.greensleevesBuffer,
      [SoundKind.PuzzleMusic]: this.puzzleMusicBuffer,
      [SoundKind.BlockPop]: this.blockPopBuffer,
    }[kind];
  }

  cancel(id: ScheduledSoundCollectionId): void {
    const collection = this.nodeCollections.get(id.soundCollectionRawId);
    if (collection === undefined) {
      return;
    }

    for (const node of collection.nodes) {
      node.disconnect();
    }

    this.nodeCollections.delete(id.soundCollectionRawId);
  }

  resume(): void {
    this.ctx.resume();
  }
}
