import { CanvasItem } from "./BlockEditor";
import { Level, STORAGE_KEYS, UiLanguage } from "./constsAndEnums";
import { writeToFirebaseStorage } from "./firebase";
import { compressJson } from "./jsonCompression";
import { MainBlockEditorTypeParams } from "./miscTypes";
import { loadData, saveData } from "./deviceStorage";
import { stringifyError } from "./stringifyError";
import { GAME_VERSION_WITHOUT_V } from "./GAME_VERSION";

export const LOG_SCHEMA_VERSION = 1;

export const LOG_COMPRESSION_SCHEME_VERSION = 1;

export const LOG_CACHE_MAX_SIZE = 200;

export const LOG_UPLOAD_COOLDOWN_SECONDS = 120;

export enum LogEventKind {
  Error = "Error",
  LoggerIdCreated = "LoggerIdCreated",
  AppLifeCycleMilestone = "AppLifeCycleMilestone",
  ButtonPressed = "ButtonPressed",
  BlockEditorCanvasItemsUpdated = "BlockEditorCanvasItemsUpdated",
  BattleResumed = "BattleResumed",
  BattlePaused = "BattlePaused",
  LevelCompleted = "LevelCompleted",
  LevelFailed = "LevelFailed",
  LanguageChanged = "LanguageChanged",
  AppActiveStatusChanged = "AppActiveStatusChanged",
}

const JSON_COMPRESSION_TABLE: readonly string[] = [];

let possiblyLoadingLoggerIdBytes: "Loading" | LoggerIdBytes = "Loading";
let wasLoggerIdJustCreated = false;

/**
 * If the app is offline, a log upload will be queued.
 * As soon as the app regains Internet connection, the log upload will be attempted.
 * Since `logEvents` returns a promise, we need to resolve that promise
 * when the upload is successfully completed.
 * We do that by pushing the promises' `resolve` functions to this array.
 */
const pendingLogUploadCompletionListeners: (() => void)[] = [];

const loggerIdBytesPromise: Promise<LoggerIdBytes> =
  (async (): Promise<LoggerIdBytes> => {
    const savedIdString = await loadData(STORAGE_KEYS.loggerIdBytes);
    const loggerIdBytesOrNull = tryParseLoggerBytesString(savedIdString);

    if (loggerIdBytesOrNull !== null) {
      possiblyLoadingLoggerIdBytes = loggerIdBytesOrNull;
      wasLoggerIdJustCreated = false;
      return possiblyLoadingLoggerIdBytes;
    }

    const newLoggerIdBytes = getBytesForNewLoggerId();

    await saveData(
      STORAGE_KEYS.loggerIdBytes,
      JSON.stringify(newLoggerIdBytes)
    );

    possiblyLoadingLoggerIdBytes = newLoggerIdBytes;
    wasLoggerIdJustCreated = true;

    return possiblyLoadingLoggerIdBytes;
  })();

const pendingLogEvents: LogEvent[] = [];

const pendingLogEventsPushedPromise: Promise<void> = loggerIdBytesPromise.then(
  (loggerIdBytes) => {
    logEventsWithLoggerId(pendingLogEvents, loggerIdBytes);

    pendingLogEvents.splice(0, pendingLogEvents.length);

    pendingLogEvents.push = (): number => {
      const error = new Error(
        "Attempted to push to pendingLogEvents after the logger id was loaded."
      );

      logEvents([
        {
          kind: LogEventKind.Error,
          errorMessage: stringifyError(error),
          timestampDateDotNow: Date.now(),
        },
      ]);

      console.error(error);

      return pendingLogEvents.length;
    };
  }
);

export type AppLifeCycleMilestoneKind =
  | "IndexDotTsFinished"
  | "AppConstructed"
  | "AppMounted"
  | "LoadingScreenStarted"
  | "LoadingScreenFinished"
  | "AppUnmounted";

export type LoggableButtonKind =
  | "HomeScreenStart"
  | "Edit"
  | "InEditorMiniBattleScreen"
  | "ViewMap"
  | "ResumeBattle"
  | "PauseBattle"
  | "ReturnHome"
  | "OpenSettings"
  | "CloseSettingsWithPrimaryButton"
  | "CloseSettingsWithSecondaryButton"
  | "ChangeLanguage"
  | "OpenCredits"
  | "CloseCredits"
  | "OpenConfirmResetToLevelOneScreen"
  | "CloseConfirmResetToLevelOneScreen"
  | "ConfirmResetToLevelOne"
  | "CancelResetToLevelOne"
  | "NextLevel";

export type AppActiveStatusChangeKind =
  | "Activated_Web"
  | "Deactivated_Web"
  | "Activated_Native"
  | "Deactivated_Native";

export type LogEvent =
  | ErrorLogEvent
  | LoggerIdCreatedLogEvent
  | AppLifeCycleMilestoneLogEvent
  | ButtonPressedLogEvent
  | BlockEditorCanvasItemsUpdatedLogEvent
  | BattleResumedLogEvent
  | BattlePausedLogEvent
  | LevelCompletedLogEvent
  | LevelFailedLogEvent
  | LanguageChangedLogEvent
  | AppActiveStatusChangedLogEvent;

/** A 16-byte ID. */
type LoggerIdBytes = readonly [
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number,
  number
];

export interface BaseLogEvent {
  readonly timestampDateDotNow: number;
}

export interface ErrorLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.Error;
  readonly errorMessage: string;
}

export interface LoggerIdCreatedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.LoggerIdCreated;
  readonly loggerIdBytes: LoggerIdBytes;
}

export interface AppLifeCycleMilestoneLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.AppLifeCycleMilestone;
  readonly milestoneKind: AppLifeCycleMilestoneKind;
  readonly gameVersionWithoutV: string;
}

export interface ButtonPressedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.ButtonPressed;
  readonly buttonKind: LoggableButtonKind;
}

export interface BlockEditorCanvasItemsUpdatedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.BlockEditorCanvasItemsUpdated;
  readonly canvasItems: readonly CanvasItem<MainBlockEditorTypeParams>[];
}

export interface BattleResumedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.BattleResumed;
  readonly battleAnimationOffsetSeconds: number;
  readonly canvasItems: readonly CanvasItem<MainBlockEditorTypeParams>[];
}

export interface BattlePausedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.BattlePaused;
  readonly battleAnimationOffsetSeconds: number;
  readonly canvasItems: readonly CanvasItem<MainBlockEditorTypeParams>[];
}

export interface LevelCompletedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.LevelCompleted;
  readonly completedLevel: Level;
  readonly canvasItems: readonly CanvasItem<MainBlockEditorTypeParams>[];
}

export interface LevelFailedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.LevelFailed;
  readonly failedLevel: Level;
  readonly didHeroSurvive: boolean;
  readonly canvasItems: readonly CanvasItem<MainBlockEditorTypeParams>[];
}

export interface LanguageChangedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.LanguageChanged;
  readonly newLanguage: UiLanguage;
}

export interface AppActiveStatusChangedLogEvent extends BaseLogEvent {
  readonly kind: LogEventKind.AppActiveStatusChanged;
  readonly statusChangeKind: AppActiveStatusChangeKind;
}

interface LogCollection {
  readonly logSchemaVersion: number;
  readonly logEvents: readonly LogEvent[];
  readonly lastEmptiedDateDotNow: number;
  readonly lastUpdatedDateDotNow: number;
}

export function getBytesForNewLoggerId(): LoggerIdBytes {
  const buffer = new ArrayBuffer(16);
  const view = new DataView(buffer);
  view.setFloat64(0, Math.random(), true);
  view.setFloat64(8, Date.now(), true);

  const u8s = new Uint8Array(buffer);
  return Array.from(u8s) as unknown as LoggerIdBytes;
}

export function logEvents(newEvents: readonly LogEvent[]): Promise<void> {
  if (possiblyLoadingLoggerIdBytes === "Loading") {
    pendingLogEvents.push(...newEvents);

    return pendingLogEventsPushedPromise;
  }

  return logEventsWithLoggerId(newEvents, possiblyLoadingLoggerIdBytes);
}

function logEventsWithLoggerId(
  newEvents: readonly LogEvent[],
  loggerIdBytes: LoggerIdBytes
): Promise<void> {
  const logCache = getAndSaveUpToDateLogCacheOrDefault(
    loggerIdBytes,
    wasLoggerIdJustCreated
  );
  const nextUploadTimeDateDotNow =
    logCache.lastEmptiedDateDotNow + LOG_UPLOAD_COOLDOWN_SECONDS * 1000;

  const allEvents = logCache.logEvents.concat(newEvents);
  updateLogCache({
    ...logCache,
    logEvents: allEvents,
  });

  if (
    allEvents.length >= LOG_CACHE_MAX_SIZE ||
    Date.now() > nextUploadTimeDateDotNow
  ) {
    if (!navigator.onLine) {
      console.log("Attempted to send logs, but app is offline.");

      return new Promise((resolve) => {
        pendingLogUploadCompletionListeners.push(resolve);
      });
    }

    console.log("Sending logs.");
    return uploadLogEvents(allEvents, loggerIdBytes).then(
      emptyLogCacheAssumingLoggerIdBytesHavedLoaded
    );
  }

  return Promise.resolve();
}

function getAndSaveUpToDateLogCacheOrDefault(
  loggerIdBytes: LoggerIdBytes,
  wasLoggerIdJustCreated: boolean
): LogCollection {
  const savedStr = localStorage.getItem(STORAGE_KEYS.logCache);
  if (savedStr === null) {
    return getAndSaveDefaultLogCache(loggerIdBytes, wasLoggerIdJustCreated);
  }
  try {
    const saved = JSON.parse(savedStr);

    if (saved.logSchemaVersion !== LOG_SCHEMA_VERSION) {
      return getAndSaveDefaultLogCache(loggerIdBytes, wasLoggerIdJustCreated);
    }

    return saved;
  } catch {
    return getAndSaveDefaultLogCache(loggerIdBytes, wasLoggerIdJustCreated);
  }
}

function getAndSaveDefaultLogCache(
  loggerIdBytes: LoggerIdBytes,
  wasLoggerIdJustCreated: boolean
): LogCollection {
  const now = Date.now();
  const defaultCache: LogCollection = {
    logSchemaVersion: LOG_SCHEMA_VERSION,
    logEvents: wasLoggerIdJustCreated
      ? [
          {
            kind: LogEventKind.LoggerIdCreated,
            loggerIdBytes,
            timestampDateDotNow: now,
          },
        ]
      : [],
    lastEmptiedDateDotNow: now,
    lastUpdatedDateDotNow: now,
  };
  localStorage.setItem(STORAGE_KEYS.logCache, JSON.stringify(defaultCache));
  return defaultCache;
}

function updateLogCache(newCache: LogCollection): void {
  const now = Date.now();
  const timestampedCache: LogCollection = {
    ...newCache,
    lastUpdatedDateDotNow: now,
    lastEmptiedDateDotNow:
      newCache.logEvents.length === 0 ? now : newCache.lastEmptiedDateDotNow,
  };
  const s = JSON.stringify(timestampedCache);
  localStorage.setItem(STORAGE_KEYS.logCache, s);
}

function emptyLogCacheAssumingLoggerIdBytesHavedLoaded(): void {
  if (possiblyLoadingLoggerIdBytes === "Loading") {
    throw new Error(
      "Called emptyLogCacheAssumingLoggerIdBytesHavedLoaded() but logger ID bytes are still loading."
    );
  }

  const logCache = getAndSaveUpToDateLogCacheOrDefault(
    possiblyLoadingLoggerIdBytes,
    wasLoggerIdJustCreated
  );
  updateLogCache({
    ...logCache,
    logEvents: [],
  });
}

function uploadLogEvents(
  events: readonly LogEvent[],
  loggerIdBytes: LoggerIdBytes
): Promise<void> {
  const startTime = performance.now();
  const encoded = encodeLogEvents(events, loggerIdBytes);
  const endTime = performance.now();
  console.log("Encoding took", endTime - startTime, "ms");

  const loggerIdString = loggerIdBytes
    .map((b) => leftpadWithZero(b.toString(16), 2))
    .join("");
  const nowStr = Date.now().toString(16);
  const url = `logs/${loggerIdString}_${nowStr}.dat`;
  return writeToFirebaseStorage(url, new Blob([encoded])).then(noOp);
}

function encodeLogEvents(
  logEvents: readonly LogEvent[],
  loggerIdBytes: LoggerIdBytes
): Uint8Array {
  return compressJson(
    {
      loggerIdBytes,
      logSchemaVersion: LOG_SCHEMA_VERSION,
      logCompressionSchemeVersion: LOG_COMPRESSION_SCHEME_VERSION,
      gameVersion: GAME_VERSION_WITHOUT_V,
      logEvents,
    },
    JSON_COMPRESSION_TABLE
  );
}

function noOp(): void {}

function leftpadWithZero(s: string, targetLength: number): string {
  if (s.length > targetLength) {
    throw new Error(
      `String is too long to pad. Target length: ${targetLength}. String: ${JSON.stringify(
        s
      )}`
    );
  }
  return "0".repeat(targetLength - s.length) + s;
}

function tryParseLoggerBytesString(s: null | string): null | LoggerIdBytes {
  if (s === null) {
    return null;
  }

  let parsed: unknown;
  try {
    parsed = JSON.parse(s);
  } catch {
    return null;
  }

  if (!isLoggerIdBytes(parsed)) {
    return null;
  }

  return parsed;
}

function isLoggerIdBytes(data: unknown): data is LoggerIdBytes {
  return Array.isArray(data) && data.length === 16 && data.every(isUint8);
}

function isUint8(data: unknown): boolean {
  return (
    Number.isInteger(data) && 0 <= (data as number) && (data as number) <= 255
  );
}

export function getPossiblyLoadingLoggerIdBytes(): "Loading" | LoggerIdBytes {
  return possiblyLoadingLoggerIdBytes;
}

window.addEventListener("online", async () => {
  const loggerIdBytes = await loggerIdBytesPromise;

  const logCache = getAndSaveUpToDateLogCacheOrDefault(
    loggerIdBytes,
    wasLoggerIdJustCreated
  );

  await uploadLogEvents(logCache.logEvents, loggerIdBytes);
  emptyLogCacheAssumingLoggerIdBytesHavedLoaded();

  for (const listener of pendingLogUploadCompletionListeners) {
    listener();
  }

  pendingLogUploadCompletionListeners.splice(
    0,
    pendingLogUploadCompletionListeners.length
  );
});
