import React from "react";
import ReactDOM from "react-dom/client";
import { App } from "./App";
import * as serviceWorkerRegistration from "./serviceWorkerRegistration";
import reportWebVitals from "./reportWebVitals";
import { GAME_VERSION_WITHOUT_V } from "./GAME_VERSION";
import "./firebase";
import { compressJson, decompressJson } from "./jsonCompression";
import { LogEventKind, logEvents } from "./log";
import { stringifyError } from "./stringifyError";

const root = ReactDOM.createRoot(
  document.getElementById("Root") as HTMLElement
);
root.render(
  <React.StrictMode>
    <App />
  </React.StrictMode>
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.register();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();

console.log(`Castle Coders v${GAME_VERSION_WITHOUT_V}`);

(window as any).compressJson = compressJson;
(window as any).decompressJson = decompressJson;

window.addEventListener("error", (errorEvent) => {
  const error = {
    rawError: errorEvent.error,
    message: errorEvent.message,
    filename: errorEvent.filename,
    lineno: errorEvent.lineno,
    colno: errorEvent.colno,
  };

  console.error(
    error,
    "This error was caught by the global error handler and logged."
  );

  logEvents([
    {
      kind: LogEventKind.Error,
      errorMessage: stringifyError(error),
      timestampDateDotNow: Date.now(),
    },
  ]);
});

window.addEventListener(
  "unhandledrejection",
  (rejectionEvent: PromiseRejectionEvent) => {
    const error = {
      isTrusted: rejectionEvent.isTrusted,
      promise: rejectionEvent.promise,
      reason: rejectionEvent.reason,
      timeStamp: rejectionEvent.timeStamp,
    };

    console.error(
      "Unhandled rejection: ",
      rejectionEvent,
      "This error was caught by the global error handler and logged as ",
      error
    );

    logEvents([
      {
        kind: LogEventKind.Error,
        errorMessage: stringifyError(error),
        timestampDateDotNow: Date.now(),
      },
    ]);
  }
);

logEvents([
  {
    kind: LogEventKind.AppLifeCycleMilestone,
    milestoneKind: "IndexDotTsFinished",
    gameVersionWithoutV: GAME_VERSION_WITHOUT_V,
    timestampDateDotNow: Date.now(),
  },
]);
