export interface Vec2 {
  readonly x: number;
  readonly y: number;
}

export function addVec(a: Vec2, b: Vec2): Vec2 {
  return { x: a.x + b.x, y: a.y + b.y };
}

export function subVec(a: Vec2, b: Vec2): Vec2 {
  return { x: a.x - b.x, y: a.y - b.y };
}

export function invertY(a: Vec2): Vec2 {
  return { x: a.x, y: -a.y };
}

export function scaleVec(v: Vec2, s: number): Vec2 {
  return { x: v.x * s, y: v.y * s };
}

export function areVecsEqual(a: Vec2, b: Vec2): boolean {
  return a.x === b.x && a.y === b.y;
}
