function getNextLevel(level: Level): null | Level {
  if (level < LAST_LEVEL) {
    return level + 1;
  }
  return null;
}

function periodicColorSinInterp(
  a: Color32,
  b: Color32,
  elapsedSeconds: number,
  frequencyHertz: number
): Color32 {
  const f =
    Math.cos(frequencyHertz * 2 * Math.PI * elapsedSeconds) * -0.5 + 0.5;
  return {
    r: Math.round(a.r + f * (b.r - a.r)),
    g: Math.round(a.g + f * (b.g - a.g)),
    b: Math.round(a.b + f * (b.b - a.b)),
    a: Math.round(a.a + f * (b.a - a.a)),
  };
}

export function parseColorHexString(s: string): Color32 {
  const withoutHash = s.charAt(0) === "#" ? s.slice(1) : s;

  if (!(withoutHash.length === 6 || withoutHash.length === 8)) {
    throw new Error("Invalid color hex string.");
  }

  return {
    r: parseInt(withoutHash.slice(0, 2), 16),
    g: parseInt(withoutHash.slice(2, 4), 16),
    b: parseInt(withoutHash.slice(4, 6), 16),
    a: withoutHash.length === 8 ? parseInt(withoutHash.slice(6, 8), 16) : 255,
  };
}

function getStartingBlockEditorStateForLevel(
  level: Level
): BlockEditorState<MainBlockEditorTypeParams> {
  return {
    palette: {
      blockKinds: getPaletteBlockKinds(level),
      scroll: { x: 0, y: 0 },
      scrollInProgress: null,
    },
    uiTouch: null,
    literalEditor: null,
    dropdownMenu: null,
    canvas: {
      items: getStartingCanvasItemsForLevel(level),
      scroll: { x: 0, y: 0 },
      scrollInProgress: null,
    },
    cursor: "default",
  };
}

