export function stringifyColor(color: Color32): string {
  if (!isValidColor(color)) {
    throw new Error(
      `Cannot stringify color because it is invalid. color: ${JSON.stringify(
        color
      )}.`
    );
  }

  return (
    "#" +
    leftpadWithZero(color.r.toString(16), 2) +
    leftpadWithZero(color.g.toString(16), 2) +
    leftpadWithZero(color.b.toString(16), 2) +
    leftpadWithZero(color.a.toString(16), 2)
  );
}

function isValidColor(color: Color32): boolean {
  const { r, g, b, a } = color;
  return (
    Number.isInteger(r) &&
    0 <= r &&
    r <= 255 &&
    Number.isInteger(g) &&
    0 <= g &&
    g <= 255 &&
    Number.isInteger(b) &&
    0 <= b &&
    b <= 255 &&
    Number.isInteger(a) &&
    0 <= a &&
    a <= 255
  );
}

function leftpadWithZero(s: string, desiredLength: number): string {
  if (!Number.isInteger(desiredLength)) {
    throw new Error(
      `Cannot leftpad because desiredLength is not an integer. desiredLength: ${desiredLength}.`
    );
  }

  if (s.length > desiredLength) {
    throw new Error(
      `Cannot leftpad because string is too long to pad. desiredLength: ${desiredLength}. Actual length: ${
        s.length
      }. String: ${JSON.stringify(s)}`
    );
  }

  return "0".repeat(desiredLength - s.length) + s;
}

