function noOp(): void {}

function getButtonAt(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  const { screen } = state;

  if (
    screen.animationKind === AnimationKind.Edit &&
    screen.blockEditor.literalEditor !== null
  ) {
    const elements = document.elementsFromPoint(
      clientPosition.x,
      clientPosition.y
    );
    if (
      elements.some((elem) => elem.id === DOM_ELEMENT_IDS.blockLiteralEditor)
    ) {
      return null;
    }
  }

  if (screen.animationKind === AnimationKind.StaticHome) {
    return getButtonAtPositionAssumingHomeScreen(
      clientPosition,
      state,
      resources
    );
  }

  if (screen.animationKind === AnimationKind.StaticSettings) {
    return getButtonAtPositionAssumingSettingsScreen(
      clientPosition,
      state,
      resources
    );
  }

  if (screen.animationKind === AnimationKind.StaticConfirmResetToLevelOne) {
    return getButtonAtPositionAssumingConfirmResetToLevelOne(
      clientPosition,
      state,
      resources
    );
  }

  if (screen.animationKind === AnimationKind.Credits) {
    return getButtonAtPositionAssumingCreditsScreen(
      clientPosition,
      state,
      resources
    );
  }

  if (
    [
      AnimationKind.Edit,
      AnimationKind.PlayBattle,
      AnimationKind.PauseBattle,
    ].includes(screen.animationKind)
  ) {
    return getButtonAtPositionAssumingPuzzleScreen(
      clientPosition,
      state,
      resources
    );
  }

  return null;
}

function getButtonAtPositionAssumingHomeScreen(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  if (isWithinHomeScreenStartButton(clientPosition, state, resources)) {
    return ButtonKind.HomeScreenStart;
  }

  if (isWithinHomeScreenSettingsButton(clientPosition, state, resources)) {
    return ButtonKind.Settings;
  }

  return null;
}

function getButtonAtPositionAssumingSettingsScreen(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  if (isWithinSettingsScreenLanguageButton(clientPosition, state, resources)) {
    return ButtonKind.Language;
  }

  if (isWithinSettingsScreenCreditsButton(clientPosition, state, resources)) {
    return ButtonKind.Credits;
  }

  if (isWithinSettingsScreenResetButton(clientPosition, state, resources)) {
    return ButtonKind.OpenConfirmResetToLevelOneScreen;
  }

  if (isWithinSettingsScreenCloseButton(clientPosition, state, resources)) {
    return ButtonKind.SettingsScreenClose;
  }

  if (
    isWithinSettingsScreenSecondaryCloseButton(clientPosition, state, resources)
  ) {
    return ButtonKind.SettingsScreenSecondaryClose;
  }

  return null;
}

function getButtonAtPositionAssumingConfirmResetToLevelOne(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  const hypotheticalButton = getButtonAtPositionAssumingSettingsScreen(
    clientPosition,
    state,
    resources
  );

  if (hypotheticalButton === ButtonKind.Credits) {
    return ButtonKind.ConfirmResetToLevelOne;
  }

  if (hypotheticalButton === ButtonKind.OpenConfirmResetToLevelOneScreen) {
    return ButtonKind.CancelResetToLevelOne;
  }

  if (hypotheticalButton === ButtonKind.SettingsScreenClose) {
    return ButtonKind.ConfirmResetToLevelOneScreenClose;
  }

  if (hypotheticalButton === ButtonKind.SettingsScreenSecondaryClose) {
    return hypotheticalButton;
  }

  return null;
}

function getButtonAtPositionAssumingCreditsScreen(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  // If the user taps anywhere, we close the credits screen.
  return ButtonKind.CreditsScreenClose;
}

function getButtonAtPositionAssumingPuzzleScreen(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): null | ButtonKind {
  if (isWithinPuzzleScreenLeftButton(clientPosition, state, resources)) {
    return ButtonKind.PuzzleScreenLeft;
  }

  if (isWithinPuzzleScreenMiddleButton(clientPosition, state, resources)) {
    return ButtonKind.PuzzleScreenMiddle;
  }

  if (isWithinPuzzleScreenRightButton(clientPosition, state, resources)) {
    return ButtonKind.PuzzleScreenRight;
  }

  if (
    isScreenShowingVictory(
      state.screen,
      resources.getCurrentLevelBattleEvents
    ) &&
    isWithinNextLevelButton(clientPosition, state, resources)
  ) {
    return ButtonKind.NextLevel;
  }

  if (
    state.screen.animationKind === AnimationKind.Edit &&
    resources.renderTutorialCanvasElement() === NULL_CANVAS_ELEMENT &&
    clientPosition.x >=
      (1 - IN_EDITOR_MINI_BATTLE_SCREEN_SCALE_FACTOR) * state.appWidth &&
    clientPosition.y >=
      (1 - IN_EDITOR_MINI_BATTLE_SCREEN_SCALE_FACTOR) * state.appHeight
  ) {
    return ButtonKind.InEditorMiniBattleScreen;
  }

  return null;
}

