function isWithinNextLevelButton(
  clientPosition: Vec2,
  state: State,
  resources: ButtonClickDetectorResources
): boolean {
  if (state.screen.level >= LAST_LEVEL) {
    return false;
  }

  const { appWidth, appHeight, language, screen } = state;

  const backgroundImageScale = appWidth / SIZING.castleInteriorImage.width;
  const battleOutcomeTextDimensions = resources.measureCanvasText({
    text: getUiText(language, UiTextKind.LevelCleared).replaceAll(
      TEXT_SENTINELS.levelNumber,
      String(screen.level)
    ),
    fontSize: SIZING.unscaledBattleOutcomeTextSizePx * backgroundImageScale,
    fontFamily: BATTLE_OUTCOME_TEXT_FONT_FAMILY,
  });
  const nextLevelButtonTextDimensions = resources.measureCanvasText({
    text: getUiText(language, UiTextKind.NextLevel),
    fontSize: SIZING.unscaledButtonTextSizePx * backgroundImageScale,
    fontFamily: BUTTON_TEXT_FONT_FAMILY,
  });
  const nextLevelButtonPadding = nextLevelButtonTextDimensions.height;
  const victoryButton = renderRect({
    x:
      state.appWidth / 2 -
      (nextLevelButtonTextDimensions.width + 2 * nextLevelButtonPadding) / 2,
    y:
      0 +
      0.5 * appHeight +
      0.5 * battleOutcomeTextDimensions.height +
      2 * battleOutcomeTextDimensions.height,
    w: nextLevelButtonTextDimensions.width + 2 * nextLevelButtonPadding,
    h: nextLevelButtonTextDimensions.height + 2 * nextLevelButtonPadding,
    fill: COLOR_THEME.buttonBlue,
    rx: 5,
    ry: 5,
  });

  const ctx = resources.tempCanvasCtx;
  ctx.canvas.width = appWidth;
  ctx.canvas.height = appHeight;
  ctx.clearRect(0, 0, ctx.canvas.width, ctx.canvas.height);
  paintCanvasElement(victoryButton, ctx);
  const imageX = Math.floor(clientPosition.x);
  const imageY = Math.floor(clientPosition.y);
  const imageData = ctx.getImageData(imageX, imageY, 1, 1).data;
  return imageData[3] > 0;
}

function getDepressedButtonBBox(nondepressedBBox: Rectangle): Rectangle {
  const nondepressedWidth = nondepressedBBox.dimensions.width;
  const nondepressedHeight = nondepressedBBox.dimensions.height;
  const width = nondepressedWidth * SIZING.depressedButtonScale;
  const height = nondepressedHeight * SIZING.depressedButtonScale;
  const topLeft = addVec(nondepressedBBox.topLeft, {
    x: 0.5 * (nondepressedWidth - width),
    y: 0.5 * (nondepressedHeight - height),
  });
  return {
    topLeft,
    dimensions: { width, height },
  };
}

function areAnyBlocksReachable(screen: ScreenState): boolean {
  return screen.blockEditor.canvas.items.some(
    (item) =>
      item.content[0].kind === BlockKind.WhenBattleStarts &&
      item.content.length >= 2
  );
}

function withAlpha(alpha: number, e: CanvasElement): CanvasElement {
  return {
    kind: CanvasElementKind.Group,
    alpha,
    children: [e],
  };
}

function getSavedStateOrDefault(): Promise<SavableState> {
  return loadData(STORAGE_KEYS.gameState).then(
    (savedString): Promise<SavableState> => {
      const parsed = parseStringAsState(savedString);

      if (parsed === null) {
        return getDefaultSavableStateByGuessingPreferredLanguageBasedOnDeviceLanguage();
      }

      return Promise.resolve(parsed);
    }
  );
}

function getStateSavedInLocalStorageOrDefault(): SavableState {
  const savedStr = localStorage.getItem(STORAGE_KEYS.gameState);
  return (
    parseStringAsState(savedStr) ??
    getDefaultSavableStateByGuessingPreferredLanguageBasedOnBrowserLanguage()
  );
}

function parseStringAsState(s: null | string): null | SavableState {
  if (s === null) {
    return null;
  }

  try {
    const parsed = JSON.parse(s);
    if (parsed.version !== SETTINGS_VERSION) {
      return null;
    }

    const { language, blockEditor, level } = parsed;
    return { language, blockEditor, level };
  } catch {
    console.error("Failed to parse saved state: " + JSON.stringify(s));
    return null;
  }
}

async function getDefaultSavableStateByGuessingPreferredLanguageBasedOnDeviceLanguage(): Promise<SavableState> {
  const level = Level.Level1;
  const blockEditor = getStartingBlockEditorStateForLevel(level);
  const deviceLanguage =
    await tryToGuessPreferredLanguageBasedOnDeviceLanguage();
  const language =
    deviceLanguage ?? guessPreferredLanguageBasedOnBrowserLanguage();
  return { language, blockEditor, level };
}

function getDefaultSavableStateByGuessingPreferredLanguageBasedOnBrowserLanguage(): SavableState {
  const level = Level.Level1;
  const blockEditor = getStartingBlockEditorStateForLevel(level);
  const language = guessPreferredLanguageBasedOnBrowserLanguage();
  return { language, blockEditor, level };
}

async function tryToGuessPreferredLanguageBasedOnDeviceLanguage(): Promise<null | UiLanguage> {
  try {
    const deviceLang = await Device.getLanguageTag();
    return tryToGuessPreferredLanguageBasedOnString(deviceLang.value);
  } catch {
    return null;
  }
}

function guessPreferredLanguageBasedOnBrowserLanguage(): UiLanguage {
  const navLang = navigator.language;
  return tryToGuessPreferredLanguageBasedOnString(navLang) ?? DEFAULT_LANGUAGE;
}

function tryToGuessPreferredLanguageBasedOnString(
  langStr: string
): null | UiLanguage {
  return langStr.startsWith("en")
    ? UiLanguage.English
    : langStr.startsWith("ja")
    ? UiLanguage.Japanese
    : null;
}

function saveState(
  state: State,
  getCurrentLevelBattleEvents: () => readonly BattleEvent[]
): void {
  const { language, screen } = state;
  const unsafeNextLevel = screen.level + 1;
  const { blockEditor, level } =
    isScreenShowingVictory(screen, getCurrentLevelBattleEvents) &&
    screen.level < LAST_LEVEL
      ? {
          blockEditor: getStartingBlockEditorStateForLevel(unsafeNextLevel),
          level: unsafeNextLevel,
        }
      : screen;
  const savableState = {
    version: SETTINGS_VERSION,
    language,
    blockEditor,
    level,
  };
  const stateString = JSON.stringify(savableState);
  saveData(STORAGE_KEYS.gameState, stateString);
}

function typesafeCast<T>(x: T): T {
  return x;
}

function getLaterTimestamp(a: Timestamp, b: Timestamp): Timestamp {
  return {
    millisecondsSinceEpoch: Math.max(
      a.millisecondsSinceEpoch,
      b.millisecondsSinceEpoch
    ),
  };
}

function areAnyTrue(bools: readonly boolean[]): boolean {
  return bools.some(identity);
}

function identity<T>(x: T): T {
  return x;
}

function flagBlockSequenceAsInactiveExcept(
  activeBlockAddress: NodeAddress,
  sequence: BlockSequence<MinimapBlockEditorTypeParams>,
  sequenceAddress: NodeAddress
): BlockSequence<MinimapBlockEditorTypeParams> {
  return sequence.map((block, i) =>
    flagBlockAsInactiveExcept(
      activeBlockAddress,
      block,
      sequenceAddress.concat([i])
    )
  );
}

function flagBlockAsInactiveExcept(
  activeBlockAddress: NodeAddress,
  block: Block<MinimapBlockEditorTypeParams>,
  blockAddress: NodeAddress
): Block<MinimapBlockEditorTypeParams> {
  return {
    kind: areNodeAddressesEqual(activeBlockAddress, blockAddress)
      ? getActiveVersionOfBlockKind(block.kind)
      : getInactiveVersionOfBlockKind(block.kind),
    isLiteral: block.isLiteral,
    args: {
      expressions: block.args.expressions.map((expression, expressionIndex) =>
        expression.isLiteral
          ? expression
          : flagBlockAsInactiveExcept(
              activeBlockAddress,
              expression,
              blockAddress.concat([
                ARG_EXPRESSION_ADDRESS_PART,
                expressionIndex,
              ])
            )
      ),
      dropdowns: block.args.dropdowns,
      sequences: block.args.sequences.map((sequence, sequenceIndex) =>
        flagBlockSequenceAsInactiveExcept(
          activeBlockAddress,
          sequence,
          blockAddress.concat([
            ARG_COMMAND_SEQUENCE_ADDRESS_PART,
            sequenceIndex,
          ])
        )
      ),
    },
  };
}

function getActiveVersionOfBlockKind(kind: MinimapBlockKind): BlockKind {
  const halfDict: ReadonlyRecord<BlockKind, BlockKind> = mapBlockKindRecord(
    BLOCK_DEF_STRINGS,
    (_val, kind) => kind
  );
  const dict: ReadonlyRecord<MinimapBlockKind, BlockKind> = {
    ...halfDict,
    ...mapBlockKindRecordToInactiveBlockKindRecord(halfDict, identity),
  };
  return dict[kind];
}

function getInactiveVersionOfBlockKind(
  kind: MinimapBlockKind
): MinimapBlockKind {
  const halfDict: ReadonlyRecord<BlockKind, InactiveBlockKind> = {
    [BlockKind.WhenBattleStarts]: InactiveBlockKind.WhenBattleStarts,
    [BlockKind.MoveForward]: InactiveBlockKind.MoveForward,
    [BlockKind.Attack]: InactiveBlockKind.Attack,
    [BlockKind.Heal]: InactiveBlockKind.Heal,
    [BlockKind.MoveBackward]: InactiveBlockKind.MoveBackward,
    [BlockKind.Forever]: InactiveBlockKind.Forever,
    [BlockKind.If]: InactiveBlockKind.If,
    [BlockKind.XInFront]: InactiveBlockKind.XInFront,
    [BlockKind.And]: InactiveBlockKind.And,
    [BlockKind.Or]: InactiveBlockKind.Or,
    [BlockKind.Not]: InactiveBlockKind.Not,
    [BlockKind.Health]: InactiveBlockKind.Health,
    [BlockKind.LessThan]: InactiveBlockKind.LessThan,
    [BlockKind.Equal]: InactiveBlockKind.Equal,
    [BlockKind.GreaterThan]: InactiveBlockKind.GreaterThan,
  };
  const dict: ReadonlyRecord<MinimapBlockKind, InactiveBlockKind> = {
    ...halfDict,
    ...mapBlockKindRecordToInactiveBlockKindRecord(halfDict, identity),
  };
  return dict[kind];
}

export function toU8(n: number): number {
  const out = Math.min(255, Math.max(0, Math.round(n)));

  if (!Number.isInteger(out)) {
    return 0;
  }

  return out;
}

function getRboxAtAddressFromBlockSequence(
  targetAddress: NodeAddress,
  spec: BlockSequenceRenderSpec<any>
): null | Rectangle {
  if (areNodeAddressesEqual(targetAddress, spec.address)) {
    return spec.rbox;
  }

  for (const blockSpec of spec.blockSpecs) {
    const subRbox = getRboxAtAddressFromBlock(targetAddress, blockSpec);
    if (subRbox !== null) {
      return subRbox;
    }
  }

  return null;
}

function getRboxAtAddressFromBlock(
  targetAddress: NodeAddress,
  spec: BlockRenderSpec<any>
): null | Rectangle {
  if (areNodeAddressesEqual(targetAddress, spec.address)) {
    return spec.rbox;
  }

  for (const expressionSpec of spec.subexpressionSpecs) {
    if (expressionSpec.kind === RenderSpecKind.Literal) {
      continue;
    }

    const subRbox = getRboxAtAddressFromBlock(targetAddress, expressionSpec);
    if (subRbox !== null) {
      return subRbox;
    }
  }

  for (const sequenceSpec of spec.subsequenceSpecs) {
    const subRbox = getRboxAtAddressFromBlockSequence(
      targetAddress,
      sequenceSpec
    );
    if (subRbox !== null) {
      return subRbox;
    }
  }

  return null;
}

function disableBlockDeletionBasedOnLevel(
  original: BlockDefs<MainBlockEditorTypeParams>,
  level: Level
): BlockDefs<MainBlockEditorTypeParams> {
  switch (level) {
    case Level.Level1:
    case Level.Level2:
    case Level.Level3:
    case Level.Level4:
    case Level.Level5:
    case Level.Level6:
    case Level.Level10:
      return original;

    case Level.Level7: {
      const NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE = [
        BlockKind.MoveForward,
      ];
      return mapBlockKindRecord(original, (originalDef, kind) =>
        NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE.includes(kind)
          ? { ...originalDef, isDeletable: false }
          : originalDef
      );
    }

    case Level.Level8: {
      const NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE = [
        BlockKind.MoveForward,
        BlockKind.Attack,
      ];
      return mapBlockKindRecord(original, (originalDef, kind) =>
        NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE.includes(kind)
          ? { ...originalDef, isDeletable: false }
          : originalDef
      );
    }

    case Level.Level9: {
      const NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE = [
        BlockKind.MoveForward,
        BlockKind.Attack,
        BlockKind.Heal,
      ];
      return mapBlockKindRecord(original, (originalDef, kind) =>
        NORMALLY_DELETABLE_BLOCK_KINDS_THAT_ARE_UNDELETABLE.includes(kind)
          ? { ...originalDef, isDeletable: false }
          : originalDef
      );
    }
  }
}

function disableDropdownValuesBasedOnLevel(
  original: BlockDefs<MainBlockEditorTypeParams>,
  level: Level
): BlockDefs<MainBlockEditorTypeParams> {
  const allowedDropdownValues = getDropdownValuesAllowedOnLevel(level);

  return mapBlockKindRecord(
    original,
    (originalDef, kind): BlockDef<MainBlockEditorTypeParams> => {
      // These two cases have the same code (after type erasure).
      // Unfortunately, TypeScript doesn't know that, so we need to
      // duplicate the code, in order to avoid type errors.
      // We also could have used `as` to perform an unchecked cast,
      // but it's easy to forget properties in BlockDefs, so
      // we prefer to duplicate the code and keep the type safety.

      if (originalDef.kind === BlockDefKind.Command) {
        return {
          ...originalDef,
          kind: originalDef.kind,
          signature: originalDef.signature.map(
            (
              signaturePart
            ): CommandSignaturePart<MainBlockEditorTypeParams> => {
              if (signaturePart.kind !== BlockSignaturePartKind.Dropdown) {
                return signaturePart;
              }

              return {
                ...signaturePart,
                choices: signaturePart.choices.filter((choice) =>
                  allowedDropdownValues.includes(choice)
                ),
              };
            }
          ),
        };
      }

      return {
        ...originalDef,
        kind: originalDef.kind,
        signature: originalDef.signature.map(
          (signaturePart): QuerySignaturePart<MainBlockEditorTypeParams> => {
            if (signaturePart.kind !== BlockSignaturePartKind.Dropdown) {
              return signaturePart;
            }

            return {
              ...signaturePart,
              choices: signaturePart.choices.filter((choice) =>
                allowedDropdownValues.includes(choice)
              ),
            };
          }
        ),
      };
    }
  );
}

function getDropdownValuesAllowedOnLevel(level: Level): readonly string[] {
  switch (level) {
    case Level.Level1:
    case Level.Level2:
    case Level.Level3:
    case Level.Level4:
    case Level.Level5:
    case Level.Level6:
    case Level.Level7:
      return [DropdownValueKind.Nothing];

    case Level.Level8:
    case Level.Level9:
      return [DropdownValueKind.Nothing, DropdownValueKind.Goblin];

    case Level.Level10:
      return [
        DropdownValueKind.Nothing,
        DropdownValueKind.Goblin,
        DropdownValueKind.Witch,
      ];
  }
}

function doesBlockKindConsumeHeroTurn(kind: BlockKind): boolean {
  switch (kind) {
    case BlockKind.MoveForward:
    case BlockKind.MoveBackward:
    case BlockKind.Attack:
    case BlockKind.Heal:
      return true;

    // Control structures do not consume a turn.
    case BlockKind.WhenBattleStarts:
    case BlockKind.Forever:
    case BlockKind.If:
      return false;

    // Queries do not consume a turn.
    case BlockKind.XInFront:
    case BlockKind.And:
    case BlockKind.Or:
    case BlockKind.Not:
    case BlockKind.Health:
    case BlockKind.LessThan:
    case BlockKind.Equal:
    case BlockKind.GreaterThan:
      return false;
  }
}

function doesBlockSequenceContainNonEmptyLiteral(
  sequence: BlockSequence<BlockEditorTypeParams>
): boolean {
  return sequence.some(doesBlockContainNonEmptyLiteral);
}

function doesBlockContainNonEmptyLiteral(
  block: Block<BlockEditorTypeParams>
): boolean {
  return (
    block.args.expressions.some((expr) =>
      expr.isLiteral
        ? expr.source.length > 0
        : doesBlockContainNonEmptyLiteral(expr)
    ) || block.args.sequences.some(doesBlockSequenceContainNonEmptyLiteral)
  );
}

function getBlockSpecFromBlockSequenceSpecSatisfying<
  B extends BlockEditorTypeParams
>(
  seqSpec: BlockSequenceRenderSpec<B>,
  predicate: (blockSpec: BlockRenderSpec<B>) => boolean
): null | BlockRenderSpec<B> {
  for (const blockSpec of seqSpec.blockSpecs) {
    if (predicate(blockSpec)) {
      return blockSpec;
    }
  }

  for (const blockSpec of seqSpec.blockSpecs) {
    const subBlockSpec = getSubBlockSpecFromBlockSpecSatisfying(
      blockSpec,
      predicate
    );
    if (subBlockSpec !== null) {
      return subBlockSpec;
    }
  }

  return null;
}

function getSubBlockSpecFromBlockSpecSatisfying<
  B extends BlockEditorTypeParams
>(
  blockSpec: BlockRenderSpec<B>,
  predicate: (blockSpec: BlockRenderSpec<B>) => boolean
): null | BlockRenderSpec<B> {
  for (const subBlockSpec of blockSpec.subexpressionSpecs) {
    if (subBlockSpec.kind === RenderSpecKind.Literal) {
      continue;
    }

    if (predicate(subBlockSpec)) {
      return subBlockSpec;
    }
  }

  for (const subSeqSpec of blockSpec.subsequenceSpecs) {
    const subBlockSpec = getBlockSpecFromBlockSequenceSpecSatisfying(
      subSeqSpec,
      predicate
    );
    if (subBlockSpec !== null) {
      return subBlockSpec;
    }
  }

  return null;
}

function mapTriplet(
  src: ColorTriplet,
  f: (preim: Color32) => Color32
): ColorTriplet {
  return {
    primary: f(src.primary),
    secondary: f(src.secondary),
    tertiary: f(src.tertiary),
  };
}

function compositeColor32(foreground: Color32, background: Color32): Color32 {
  const fgAlpha = foreground.a / 255;
  const bgAlpha = background.a / 255;
  const outAlpha = fgAlpha + bgAlpha * (1.0 - fgAlpha);

  const unrounded = {
    r:
      (foreground.r * fgAlpha + background.r * bgAlpha * (1.0 - fgAlpha)) /
      outAlpha,
    g:
      (foreground.g * fgAlpha + background.g * bgAlpha * (1.0 - fgAlpha)) /
      outAlpha,
    b:
      (foreground.b * fgAlpha + background.b * bgAlpha * (1.0 - fgAlpha)) /
      outAlpha,
    a: 255 * outAlpha,
  };

  return {
    r: toU8(unrounded.r),
    g: toU8(unrounded.g),
    b: toU8(unrounded.b),
    a: toU8(unrounded.a),
  };
}

function getSvgTextDimensionCacheKey(
  text: string,
  fontFamily: string,
  fontSizePx: number
): string {
  if (text.includes(SVG_TEXT_DIMENSION_CACHE_KEY_SEPARATOR)) {
    throw new Error(
      "Text cannot include SVG_TEXT_DIMENSION_CACHE_KEY_SEPARATOR. Text: " +
        JSON.stringify(text)
    );
  }

  return `${fontSizePx} ${text}${SVG_TEXT_DIMENSION_CACHE_KEY_SEPARATOR}${fontFamily}`;
}

function willHeroKillEnemyAtIndexAssumingEnemiesCannotSummon(
  ibec: ImmutableBlockExecutionContext,
  enemyIndex: number
): boolean {
  const enemyKind = ibec.levelState.squares[enemyIndex].kind;

  if (enemyKind === SquareKind.Empty || enemyKind === SquareKind.Hero) {
    throw new Error(
      `Illegal enemyIndex passed to willHeroKillEnemyAtIndexAssumingEnemiesCannotSummon: ${enemyIndex}. The square at ${enemyIndex} contains ${enemyKind}.`
    );
  }

  const mbec = cloneBlockExecutionContext(ibec);

  while (
    mbec.gas > 0 &&
    mbec.stack.length > 0 &&
    isBattleStillOngoing(mbec.levelState)
  ) {
    evalAndPopTopStackBlock(mbec, false);

    if (mbec.levelState.squares[enemyIndex].kind !== enemyKind) {
      return true;
    }
  }

  return false;
}

function cloneBlockExecutionContext(
  ibec: ImmutableBlockExecutionContext
): MutableBlockExecutionContext {
  return {
    gas: ibec.gas,
    stack: ibec.stack.slice(),
    levelState: ibec.levelState,
    events: ibec.events.slice(),
  };
}

function isBattleStillOngoing(levelState: LevelState): boolean {
  const heroIndex = getHeroIndex(levelState);

  if (heroIndex === -1) {
    return false;
  }

  if ((levelState.squares[heroIndex] as HeroSquareState).health <= 0) {
    return false;
  }

  if (heroIndex === levelState.squares.length - 1) {
    return false;
  }

  return true;
}

function addFailLevelEventIfNotAlreadyPresent(events: BattleEvent[]): void {
  if (events.some((e) => e.kind === BattleEventKind.FailLevel)) {
    return;
  }

  events.push({ kind: BattleEventKind.FailLevel });
}

function renderCastleSquareOccupant({
  square,
  squareIndex,
  backgroundImageTop,
  backgroundImageScale,
  elapsedSeconds,
  battleAnimationData,
  screen,
}: {
  readonly square: SquareState;
  readonly squareIndex: number;
  readonly backgroundImageTop: number;
  readonly backgroundImageScale: number;
  readonly elapsedSeconds: number;
  readonly battleAnimationData: BattleAnimationData;
  readonly screen: ScreenState;
}): CanvasElement {
  const {
    characterDisplacements,
    witchAnimationFrame,
    battleStatus,
    displayHeroHealingAnimation,
    damageOverlayAlphas,
    squareOccupantAlphas,
  } = battleAnimationData;

  if (square.kind === SquareKind.Empty) {
    return NULL_CANVAS_ELEMENT;
  }

  // We need to partially fade the hearts
  // (i.e., reduce the opacity of the hearts)
  // if and only if a tall instruction box is visible
  // AND the tall instruction box's text goes all the way to the
  // permitted bottom
  // AND the character's x-coordinate is small enough
  // that the character is obstructing some of the text of
  // the tall instruction box.
  //
  // The only tall instruction box with text that goes to the bottom is
  // the box for the "User forever" hint.
  const shouldPartiallyFadeHearts =
    isCodeProbablyErroneouslyMissingATopLevelForeverBlock(
      screen,
      battleAnimationData
    ) && squareIndex <= 2;
  const rootHeartOpacityFactor = shouldPartiallyFadeHearts ? 0.5 : 1;

  if (square.kind === SquareKind.Hero) {
    if (square.health <= 0 || battleStatus !== BattleStatus.InProgress) {
      return NULL_CANVAS_ELEMENT;
    }

    const characterBBox: Rectangle = {
      topLeft: {
        x:
          (40 + squareIndex * 150 + characterDisplacements[squareIndex].x) *
          backgroundImageScale,
        y:
          backgroundImageTop +
          (1020 + characterDisplacements[squareIndex].y) * backgroundImageScale,
      },
      dimensions: {
        width: 150 * backgroundImageScale,
        height:
          150 *
          backgroundImageScale *
          (images.hero.naturalHeight / images.hero.naturalWidth),
      },
    };

    const heartElements = getCharacterHeartsCanvasElements({
      characterBBox,
      square,
      squareIndex,
      backgroundImageScale,
      battleAnimationData,
      heartOpacity: rootHeartOpacityFactor,
    });

    return {
      kind: CanvasElementKind.Group,
      alpha: squareOccupantAlphas[squareIndex],
      children: [
        heartElements.all,

        {
          kind: CanvasElementKind.Group,
          alpha: !displayHeroHealingAnimation
            ? 0
            : periodicColorSinInterp(
                { r: 128, g: 0, b: 0, a: 0 },
                { r: 255, g: 128, b: 128, a: 255 },
                elapsedSeconds,
                3
              ).a / 255,
          children: heartElements.orderedHeartMarkImageElements.map(
            (image, heartIndex) => {
              if (
                heartIndex < square.health + BATTLE_CONFIG.heroHeal &&
                square.health <= heartIndex
              ) {
                return {
                  ...image,
                  source: images.heart_mark,
                };
              }

              return NULL_CANVAS_ELEMENT;
            }
          ),
        },

        {
          kind: CanvasElementKind.Image,
          source: images.hero,
          dx: characterBBox.topLeft.x,
          dy: characterBBox.topLeft.y,
          dw: characterBBox.dimensions.width,
          dh: characterBBox.dimensions.height,
        },

        {
          kind: CanvasElementKind.Group,
          alpha: damageOverlayAlphas[squareIndex],
          children: [
            {
              kind: CanvasElementKind.Image,
              source: damageOverlays.hero,
              dx: characterBBox.topLeft.x,
              dy: characterBBox.topLeft.y,
              dw: characterBBox.dimensions.width,
              dh: characterBBox.dimensions.height,
            },
          ],
        },
      ],
    };
  }

  if (square.kind === SquareKind.Goblin) {
    const imageSource =
      square.color === GoblinColor.Green
        ? images.green_goblin
        : square.color === GoblinColor.Purple
        ? images.purple_goblin
        : typecheckedAssertNever(square.color);
    const characterBBox: Rectangle = {
      topLeft: {
        x:
          (40 + squareIndex * 150 + characterDisplacements[squareIndex].x) *
          backgroundImageScale,
        y: backgroundImageTop + 1020 * backgroundImageScale,
      },
      dimensions: {
        width: 150 * backgroundImageScale,
        height:
          150 *
          backgroundImageScale *
          (imageSource.naturalHeight / imageSource.naturalWidth),
      },
    };
    return {
      kind: CanvasElementKind.Group,
      alpha: squareOccupantAlphas[squareIndex],
      children: [
        getCharacterHeartsCanvasElements({
          characterBBox,
          square,
          squareIndex,
          backgroundImageScale,
          battleAnimationData,
          heartOpacity: rootHeartOpacityFactor,
        }).all,

        {
          kind: CanvasElementKind.Image,
          source: imageSource,
          dx: characterBBox.topLeft.x,
          dy: characterBBox.topLeft.y,
          dw: characterBBox.dimensions.width,
          dh: characterBBox.dimensions.height,
        },

        {
          kind: CanvasElementKind.Group,
          alpha: damageOverlayAlphas[squareIndex],
          children: [
            {
              kind: CanvasElementKind.Image,
              source: damageOverlays.goblin,
              dx: characterBBox.topLeft.x,
              dy: characterBBox.topLeft.y,
              dw: characterBBox.dimensions.width,
              dh: characterBBox.dimensions.height,
            },
          ],
        },
      ],
    };
  }

  if (square.kind === SquareKind.Witch) {
    const witchImage: HTMLImageElement = [
      images.witch_idle,
      images.witch_wand_raised,
      images.witch_wand_lowered,
      images.witch_idle,
    ][witchAnimationFrame];

    const characterBBox: Rectangle = {
      topLeft: {
        x:
          (40 + squareIndex * 150 + characterDisplacements[squareIndex].x) *
          backgroundImageScale,
        y: backgroundImageTop + 1020 * backgroundImageScale,
      },
      dimensions: {
        width: 150 * backgroundImageScale,
        height:
          150 *
          backgroundImageScale *
          (witchImage.naturalHeight / witchImage.naturalWidth),
      },
    };

    return {
      kind: CanvasElementKind.Group,
      alpha: squareOccupantAlphas[squareIndex],
      children: [
        getCharacterHeartsCanvasElements({
          characterBBox,
          square,
          squareIndex,
          backgroundImageScale,
          battleAnimationData,
          heartOpacity: rootHeartOpacityFactor,
        }).all,

        {
          kind: CanvasElementKind.Image,
          source: witchImage,
          dx: characterBBox.topLeft.x,
          dy: characterBBox.topLeft.y,
          dw: characterBBox.dimensions.width,
          dh: characterBBox.dimensions.height,
        },

        witchImage !== images.witch_idle
          ? NULL_CANVAS_ELEMENT
          : {
              kind: CanvasElementKind.Group,
              alpha: damageOverlayAlphas[squareIndex],
              children: [
                {
                  kind: CanvasElementKind.Image,
                  source: damageOverlays.witch_idle,
                  dx: characterBBox.topLeft.x,
                  dy: characterBBox.topLeft.y,
                  dw: characterBBox.dimensions.width,
                  dh: characterBBox.dimensions.height,
                },
              ],
            },
      ],
    };
  }

  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const exhaustivenessCheck: never = square;
  return exhaustivenessCheck;
}

function sum(nums: readonly number[]): number {
  let total = 0;
  for (const n of nums) {
    total += n;
  }
  return total;
}

function getRectCenter(r: Rectangle): Vec2 {
  return {
    x: r.topLeft.x + 0.5 * r.dimensions.width,
    y: r.topLeft.y + 0.5 * r.dimensions.height,
  };
}

function didHeroDefeatAllEnemiesButForgetToMoveToStairs(
  levelState: LevelState,
  hasFailLevelEventBeenEmitted: boolean
): boolean {
  const heroIndex = getHeroIndex(levelState);
  if (heroIndex === -1) {
    return false;
  }

  const { squares } = levelState;

  return (
    (squares[heroIndex] as HeroSquareState).health > 0 &&
    squares
      .slice(heroIndex + 1)
      .every((square) => square.kind === SquareKind.Empty) &&
    hasFailLevelEventBeenEmitted
  );
}

function didHeroSurviveLevelButFailToReachStairs(
  levelState: LevelState,
  hasFailLevelEventBeenEmitted: boolean
): boolean {
  const heroIndex = getHeroIndex(levelState);
  if (heroIndex === -1) {
    return false;
  }

  const { squares } = levelState;

  return (
    (squares[heroIndex] as HeroSquareState).health > 0 &&
    hasFailLevelEventBeenEmitted
  );
}

function containsEnemy(square: SquareState): boolean {
  return [SquareKind.Goblin, SquareKind.Witch].includes(square.kind);
}

function getEnemyCombinationKindOfEnemiesInitiallyBlockingHeroFromStairs(
  level: Level
): "NoEnemies" | EnemyCombinationKind {
  const initialLevelState = getInitialLevelState(level);
  const enemiesInitiallyBetweenHeroAndStairs = initialLevelState.squares
    .slice(getHeroIndex(initialLevelState) + 1)
    .filter(containsEnemy);

  if (enemiesInitiallyBetweenHeroAndStairs.length === 0) {
    return "NoEnemies";
  }

  const everyInitialEnemyWasGoblin = enemiesInitiallyBetweenHeroAndStairs.every(
    (square) => square.kind === SquareKind.Goblin
  );
  const everyInitialEnemyWasWitch = enemiesInitiallyBetweenHeroAndStairs.every(
    (square) => [SquareKind.Witch].includes(square.kind)
  );

  if (
    everyInitialEnemyWasGoblin &&
    enemiesInitiallyBetweenHeroAndStairs.length === 1
  ) {
    return EnemyCombinationKind.SingleGoblin;
  }

  if (
    everyInitialEnemyWasGoblin &&
    enemiesInitiallyBetweenHeroAndStairs.length >= 2
  ) {
    return EnemyCombinationKind.MultipleGoblins;
  }

  if (
    everyInitialEnemyWasWitch &&
    enemiesInitiallyBetweenHeroAndStairs.length === 1
  ) {
    return EnemyCombinationKind.SingleWitch;
  }

  throw new Error("Impossible enemy combination kind for level " + level);
}

function performPreflightSanityCheckIfNotAlreadyPerformed(): void {
  if (hasPerformedPreflightSanityCheck) {
    return;
  }

  performPreflightSanityCheck();

  console.log("Finished preflight sanity check.");
  hasPerformedPreflightSanityCheck = true;
}

function performPreflightSanityCheck(): void {
  performEnemyCombinationSanityCheckOnAllLevels();
}

function performEnemyCombinationSanityCheckOnAllLevels(): void {
  for (let level = Level.Level1; level <= LAST_LEVEL; ++level) {
    // This will throw if the level has a supposedly impossible enemy combination.
    getEnemyCombinationKindOfEnemiesInitiallyBlockingHeroFromStairs(level);
  }
}

function getBattleSoundsAssumingLevelJustStarted(
  initialLevelState: LevelState,
  events: readonly BattleEvent[]
): readonly Sound[] {
  const sounds: Sound[] = [];

  let levelState = initialLevelState;
  let timeCursor = BATTLE_START_DELAY_SECONDS;

  for (let eventIndex = 0; eventIndex < events.length; ++eventIndex) {
    const event = events[eventIndex];

    const nonoffsetSounds = getNonoffsetBattleEventSounds(event, levelState);
    sounds.push(
      ...nonoffsetSounds.map(
        // eslint-disable-next-line no-loop-func
        (nonoffset): Sound => ({
          ...nonoffset,
          timeInSeconds: timeCursor + nonoffset.timeInSeconds,
        })
      )
    );

    const eventDuration = getBattleEventDuration(event, levelState);
    timeCursor += eventDuration;

    const nextEventIsClimbStairs =
      eventIndex + 1 < events.length &&
      events[eventIndex + 1].kind === BattleEventKind.ClimbStairs;
    if (!nextEventIsClimbStairs && event.kind !== BattleEventKind.EvalBlock) {
      timeCursor += INTER_EVENT_DELAY_SECONDS;
    }

    levelState = applyBattleEventUnchecked(event, levelState);
  }

  return sounds;
}

function getNonoffsetBattleEventSounds(
  event: BattleEvent,
  levelState: LevelState
): readonly Sound[] {
  const sounds: Sound[] = [];

  const isClubAttack =
    event.kind === BattleEventKind.Attack &&
    CLUB_ATTACKERS.includes(levelState.squares[event.attackerIndex].kind);
  if (isClubAttack) {
    sounds.push({ kind: SoundKind.ClubAttack, timeInSeconds: 0 });
  }

  const isSwordAttack =
    event.kind === BattleEventKind.Attack &&
    SWORD_ATTACKERS.includes(levelState.squares[event.attackerIndex].kind);
  if (isSwordAttack) {
    sounds.push({ kind: SoundKind.SwordAttack, timeInSeconds: 0 });
  }

  const isFireballAttack =
    event.kind === BattleEventKind.Attack &&
    FIREBALL_ATTACKERS.includes(levelState.squares[event.attackerIndex].kind);
  const isWitchSummon =
    event.kind === BattleEventKind.Summon &&
    levelState.squares[event.summonerIndex].kind === SquareKind.Witch;
  if (isFireballAttack || isWitchSummon) {
    sounds.push({
      kind: SoundKind.FireballLaunch,
      timeInSeconds: 0.3,
      durationInSeconds: 0.3,
    });
    sounds.push({ kind: SoundKind.FireballExplosion, timeInSeconds: 0.6 });
  }

  if ([BattleEventKind.Advance, BattleEventKind.Retreat].includes(event.kind)) {
    sounds.push({ kind: SoundKind.CharacterMovement, timeInSeconds: -0.1 });
  }

  if (event.kind === BattleEventKind.ClimbStairs) {
    sounds.push({
      kind: SoundKind.SuccessMusic,
      timeInSeconds: getBattleEventDuration(event, levelState) + 0.05,
    });
  }

  if (event.kind === BattleEventKind.Attack) {
    const target = levelState.squares[event.targetIndex];
    if (
      "health" in target &&
      target.health <= event.damage &&
      target.kind === SquareKind.Goblin
    ) {
      sounds.push({
        kind: SoundKind.GoblinYelp,
        timeInSeconds: 0.75 * getBattleEventDuration(event, levelState),
      });
    }
  }

  if (event.kind === BattleEventKind.Attack) {
    const target = levelState.squares[event.targetIndex];
    if (
      "health" in target &&
      target.health <= event.damage &&
      target.kind === SquareKind.Witch
    ) {
      sounds.push({
        kind: SoundKind.WitchYelp,
        timeInSeconds: 0.5 * getBattleEventDuration(event, levelState),
      });
    }
  }

  if (event.kind === BattleEventKind.Attack) {
    const target = levelState.squares[event.targetIndex];
    if (
      "health" in target &&
      target.health <= event.damage &&
      target.kind === SquareKind.Hero
    ) {
      sounds.push({
        kind: SoundKind.HeroYelp,
        timeInSeconds: 0.5 * getBattleEventDuration(event, levelState),
      });
    }
  }

  if (event.kind === BattleEventKind.FailHeroAction) {
    sounds.push({
      kind: SoundKind.ErrorBuzzer,
      timeInSeconds: 0,
    });
  }

  if (event.kind === BattleEventKind.Heal) {
    sounds.push({
      kind: SoundKind.Heal,
      timeInSeconds: 0,
    });
  }

  return sounds;
}

function didConnectBlock(
  oldBlockEditorState: BlockEditorState<MainBlockEditorTypeParams>,
  newBlockEditorState: BlockEditorState<MainBlockEditorTypeParams>
): boolean {
  if (
    !(
      oldBlockEditorState.uiTouch !== null &&
      oldBlockEditorState.uiTouch.kind === UiTouchStateKind.BlockDrag &&
      newBlockEditorState.uiTouch === null
    )
  ) {
    return false;
  }

  const oldItems = oldBlockEditorState.canvas.items;
  const newItems = newBlockEditorState.canvas.items;

  const oldBlockCount = sum(
    oldItems.map((item) => getBlockSequenceRecursiveBlockCount(item.content))
  );
  const newBlockCount = sum(
    newItems.map((item) => getBlockSequenceRecursiveBlockCount(item.content))
  );

  if (
    oldBlockEditorState.uiTouch.draggeeOriginalAddress[0] ===
    PALETTE_ADDRESS_PART
  ) {
    // If the user releases a palette block sequence that they were dragging,
    // exactly one of the following must happen:
    // 1. The sequence gets deleted. In other words, nothing gets connected.
    // 2. The sequence starts a new canvas item. In other words, nothing gets connected.
    // 3. The sequence is added to an existing canvas item.

    // Check Case 2 ("sequence starts new canvas item").
    // If Case 2 is true, then there will now be more items than before.
    if (newItems.length > oldItems.length) {
      return false;
    }

    // Check Case 1 ("sequence gets deleted").
    // If Case 1 is true, than there will now be the same number of canvas blocks as before,
    // since the palette block sequence was not added to the canvas.
    if (newBlockCount === oldBlockCount) {
      return false;
    }

    // By process of elimination, we must be in Case 3.
    return true;
  }

  // If the user releases a canvas block sequence that they were dragging,
  // exactly one of the following must happen:
  //
  // 1. The sequence gets deleted.
  // 2. The sequence starts a new canvas item.
  // 3. The sequence is an item's top-level sequence, and only changes
  //    position (but no content changes were made).
  // 4. The sequence gets connected to a block that it was not previously connected to.
  //
  // We want to return `true` if and only if it's Case 4.

  // Check Case 2 ("sequence starts new canvas item").
  // If Case 2 is true, then there will now be more items than before.
  if (newItems.length > oldItems.length) {
    return false;
  }

  // Check Case 1 ("sequence gets deleted").
  // If Case 1 is true, than there will now be less total blocks than before.
  if (newBlockCount < oldBlockCount) {
    return false;
  }

  // Check Case 3 ("top-level sequence only changes position").
  const isDraggeeATopLevelSequenceThatOnlyChangedPosition =
    oldBlockEditorState.uiTouch.draggeeOriginalAddress.length === 3 &&
    oldBlockEditorState.uiTouch.draggeeOriginalAddress[0] ===
      CANVAS_ADDRESS_PART &&
    oldBlockEditorState.uiTouch.draggeeOriginalAddress[2] === 0 &&
    newItems.length === oldItems.length;
  if (isDraggeeATopLevelSequenceThatOnlyChangedPosition) {
    return false;
  }

  // By process of elimination, we must be in Case 4.
  return true;
}

function getBlockSequenceRecursiveBlockCount(
  sequence: BlockSequence<BlockEditorTypeParams>
): number {
  return sum(sequence.map(getBlockRecursiveBlockCount));
}

function getBlockRecursiveBlockCount(
  block: Block<BlockEditorTypeParams>
): number {
  return (
    1 +
    sum(
      block.args.expressions.map((expr) =>
        expr.isLiteral ? 0 : getBlockRecursiveBlockCount(expr)
      )
    ) +
    sum(block.args.sequences.map(getBlockSequenceRecursiveBlockCount))
  );
}

function getCurrentScreenOrientation(): OrientationType {
  const naiveType: undefined | OrientationType =
    window.screen.orientation?.type;

  if (naiveType === undefined) {
    return window.innerWidth >= window.innerHeight
      ? "landscape-primary"
      : "portrait-primary";
  }

  return naiveType;
}

function getCharacterHeartsCanvasElements({
  characterBBox,
  square,
  squareIndex,
  backgroundImageScale,
  battleAnimationData,
  heartOpacity,
}: {
  readonly characterBBox: Rectangle;
  readonly square: { readonly health: number; readonly maxHealth: number };
  readonly squareIndex: number;
  readonly backgroundImageScale: number;
  readonly battleAnimationData: BattleAnimationData;
  readonly heartOpacity: number;
}): {
  readonly all: CanvasElement;
  readonly orderedHeartMarkImageElements: readonly CanvasImage[];
} {
  const {
    advanceHealthChanges,
    damageOverlayAlphas,
    hasDamageOverlayAlphaPeaked,
  } = battleAnimationData;
  const heartMarkDimensions: Dimensions = {
    width: 32 * backgroundImageScale,
    height:
      32 *
      backgroundImageScale *
      (images.heart_mark.naturalHeight / images.heart_mark.naturalWidth),
  };

  const topRowHeartCount = Math.max(
    0,
    Math.min(MAX_HEARTS_PER_ROW, square.maxHealth - MAX_HEARTS_PER_ROW)
  );
  const bottomRowHeartCount = Math.min(MAX_HEARTS_PER_ROW, square.maxHealth);
  const scaledInterHeartMarkHorizontalMargin =
    backgroundImageScale * UNSCALED_INTER_HEART_MARK_HORIZONTAL_MARGIN;
  const bottomRowHeartMarks: CanvasImage[] = new Array(bottomRowHeartCount)
    .fill(undefined)
    .map((_, heartIndex) => {
      return {
        kind: CanvasElementKind.Image,
        source:
          heartIndex < square.health + advanceHealthChanges[squareIndex]
            ? images.heart_mark
            : images.empty_heart_mark,
        dx:
          characterBBox.topLeft.x +
          0.5 * characterBBox.dimensions.width -
          0.5 *
            (bottomRowHeartCount * heartMarkDimensions.width +
              bottomRowHeartCount * scaledInterHeartMarkHorizontalMargin) +
          heartIndex *
            (heartMarkDimensions.width + scaledInterHeartMarkHorizontalMargin),
        dy: characterBBox.topLeft.y - 40 * backgroundImageScale,
        dw: heartMarkDimensions.width,
        dh: heartMarkDimensions.height,
      };
    });
  const topRowHeartMarks: CanvasImage[] = new Array(topRowHeartCount)
    .fill(undefined)
    .map((_, heartIndex) => {
      return {
        kind: CanvasElementKind.Image,
        source:
          heartIndex + MAX_HEARTS_PER_ROW <
          square.health + advanceHealthChanges[squareIndex]
            ? images.heart_mark
            : images.empty_heart_mark,
        dx:
          characterBBox.topLeft.x +
          0.5 * characterBBox.dimensions.width -
          0.5 *
            (topRowHeartCount * heartMarkDimensions.width +
              topRowHeartCount * scaledInterHeartMarkHorizontalMargin) +
          heartIndex *
            (heartMarkDimensions.width + scaledInterHeartMarkHorizontalMargin),
        dy: characterBBox.topLeft.y - 70 * backgroundImageScale,
        dw: heartMarkDimensions.width,
        dh: heartMarkDimensions.height,
      };
    });
  const orderedHeartMarkImageElements =
    bottomRowHeartMarks.concat(topRowHeartMarks);

  const damagedHearts: CanvasGroup = {
    kind: CanvasElementKind.Group,
    children: orderedHeartMarkImageElements.map((image, heartIndex) => {
      if (
        advanceHealthChanges[squareIndex] < 0 &&
        heartIndex < square.health &&
        square.health - 1 <= heartIndex
      ) {
        return {
          kind: CanvasElementKind.Group,
          children: [
            {
              kind: CanvasElementKind.Group,
              alpha: !hasDamageOverlayAlphaPeaked[squareIndex]
                ? 1
                : damageOverlayAlphas[squareIndex],
              children: [
                {
                  ...image,
                  source: images.heart_mark,
                },
              ],
            },
            {
              kind: CanvasElementKind.Group,
              alpha: damageOverlayAlphas[squareIndex],
              children: [
                {
                  ...image,
                  source: damageOverlays.heart_mark,
                },
              ],
            },
          ],
        };
      }

      return NULL_CANVAS_ELEMENT;
    }),
  };

  const all: CanvasGroup = {
    kind: CanvasElementKind.Group,
    alpha: heartOpacity,
    children: typesafeCast<readonly CanvasElement[]>(
      orderedHeartMarkImageElements
    ).concat(damagedHearts),
  };

  return { all, orderedHeartMarkImageElements };
}

function getNondepressedPuzzleScreenButtonBBoxes(
  backgroundImageScale: number,
  safeAreaInsets: SafeAreaInsets["insets"]
): {
  readonly nondepressedLeftButtonBBox: Rectangle;
  readonly nondepressedMiddleButtonBBox: Rectangle;
  readonly nondepressedRightButtonBBox: Rectangle;
} {
  const nondepressedLeftButtonBBox: Rectangle = {
    topLeft: {
      x: 890 * backgroundImageScale - safeAreaInsets.right,
      y: 5,
    },
    dimensions: {
      width: 120 * backgroundImageScale,
      height:
        120 *
        backgroundImageScale *
        (images.buttons.puzzle.edit.naturalHeight /
          images.buttons.puzzle.edit.naturalWidth),
    },
  };

  const nondepressedMiddleButtonBBox: Rectangle = {
    topLeft: {
      x: 1020 * backgroundImageScale - safeAreaInsets.right,
      y: 5,
    },
    dimensions: {
      width: 120 * backgroundImageScale,
      height:
        120 *
        backgroundImageScale *
        // The View, Play, and Pause buttons all have the same dimensions
        // (and thus the same aspect ratio).
        // So we arbitrarily choose to use View here,
        // but this code will work correctly for all 3 buttons.
        (images.buttons.puzzle.view.naturalHeight /
          images.buttons.puzzle.view.naturalWidth),
    },
  };

  const nondepressedRightButtonBBox: Rectangle = {
    topLeft: {
      x: 1150 * backgroundImageScale - safeAreaInsets.right,
      y: 5,
    },
    dimensions: {
      width: 120 * backgroundImageScale,
      height:
        120 *
        backgroundImageScale *
        (images.buttons.puzzle.home.naturalHeight /
          images.buttons.puzzle.home.naturalWidth),
    },
  };

  return {
    nondepressedLeftButtonBBox,
    nondepressedMiddleButtonBBox,
    nondepressedRightButtonBBox,
  };
}

function areCanvasItemsEqual(
  itemA: CanvasItem<MainBlockEditorTypeParams>,
  itemB: CanvasItem<MainBlockEditorTypeParams>
): boolean {
  return (
    areVecsEqual(itemA.position, itemB.position) &&
    areBlockSequencesEqual(itemA.content, itemB.content)
  );
}

function areBlockSequencesEqual(
  a: BlockSequence<MainBlockEditorTypeParams>,
  b: BlockSequence<MainBlockEditorTypeParams>
): boolean {
  return (
    a.length === b.length &&
    a.every((aBlock, i) => areBlocksEqual(aBlock, b[i]))
  );
}

function areBlocksEqual(
  a: Block<MainBlockEditorTypeParams>,
  b: Block<MainBlockEditorTypeParams>
): boolean {
  return (
    a.kind === b.kind &&
    a.args.dropdowns.length === b.args.dropdowns.length &&
    a.args.dropdowns.every(
      (aDropdown, i) => aDropdown === b.args.dropdowns[i]
    ) &&
    a.args.expressions.length === b.args.expressions.length &&
    a.args.expressions.every((aExpression, i) => {
      const bExpression = b.args.expressions[i];

      if (aExpression.isLiteral && bExpression.isLiteral) {
        return aExpression.source === bExpression.source;
      }

      if (!aExpression.isLiteral && !bExpression.isLiteral) {
        return areBlocksEqual(aExpression, bExpression);
      }

      return false;
    }) &&
    a.args.sequences.length === b.args.sequences.length &&
    a.args.sequences.every((aSequence, i) =>
      areBlockSequencesEqual(aSequence, b.args.sequences[i])
    )
  );
}

function isCodeProbablyErroneouslyMissingATopLevelForeverBlock(
  screen: ScreenState,
  battleAnimationData: BattleAnimationData
): boolean {
  const blockEditorCanvasItems = screen.blockEditor.canvas.items;
  const entryPointItemContent = blockEditorCanvasItems
    .map((item) => item.content)
    .find(
      (content) =>
        content.length > 0 && content[0].kind === BlockKind.WhenBattleStarts
    );

  return (
    screen.animationKind === AnimationKind.PauseBattle &&
    Level.Level8 <= screen.level &&
    screen.level <= Level.Level10 &&
    battleAnimationData.didHeroSurviveLevelButFailToReachStairs &&
    !battleAnimationData.didHeroDefeatAllEnemiesButForgetToMoveToStairs &&
    entryPointItemContent !== undefined &&
    entryPointItemContent.some(
      (block) =>
        block.kind === BlockKind.If &&
        block.args.expressions.length > 0 &&
        !block.args.expressions[0].isLiteral &&
        block.args.sequences.length > 0 &&
        block.args.sequences[0].length > 0
    )
  );
}

function isNodeOrDescendantOfNode(a: Node, b: Node): boolean {
  let current: Node | null = a;
  while (current !== null) {
    if (current === b) {
      return true;
    }

    current = current.parentNode;
  }
  return false;
}
