// Initialize Firebase
export const firebaseApp = initializeApp(firebaseConfig);
const storage = getStorage(firebaseApp);

export function writeToFirebaseStorage(
  url: string,
  blob: Blob
): Promise<UploadResult> {
  const ref_ = ref(storage, url);
  return uploadBytes(ref_, blob);
}
