import { stringifyColor, typecheckedAssertNever } from "./App";
import { Color32 } from "./BlockEditor";

export enum CanvasElementKind {
  Group = "Group",
  Image = "Image",
  Text = "Text",
  Path = "Path",
  Clear = "Clear",
}

export enum PathInstructionKind {
  MoveTo = "MoveTo",
  LineTo = "LineTo",
  ArcTo = "ArcTo",
}

export enum TransformKind {
  Translate = "Translate",
  Rotate = "Rotate",
  Scale = "Scale",
}

export const NULL_CANVAS_ELEMENT: CanvasElement = {
  kind: CanvasElementKind.Group,
  children: [],
};

export type CanvasElement =
  | CanvasGroup
  | CanvasImage
  | CanvasText_
  | CanvasPath_
  | CanvasClear;

export interface CanvasGroup {
  readonly kind: CanvasElementKind.Group;
  readonly children: readonly CanvasElement[];
  readonly clipPath?: Path;
  readonly transform?: readonly Transform[];
  readonly alpha?: number;
}

export interface CanvasImage {
  readonly kind: CanvasElementKind.Image;
  readonly source: CanvasImageSource;
  readonly dx: number;
  readonly dy: number;
  readonly dw: number;
  readonly dh: number;
}

export interface CanvasText_ extends CanvasTextFont {
  readonly kind: CanvasElementKind.Text;
  readonly text: string;
  readonly x: number;
  readonly y: number;
  readonly maxWidth?: number;
  readonly fill?: Color32;
}

export interface CanvasTextFont {
  readonly fontWeight?: number;
  readonly fontSize: number;
  readonly fontFamily: string;

  readonly textBaseline?: CanvasRenderingContext2D["textBaseline"];
  readonly textAlign?: CanvasRenderingContext2D["textAlign"];
}

export interface CanvasPath_ {
  readonly kind: CanvasElementKind.Path;
  readonly path: Path | Path2D;
  readonly stroke?: Color32;
  readonly strokeWidth?: number;
  readonly fill?: Color32;
}

export interface CanvasClear {
  readonly kind: CanvasElementKind.Clear;
  readonly x: number;
  readonly y: number;
  readonly w: number;
  readonly h: number;
}

export type Path = readonly PathInstruction[];

export type PathInstruction = MoveTo | LineTo | ArcTo;

export interface MoveTo {
  readonly kind: PathInstructionKind.MoveTo;
  readonly x: number;
  readonly y: number;
}

export interface LineTo {
  readonly kind: PathInstructionKind.LineTo;
  readonly x: number;
  readonly y: number;
}

export interface ArcTo {
  readonly kind: PathInstructionKind.ArcTo;
  readonly x1: number;
  readonly y1: number;
  readonly x2: number;
  readonly y2: number;
  readonly radius: number;
}

export type Transform = Translate | Rotate | Scale;

export interface Translate {
  readonly kind: TransformKind.Translate;
  readonly x: number;
  readonly y: number;
}

export interface Rotate {
  readonly kind: TransformKind.Rotate;
  readonly angle: number;
}

export interface Scale {
  readonly kind: TransformKind.Scale;
  readonly x: number;
  readonly y: number;
}

export function paintCanvasElement(
  element: CanvasElement,
  ctx: CanvasRenderingContext2D
): void {
  switch (element.kind) {
    case CanvasElementKind.Group:
      ctx.save();

      applyGroupModifications(element, ctx);

      for (const child of element.children) {
        paintCanvasElement(child, ctx);
      }

      ctx.restore();

      break;

    case CanvasElementKind.Image:
      ctx.drawImage(
        element.source,
        element.dx,
        element.dy,
        element.dw,
        element.dh
      );

      break;

    case CanvasElementKind.Text:
      ctx.save();

      applyFont(element, ctx);

      if (element.fill !== undefined) {
        ctx.fillStyle = stringifyColor(element.fill);
        ctx.fillText(element.text, element.x, element.y, element.maxWidth);
      }

      ctx.restore();

      break;

    case CanvasElementKind.Path:
      ctx.save();

      if (element.path instanceof Path2D) {
        if (element.fill !== undefined) {
          ctx.fillStyle = stringifyColor(element.fill);
          ctx.fill(element.path);
        }

        if (element.strokeWidth !== undefined && element.stroke !== undefined) {
          ctx.strokeStyle = stringifyColor(element.stroke);
          ctx.lineWidth = element.strokeWidth;
          ctx.stroke(element.path);
        }
      } else {
        ctx.beginPath();

        for (const instruction of element.path) {
          executePathInstruction(instruction, ctx);
        }

        ctx.closePath();

        if (element.fill !== undefined) {
          ctx.fillStyle = stringifyColor(element.fill);
          ctx.fill();
        }

        if (element.strokeWidth !== undefined && element.stroke !== undefined) {
          ctx.strokeStyle = stringifyColor(element.stroke);
          ctx.lineWidth = element.strokeWidth;
          ctx.stroke();
        }
      }

      ctx.restore();

      break;

    case CanvasElementKind.Clear:
      ctx.clearRect(element.x, element.y, element.w, element.h);
      break;

    default:
      typecheckedAssertNever(element);
  }
}

export function applyFont(
  font: CanvasTextFont,
  ctx: CanvasRenderingContext2D
): void {
  ctx.font = `${font.fontWeight ?? ""} ${font.fontSize}px ${font.fontFamily}`
    .replace(/ {2,}/g, " ")
    .trim();

  ctx.textBaseline = font.textBaseline ?? ctx.textBaseline;
  ctx.textAlign = font.textAlign ?? ctx.textAlign;
}

function applyGroupModifications(
  element: CanvasGroup,
  ctx: CanvasRenderingContext2D
): void {
  applyTransforms(element.transform ?? [], ctx);

  ctx.globalAlpha *= element.alpha ?? 1;

  if (element.clipPath !== undefined) {
    ctx.beginPath();

    for (const instruction of element.clipPath) {
      executePathInstruction(instruction, ctx);
    }

    ctx.closePath();

    ctx.clip();
  }
}

function applyTransforms(
  transforms: readonly Transform[],
  ctx: CanvasRenderingContext2D
): void {
  for (const t of transforms) {
    applyTransform(t, ctx);
  }
}

function applyTransform(
  transform: Transform,
  ctx: CanvasRenderingContext2D
): void {
  switch (transform.kind) {
    case TransformKind.Translate:
      ctx.translate(transform.x, transform.y);
      break;

    case TransformKind.Rotate:
      ctx.rotate(transform.angle);
      break;

    case TransformKind.Scale:
      ctx.scale(transform.x, transform.y);
      break;

    default:
      typecheckedAssertNever(transform);
  }
}

function executePathInstruction(
  inst: PathInstruction,
  ctx: CanvasRenderingContext2D
): void {
  switch (inst.kind) {
    case PathInstructionKind.MoveTo:
      ctx.moveTo(inst.x, inst.y);
      break;

    case PathInstructionKind.LineTo:
      ctx.lineTo(inst.x, inst.y);
      break;

    case PathInstructionKind.ArcTo:
      ctx.arcTo(inst.x1, inst.y1, inst.x2, inst.y2, inst.radius);
      break;

    default:
      typecheckedAssertNever(inst);
  }
}
