const radius = 3;
const cornerTangentDistance = radius * Math.sqrt(3);
const sideLength = 30;
const height = (Math.sqrt(3) * sideLength) / 2;
const legTangentOffsetY = (Math.sqrt(3) / 2) * cornerTangentDistance;
const legTangentOffsetX = 0.5 * cornerTangentDistance;

const path = `M${cornerTangentDistance} 0

L${sideLength - cornerTangentDistance} 0

A${radius} ${radius} 0 0 1 ${
  sideLength - legTangentOffsetX
} ${legTangentOffsetY}

L${sideLength / 2 + legTangentOffsetX} ${height - legTangentOffsetY}

A${radius} ${radius} 0 0 1 ${sideLength / 2 - legTangentOffsetX} ${
  height - legTangentOffsetY
}

L${legTangentOffsetX} ${legTangentOffsetY}

A${radius} ${radius} 0 0 1 ${cornerTangentDistance} 0`;

export const DROPDOWN_ARROW = {
  path,
  width: sideLength,
  height,
  radius,
  cornerTangentDistance,
} as const;
