enum Code {
  // Codes 0 to 127 are for 7-bit references.

  // Codes 128 to 144 are for integers -8 to 8.

  ConstNull = 145,
  ConstFalse = 146,
  ConstTrue = 147,

  OffsetPosInt1Byte = 148,
  OffsetNegInt1Byte = 149,
  Int2Byte = 150,
  Int4Byte = 151,
  Int8Byte = 152,

  Float64 = 153,

  ConstEmptyArray = 154,
  Array1Element = 155,
  Array2Element = 156,
  Array3Element = 157,
  Array4Element = 158,
  Array5Element = 159,
  Array6Element = 160,
  Array7Element = 161,
  Array8Element = 162,
  ArrayLen1Byte = 163,
  ArrayLen2Byte = 164,
  ArrayLen4Byte = 165,

  ConstEmptyObject = 166,
  Object1Command = 167,
  Object2Command = 168,
  Object3Command = 169,
  Object4Command = 170,
  Object5Command = 171,
  Object6Command = 172,
  Object7Command = 173,
  Object8Command = 174,
  ObjectCommandCount1Byte = 175,
  ObjectCommandCount2Byte = 176,
  ObjectCommandCount4Byte = 177,

  Ref1Byte = 178,
  Ref2Byte = 179,
  Ref4Byte = 180,

  BigUint = 181,
  NegBigInt = 182,

  ConstEmptyString = 183,
  String1Byte = 184,
  String2Byte = 185,
  String3Byte = 186,
  String4Byte = 187,
  String5Byte = 188,
  String6Byte = 189,
  String7Byte = 190,
  String8Byte = 191,
  String9Byte = 192,
  String10Byte = 193,
  String11Byte = 194,
  String12Byte = 195,
  String13Byte = 196,
  String14Byte = 197,
  String15Byte = 198,
  StringLen1Byte = 199,
  StringLen2Byte = 200,
  StringLen4Byte = 201,

  ByteArray4Element = 202,
  ByteArray8Element = 203,
  ByteArray16Element = 204,
  ByteArray32Element = 205,
  ByteArray64Element = 206,
}

const MAX_UINT_1_BYTE = 256 ** 1 - 1;
const MAX_UINT_2_BYTE = 256 ** 2 - 1;
const MAX_UINT_4_BYTE = 256 ** 4 - 1;

const MAX_OFFSET_POS_INT_1_BYTE = 8 + 256;
const MIN_OFFSET_NEG_INT_1_BYTE = -8 - 256;

const MIN_INT_2_BYTE = 256 ** 2 / -2;
const MAX_INT_2_BYTE = 256 ** 2 / 2 - 1;
const MIN_INT_4_BYTE = 256 ** 4 / -2;
const MAX_INT_4_BYTE = 256 ** 4 / 2 - 1;

class NonReusableJsonAnalyzer {
  private readonly lookupTable: unknown[];
  private isUsed = false;

  private readonly handleInvalidJsonValue: (
    illegalValue: unknown,
    wasValueLosslesslyWritten: boolean
  ) => void;

  constructor(
    implicitTable: readonly unknown[],
    handleInvalidJsonValue?: (
      illegalValue: unknown,
      wasValueLosslesslyWritten: boolean
    ) => void
  ) {
    this.lookupTable = implicitTable.slice();
    this.handleInvalidJsonValue = handleInvalidJsonValue ?? ((): void => {});
  }

  writeNodeAndMarkAnalyzerAsUsed(
    node: unknown,
    pushByte: (byte: number) => void
  ): void {
    if (this.isUsed) {
      throw new Error("NonReusableJsonAnalyzer is already used.");
    }

    this.isUsed = true;

    this.writeNode(node, pushByte);
  }

  private writeNode(node: unknown, pushByte: (byte: number) => void): void {
    if (Number.isInteger(node)) {
      this.writeTaggedInt(node as number, pushByte);
      return;
    }

    if (typeof node === "number") {
      pushByte(Code.Float64);
      this.writeUntaggedF64(node, pushByte);

      if (!Number.isFinite(node)) {
        this.handleInvalidJsonValue(node, true);
      }

      return;
    }

    if (typeof node === "bigint") {
      throw new Error("BigInts are not supported.");
    }

    if (typeof node === "symbol") {
      pushByte(Code.ConstNull);
      this.handleInvalidJsonValue(node, false);
      return;
    }

    if (node === null) {
      pushByte(Code.ConstNull);
      return;
    }

    if (node === false) {
      pushByte(Code.ConstFalse);
      return;
    }

    if (node === true) {
      pushByte(Code.ConstTrue);
      return;
    }

    if (node === "") {
      pushByte(Code.ConstEmptyString);
      return;
    }

    if (Array.isArray(node) && node.length === 0) {
      pushByte(Code.ConstEmptyArray);
      return;
    }

    {
      const existingRef = this.getRefOfNodeThatEquals(node);
      if (existingRef !== null) {
        this.writeTaggedRef(existingRef, pushByte);
        return;
      }
    }

    if (Array.isArray(node)) {
      this.writeTaggedArray(node, pushByte);
      return;
    }

    if (
      node instanceof Uint8Array ||
      node instanceof Int8Array ||
      node instanceof Uint8ClampedArray ||
      node instanceof Uint16Array ||
      node instanceof Int16Array ||
      node instanceof Uint32Array ||
      node instanceof Int32Array ||
      node instanceof Float32Array ||
      node instanceof Float64Array
    ) {
      this.writeTaggedArray(Array.from(node), pushByte);
      this.handleInvalidJsonValue(node, false);
      return;
    }

    if (typeof node === "string") {
      const utf8Encoder = new TextEncoder();
      const utf8Bytes = utf8Encoder.encode(node);
      const utf8Length = utf8Bytes.length;

      const smallStringTag: undefined | Code =
        utf8Length <= 15 ? Code.String1Byte + utf8Length - 1 : undefined;

      if (smallStringTag !== undefined) {
        pushByte(smallStringTag);
      } else {
        const bigStringTag =
          utf8Length <= MAX_UINT_1_BYTE
            ? Code.StringLen1Byte
            : utf8Length <= MAX_UINT_2_BYTE
            ? Code.StringLen2Byte
            : Code.StringLen4Byte;

        pushByte(bigStringTag);
        this.writeUntaggedUnoffsetSafeUint(utf8Length, pushByte);
      }

      for (let i = 0; i < utf8Length; ++i) {
        pushByte(utf8Bytes[i]);
      }

      this.lookupTable.push(node);

      return;
    }

    let entries: [string, unknown][];
    try {
      entries = Object.entries(node as Record<string, unknown>);
    } catch {
      pushByte(Code.ConstNull);
      this.handleInvalidJsonValue(node, false);
      return;
    }

    if (entries.length === 0) {
      pushByte(Code.ConstEmptyObject);
      return;
    }

    type ObjectConstructionCommand =
      | { isObjectCopy: true; ref: number }
      | { isObjectCopy: false; key: string; value: unknown };
    const commandQueue: ObjectConstructionCommand[] = [];
    let nextUnprocessedEntryIndex = 0;
    while (nextUnprocessedEntryIndex < entries.length) {
      const [prefixObjectRef, prefixLength] =
        this.getLongestPrefixObjectRefAndEntryCount(
          entries.slice(nextUnprocessedEntryIndex)
        );

      if (prefixObjectRef === null) {
        commandQueue.push({
          isObjectCopy: false,
          key: entries[nextUnprocessedEntryIndex][0],
          value: entries[nextUnprocessedEntryIndex][1],
        });
        ++nextUnprocessedEntryIndex;
        continue;
      }

      commandQueue.push({ isObjectCopy: true, ref: prefixObjectRef });
      nextUnprocessedEntryIndex += prefixLength;
    }

    const commandCount = commandQueue.length;
    const smallObjectTag: undefined | Code =
      commandCount <= 8 ? Code.Object1Command + commandCount - 1 : undefined;

    if (smallObjectTag !== undefined) {
      pushByte(smallObjectTag);
    } else {
      const bigObjectTag =
        commandCount <= MAX_UINT_1_BYTE
          ? Code.ObjectCommandCount1Byte
          : commandCount <= MAX_UINT_2_BYTE
          ? Code.ObjectCommandCount2Byte
          : Code.ObjectCommandCount4Byte;

      pushByte(bigObjectTag);
      this.writeUntaggedUnoffsetSafeUint(commandCount, pushByte);
    }

    for (const command of commandQueue) {
      if (command.isObjectCopy) {
        this.writeTaggedRef(command.ref, pushByte);
      } else {
        this.writeNode(command.key, pushByte);
        this.writeNode(command.value, pushByte);
      }
    }

    this.lookupTable.push(node);
  }

  private writeTaggedInt(n: number, pushByte: (byte: number) => void): void {
    if (-8 <= n && n <= 8) {
      pushByte(n + 136);
      return;
    }

    if (9 <= n && n <= MAX_OFFSET_POS_INT_1_BYTE) {
      pushByte(Code.OffsetPosInt1Byte);
      pushByte(n - 9);
      return;
    }

    if (MIN_OFFSET_NEG_INT_1_BYTE <= n && n <= -9) {
      pushByte(Code.OffsetNegInt1Byte);
      pushByte(-9 - n);
      return;
    }

    if (!(MIN_INT_4_BYTE <= n && n <= MAX_INT_4_BYTE)) {
      pushByte(Code.Float64);
      this.writeUntaggedF64(n, pushByte);
      return;
    }

    if (MIN_INT_2_BYTE <= n && n <= MAX_INT_2_BYTE) {
      pushByte(Code.Int2Byte);
      const buffer = new ArrayBuffer(2);
      new DataView(buffer).setInt16(0, n, true);
      const u8s = new Uint8Array(buffer);
      for (let i = 0; i < 2; ++i) {
        pushByte(u8s[i]);
      }
      return;
    }

    pushByte(Code.Int4Byte);
    const buffer = new ArrayBuffer(4);
    new DataView(buffer).setInt32(0, n, true);
    const u8s = new Uint8Array(buffer);
    for (let i = 0; i < 4; ++i) {
      pushByte(u8s[i]);
    }
  }

  private writeUntaggedUnoffsetSafeUint(
    uint: number,
    pushByte: (byte: number) => void
  ): void {
    if (uint === 0) {
      pushByte(0);
      return;
    }

    let n = uint;
    while (n > 0) {
      pushByte(n % 256);
      n = Math.floor(n / 256);
    }
  }

  private writeUntaggedF64(n: number, pushByte: (byte: number) => void): void {
    const buffer = new ArrayBuffer(8);
    const view = new DataView(buffer);
    view.setFloat64(0, n, true);

    const u8s = new Uint8Array(buffer);
    for (let i = 0; i < 8; ++i) {
      pushByte(u8s[i]);
    }
  }

  private writeTaggedRef(ref: number, pushByte: (byte: number) => void): void {
    if (ref <= 127) {
      pushByte(ref);
      return;
    }

    const refTag =
      ref <= MAX_UINT_1_BYTE
        ? Code.Ref1Byte
        : ref <= MAX_UINT_2_BYTE
        ? Code.Ref2Byte
        : Code.Ref4Byte;

    if (ref > MAX_UINT_4_BYTE) {
      throw new Error("Reference too large. Index: " + ref);
    }

    pushByte(refTag);
    this.writeUntaggedUnoffsetSafeUint(ref, pushByte);
  }

  private writeTaggedArray(
    node: readonly unknown[],
    pushByte: (byte: number) => void
  ): void {
    const byteArrayTag = elementCountToByteArrayTag(node.length);
    if (
      byteArrayTag !== null &&
      node.every(
        (n) => Number.isInteger(n) && 0 <= (n as number) && (n as number) <= 255
      )
    ) {
      pushByte(byteArrayTag);
      for (const n of node) {
        pushByte(n as number);
      }
      return;
    }

    const smallArrayTag: undefined | Code =
      node.length <= 8 ? Code.Array1Element + node.length - 1 : undefined;

    if (smallArrayTag !== undefined) {
      pushByte(smallArrayTag);
    } else {
      const bigArrayTag =
        node.length <= MAX_UINT_1_BYTE
          ? Code.ArrayLen1Byte
          : node.length <= MAX_UINT_2_BYTE
          ? Code.ArrayLen2Byte
          : Code.ArrayLen4Byte;

      pushByte(bigArrayTag);
      this.writeUntaggedUnoffsetSafeUint(node.length, pushByte);
    }

    if (node.length > MAX_UINT_4_BYTE) {
      throw new Error("Array too long. Length: " + node.length);
    }

    for (let i = 0; i < node.length; ++i) {
      this.writeNode(node[i], pushByte);
    }

    this.lookupTable.push(node);
  }

  /**
   * If a prefix object is not found, this function returns `[null, 0]`.
   */
  private getLongestPrefixObjectRefAndEntryCount(
    goalEntries: readonly (readonly [string, unknown])[]
  ): [number | null, number] {
    if (goalEntries.length === 0) {
      return [null, 0];
    }

    let bestRef: null | number = null;
    let bestEntryCount = 0;

    for (
      let candidateRef = 0;
      candidateRef < this.lookupTable.length;
      ++candidateRef
    ) {
      const referent = this.lookupTable[candidateRef];

      if (!(referent !== null && typeof referent === "object")) {
        continue;
      }

      let candidateEntries: [string, unknown][];
      try {
        candidateEntries = Object.entries(referent as Record<string, unknown>);
      } catch {
        continue;
      }

      if (
        !(
          candidateEntries.length <= goalEntries.length &&
          candidateEntries.every(
            ([candidateKey, candidateValue], i) =>
              candidateKey === goalEntries[i][0] &&
              jsonDeepEquals(candidateValue, goalEntries[i][1])
          )
        )
      ) {
        continue;
      }

      if (candidateEntries.length > bestEntryCount) {
        bestRef = candidateRef;
        bestEntryCount = candidateEntries.length;
      }
    }

    return [bestRef, bestEntryCount];
  }

  private getRefOfNodeThatEquals(node: unknown): number | null {
    for (let i = 0; i < this.lookupTable.length; ++i) {
      if (jsonDeepEquals(node, this.lookupTable[i])) {
        return i;
      }
    }

    return null;
  }

  /**
   * The value that this function returns is **not** safe to mutate.
   * If you need to mutate the value, you must make a deep copy.
   */
  readNodeAndMarkAnalyzerAsUsed(data: Uint8Array): {
    readonly value: unknown;
    readonly bytesConsumed: number;
  } {
    if (this.isUsed) {
      throw new Error("NonReusableJsonAnalyzer is already used.");
    }

    this.isUsed = true;

    return this.readNode(data);
  }

  private readNode(data: Uint8Array): {
    value: unknown;
    bytesConsumed: number;
  } {
    const tag = data[0];
    if (tag <= 127) {
      return { value: this.getReferent(tag), bytesConsumed: 1 };
    }

    if (128 <= tag && tag <= 144) {
      return { value: tag - 136, bytesConsumed: 1 };
    }

    if (tag === Code.ConstNull) {
      return { value: null, bytesConsumed: 1 };
    }

    if (tag === Code.ConstFalse) {
      return { value: false, bytesConsumed: 1 };
    }

    if (tag === Code.ConstTrue) {
      return { value: true, bytesConsumed: 1 };
    }

    if (Code.OffsetPosInt1Byte <= tag && tag <= Code.Int4Byte) {
      return this.readTaggedInt(data);
    }

    if (tag === Code.Float64) {
      const value = this.getUntaggedF64(data.subarray(1));
      return { value, bytesConsumed: 9 };
    }

    if (Code.ConstEmptyArray <= tag && tag <= Code.ArrayLen4Byte) {
      return this.readTaggedArray(data);
    }

    if (Code.ConstEmptyObject <= tag && tag <= Code.ObjectCommandCount4Byte) {
      return this.readTaggedObject(data);
    }

    if (Code.Ref1Byte <= tag && tag <= Code.Ref4Byte) {
      return this.readReferentOfTaggedBigRef(data);
    }

    if (tag === Code.BigUint || tag === Code.NegBigInt) {
      throw new Error("BigInts are not supported.");
    }

    if (Code.ConstEmptyString <= tag && tag <= Code.StringLen4Byte) {
      return this.readTaggedString(data);
    }

    if (Code.ByteArray4Element <= tag && tag <= Code.ByteArray64Element) {
      return this.readTaggedByteArray(data);
    }

    throw new Error("Invalid node tag: " + tag);
  }

  private readTaggedInt(data: Uint8Array): {
    value: number;
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.OffsetPosInt1Byte) {
      return { value: data[1] + 9, bytesConsumed: 2 };
    }

    if (tag === Code.OffsetNegInt1Byte) {
      return { value: -9 - data[1], bytesConsumed: 2 };
    }

    if (tag === Code.Int2Byte) {
      const buffer = new ArrayBuffer(2);
      new Uint8Array(buffer).set(data.subarray(1, 3));
      const value = new DataView(buffer).getInt16(0, true);
      return { value, bytesConsumed: 3 };
    }

    if (tag === Code.Int4Byte) {
      const buffer = new ArrayBuffer(4);
      new Uint8Array(buffer).set(data.subarray(1, 5));
      const value = new DataView(buffer).getInt32(0, true);
      return { value, bytesConsumed: 5 };
    }

    if (tag === Code.Int8Byte) {
      throw new Error("Int8Byte is not supported.");
    }

    throw new Error("Invalid int tag: " + tag);
  }

  private getUntaggedF64(data: Uint8Array): number {
    const buffer = new ArrayBuffer(8);
    new Uint8Array(buffer).set(data.subarray(0, 8));
    return new DataView(buffer).getFloat64(0, true);
  }

  private readTaggedArray(data: Uint8Array): {
    value: readonly unknown[];
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.ConstEmptyArray) {
      return { value: [], bytesConsumed: 1 };
    }

    const { arrayLength, firstElementIndex } =
      this.getLengthAndFirstElementIndexOfTaggedNonEmptyArray(data);

    const out: unknown[] = [];
    let nextElementIndex = firstElementIndex;

    for (let i = 0; i < arrayLength; ++i) {
      const { bytesConsumed, value } = this.readNode(
        data.subarray(nextElementIndex)
      );
      out.push(value);
      nextElementIndex += bytesConsumed;
    }

    this.lookupTable.push(out);
    return { value: out, bytesConsumed: nextElementIndex };
  }

  private getLengthAndFirstElementIndexOfTaggedNonEmptyArray(
    data: Uint8Array
  ): {
    arrayLength: number;
    firstElementIndex: number;
  } {
    const tag = data[0];

    if (Code.Array1Element <= tag && tag <= Code.Array8Element) {
      return {
        arrayLength: tag - Code.Array1Element + 1,
        firstElementIndex: 1,
      };
    }

    if (tag === Code.ArrayLen1Byte) {
      return { arrayLength: data[1], firstElementIndex: 2 };
    }

    if (tag === Code.ArrayLen2Byte) {
      const arrayLength = this.getUntaggedU16(data.subarray(1));
      return { arrayLength, firstElementIndex: 3 };
    }

    if (tag === Code.ArrayLen4Byte) {
      const arrayLength = this.getUntaggedU32(data.subarray(1));
      return { arrayLength, firstElementIndex: 5 };
    }

    throw new Error("Invalid array tag: " + tag);
  }

  private getUntaggedU16(data: Uint8Array): number {
    const buffer = new ArrayBuffer(2);
    new Uint8Array(buffer).set(data.subarray(0, 2));
    return new DataView(buffer).getUint16(0, true);
  }

  private getUntaggedU32(data: Uint8Array): number {
    const buffer = new ArrayBuffer(4);
    new Uint8Array(buffer).set(data.subarray(0, 4));
    return new DataView(buffer).getUint32(0, true);
  }

  private readTaggedObject(data: Uint8Array): {
    value: Record<string, unknown>;
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.ConstEmptyObject) {
      return { value: {}, bytesConsumed: 1 };
    }

    const { commandCount, firstCommandIndex } =
      this.getLengthAndFirstCommandIndexOfTaggedNonEmptyObject(data);

    const entries: (readonly [string, unknown])[] = [];
    let nextCommandIndex = firstCommandIndex;

    for (let i = 0; i < commandCount; ++i) {
      const part1 = this.readNode(data.subarray(nextCommandIndex));
      nextCommandIndex += part1.bytesConsumed;

      if (typeof part1.value === "object" && part1.value !== null) {
        entries.push(...Object.entries(part1.value as Record<string, unknown>));
        continue;
      }

      const part2 = this.readNode(data.subarray(nextCommandIndex));
      nextCommandIndex += part2.bytesConsumed;

      entries.push([String(part1.value), part2.value]);
    }

    const out = Object.fromEntries(entries);
    this.lookupTable.push(out);
    return {
      value: out,
      bytesConsumed: nextCommandIndex,
    };
  }

  private getLengthAndFirstCommandIndexOfTaggedNonEmptyObject(
    data: Uint8Array
  ): {
    commandCount: number;
    firstCommandIndex: number;
  } {
    const tag = data[0];

    if (Code.Object1Command <= tag && tag <= Code.Object8Command) {
      return {
        commandCount: tag - Code.Object1Command + 1,
        firstCommandIndex: 1,
      };
    }

    if (tag === Code.ObjectCommandCount1Byte) {
      return { commandCount: data[1], firstCommandIndex: 2 };
    }

    if (tag === Code.ObjectCommandCount2Byte) {
      const commandCount = this.getUntaggedU16(data.subarray(1));
      return { commandCount, firstCommandIndex: 3 };
    }

    if (tag === Code.ObjectCommandCount4Byte) {
      const commandCount = this.getUntaggedU32(data.subarray(1));
      return { commandCount, firstCommandIndex: 5 };
    }

    throw new Error("Invalid object tag: " + tag);
  }

  private readReferentOfTaggedBigRef(data: Uint8Array): {
    value: unknown;
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.Ref1Byte) {
      const ref = data[1];
      const value = this.getReferent(ref);
      return { value, bytesConsumed: 2 };
    }

    if (tag === Code.Ref2Byte) {
      const ref = this.getUntaggedU16(data.subarray(1));
      const value = this.getReferent(ref);
      return { value, bytesConsumed: 3 };
    }

    if (tag === Code.Ref4Byte) {
      const ref = this.getUntaggedU32(data.subarray(1));
      const value = this.getReferent(ref);
      return { value, bytesConsumed: 5 };
    }

    throw new Error("Invalid ref tag: " + tag);
  }

  private getReferent(ref: number): unknown {
    if (ref >= this.lookupTable.length) {
      throw new Error(
        "Reference out of bounds. Index: " +
          ref +
          ". Lookup table length: " +
          this.lookupTable.length +
          "."
      );
    }

    return this.lookupTable[ref];
  }

  private readTaggedString(data: Uint8Array): {
    value: string;
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.ConstEmptyString) {
      return { value: "", bytesConsumed: 1 };
    }

    const { stringUtf8Length, firstByteIndex } =
      this.getLengthAndFirstCharIndexOfTaggedNonEmptyString(data);

    const utf8Bytes = data.subarray(
      firstByteIndex,
      firstByteIndex + stringUtf8Length
    );
    const value = new TextDecoder().decode(utf8Bytes);
    this.lookupTable.push(value);
    return { value, bytesConsumed: firstByteIndex + stringUtf8Length };
  }

  private getLengthAndFirstCharIndexOfTaggedNonEmptyString(data: Uint8Array): {
    stringUtf8Length: number;
    firstByteIndex: number;
  } {
    const tag = data[0];

    if (Code.String1Byte <= tag && tag <= Code.String15Byte) {
      return {
        stringUtf8Length: tag - Code.String1Byte + 1,
        firstByteIndex: 1,
      };
    }

    if (tag === Code.StringLen1Byte) {
      return { stringUtf8Length: data[1], firstByteIndex: 2 };
    }

    if (tag === Code.StringLen2Byte) {
      const stringUtf8Length = this.getUntaggedU16(data.subarray(1));
      return { stringUtf8Length, firstByteIndex: 3 };
    }

    if (tag === Code.StringLen4Byte) {
      const stringUtf8Length = this.getUntaggedU32(data.subarray(1));
      return { stringUtf8Length, firstByteIndex: 5 };
    }

    throw new Error("Invalid string tag: " + tag);
  }

  private readTaggedByteArray(data: Uint8Array): {
    value: readonly number[];
    bytesConsumed: number;
  } {
    const tag = data[0];

    if (tag === Code.ByteArray4Element) {
      const value = Array.from(data.subarray(1, 5));
      return { value, bytesConsumed: 5 };
    }

    if (tag === Code.ByteArray8Element) {
      const value = Array.from(data.subarray(1, 9));
      return { value, bytesConsumed: 9 };
    }

    if (tag === Code.ByteArray16Element) {
      const value = Array.from(data.subarray(1, 17));
      return { value, bytesConsumed: 17 };
    }

    if (tag === Code.ByteArray32Element) {
      const value = Array.from(data.subarray(1, 33));
      return { value, bytesConsumed: 33 };
    }

    if (tag === Code.ByteArray64Element) {
      const value = Array.from(data.subarray(1, 65));
      return { value, bytesConsumed: 65 };
    }

    throw new Error("Invalid byte array tag: " + tag);
  }
}

class PushableUint8Array {
  private buffer: Uint8Array;
  private length: number;

  constructor(length?: number) {
    this.buffer = new Uint8Array(length ?? 0);
    this.length = 0;
  }

  pushByte(byte: number): void {
    if (this.length >= this.buffer.length) {
      const newBuffer = new Uint8Array(Math.max(1, this.buffer.length * 2));
      newBuffer.set(this.buffer);
      this.buffer = newBuffer;
    }

    this.buffer[this.length] = byte;
    ++this.length;
  }

  pushBytes(bytes: Uint8Array): void {
    const requiredLen = this.length + bytes.length;

    let newBufferLen = this.buffer.length;
    while (newBufferLen < requiredLen) {
      newBufferLen = Math.max(1, newBufferLen * 2);
    }

    if (newBufferLen !== this.buffer.length) {
      const newBuffer = new Uint8Array(newBufferLen);
      newBuffer.set(this.buffer);
      this.buffer = newBuffer;
    }

    this.buffer.set(bytes, this.length);
    this.length += bytes.length;
  }

  unpushableSlice(start?: number, end?: number): Uint8Array {
    return this.buffer.subarray(0, this.length).slice(start, end);
  }

  unpushableSubarray(start?: number, end?: number): Uint8Array {
    return this.buffer.subarray(0, this.length).subarray(start, end);
  }

  getLength(): number {
    return this.length;
  }
}

export function compressJson(
  json: any,
  implicitTable: readonly string[]
): Uint8Array {
  const analyzer = new NonReusableJsonAnalyzer(implicitTable);
  const body = new PushableUint8Array();
  const pushByteToBody = body.pushByte.bind(body);

  analyzer.writeNodeAndMarkAnalyzerAsUsed(json, pushByteToBody);
  return body.unpushableSubarray();
}

export function decompressJson(
  data: Uint8Array,
  implicitTable: readonly string[]
): unknown {
  const analyzer = new NonReusableJsonAnalyzer(implicitTable);
  return analyzer.readNodeAndMarkAnalyzerAsUsed(data).value;
}

function jsonDeepEquals(a: unknown, b: unknown): boolean {
  // This logic fails on NaN, since NaN !== NaN.
  // However, NaN is not a valid JSON value, so this is fine.
  if (a === b) {
    return true;
  }

  if (typeof a !== typeof b) {
    return false;
  }

  if (Array.isArray(a) && Array.isArray(b)) {
    return (
      a.length === b.length &&
      a.every((aElement, i) => jsonDeepEquals(aElement, b[i]))
    );
  }

  if (
    !(
      a !== null &&
      typeof a === "object" &&
      b !== null &&
      typeof b === "object"
    )
  ) {
    return false;
  }

  try {
    const aEntries = Object.entries(a as Record<string, unknown>);
    const bEntries = Object.entries(b as Record<string, unknown>);
    return (
      aEntries.length === bEntries.length &&
      aEntries.every(
        ([aKey, aValue], i) =>
          bEntries[i][0] === aKey && jsonDeepEquals(aValue, bEntries[i][1])
      )
    );
  } catch {
    return false;
  }
}

function elementCountToByteArrayTag(elementCount: number): null | Code {
  if (elementCount === 4) {
    return Code.ByteArray4Element;
  }

  if (elementCount === 8) {
    return Code.ByteArray8Element;
  }

  if (elementCount === 16) {
    return Code.ByteArray16Element;
  }

  if (elementCount === 32) {
    return Code.ByteArray32Element;
  }

  if (elementCount === 64) {
    return Code.ByteArray64Element;
  }

  return null;
}
